import React from 'react';
import { PlaybackController } from '../playback_controller';
import { ProjectViewer, ProjectViewerMode } from '../project_viewer';
import { BubbleSort } from '../../indyplot_projects/bubble_sort/bubble_sort';
import { BubbleSortAnimation } from '../../indyplot_projects/bubble_sort/bubble_sort_animation';
import { BubbleSortControls } from './bubble_sort_controls';

const MIN_VALUES_NUMBER = 3;
const MAX_VALUES_NUMBER = 10;
const INITIAL_VALUES_NUMBER = 5;

let animation: BubbleSortAnimation;
let values: Array<number> = [];

function generateRandomValues(valuesNumber: number) {
  values = Array.from(Array(valuesNumber).keys()); // Generate an array of N integers from 0 to N-1.
  values.sort(() => Math.random() - 0.5); // Randomly shuffle the array of integers.
  animation.setValues(values);
}

export function BubbleSortProject() {
  const [isPlaybackRunning, setIsPlaybackRunning] = React.useState(false);

  const handleProjectViewerModeSwitch = React.useCallback((mode: ProjectViewerMode): boolean => {
    if (mode === 'play') {
      animation.initialize();
      BubbleSort.sort([...values], animation);
      setIsPlaybackRunning(true);
    } else {
      setIsPlaybackRunning(false);
      // TODO4: There is a bug caused the playback works in a separate thread and needs some time to stop.
      //        It leads the actors' state is changing (at least once) when the animation was already reset.
      //        So it's need to add small delay before resetting the animation to let the timer stop. Need to fix it.
      setTimeout(() => animation.reset(), 100);
    }

    return true;
  }, []);

  React.useEffect(() => {
    animation = new BubbleSortAnimation();
    generateRandomValues(INITIAL_VALUES_NUMBER);
    return () => animation.remove();
  }, []);

  const playbackController = <PlaybackController animation={animation} isRunning={isPlaybackRunning} />;

  const projectControls = (
    <BubbleSortControls
      minValuesNumber={MIN_VALUES_NUMBER}
      maxValuesNumber={MAX_VALUES_NUMBER}
      initialValuesNumber={INITIAL_VALUES_NUMBER}
      onGenerateRandomValuesButtonClick={generateRandomValues}
    />
  );

  return (
    <ProjectViewer
      playbackController={playbackController}
      projectControls={projectControls}
      onModeSwitch={handleProjectViewerModeSwitch}
    />
  );
}
