import { SVGElementsFactory } from '../svg_elements_factory';
import { NullOr, VectorOrArrayOrNumber } from '../core/types';
import { Vector } from '../core/vector';
import { Actor, IActor } from './actor';

export interface ICircleActor extends IActor {
  position?: NullOr<VectorOrArrayOrNumber>;
  radius?: NullOr<number>;
}

export class CircleActor extends Actor {
  private circle: SVGCircleElement;

  constructor(parentContainer: SVGGElement) {
    super(parentContainer);
    this.circle = SVGElementsFactory.createCircle(this.container);
  }

  setParameters(parameters: ICircleActor) {
    super.setParameters(parameters);

    if (parameters.position !== undefined) {
      this.setPosition(parameters.position);
    }

    if (parameters.radius !== undefined) {
      this.setRadius(parameters.radius);
    }
  }

  setPosition(position: NullOr<VectorOrArrayOrNumber>, time?: number, duration?: number): CircleActor {
    position = position !== null ? Vector.from(position) : position;
    const [x, y] = position ? [position.x, position.y] : [null, null];
    this.setAttribute(this.circle, 'cx', 'position', x, time, duration);
    this.setAttribute(this.circle, 'cy', 'position', y, time, duration);
    return this;
  }

  setRadius(radius: NullOr<number>, time?: number, duration?: number): CircleActor {
    if (radius !== null && (!Number.isFinite(radius) || radius < 0.0)) {
      throw Error('`radius` must be either `null` or a non-negative real number');
    }

    this.setAttribute(this.circle, 'r', 'radius', radius, time, duration);

    return this;
  }
}
