import { Vector } from './indyplot/core/vector';
import { Box } from './indyplot/core/box';

export function makeRandomPoints(
  pointsNumber: number,
  range: Box,
  minDistance: number,
  attemptsLimit: number = 100
): Array<Vector> {
  const points: Array<Vector> = [];
  for (let i = 0; i < pointsNumber; ++i) {
    let attemptsCounter = 0;
    let isTooClose = false;
    do {
      const x = Math.random() * range.width + range.left;
      const y = Math.random() * range.height + range.top;
      const point = new Vector(x, y);
      isTooClose = Math.min(...points.map((v) => v.distance(point)), minDistance) < minDistance;
      if (!isTooClose) {
        points.push(point);
      }
      attemptsCounter++;
    } while (isTooClose && attemptsCounter < attemptsLimit);
  }

  return points;
}
