import { Animation } from '../animation';
import { Colors } from '../colors';
import { GroupActor } from '../indyplot/actors/group_actor';
import { Vector } from '../indyplot/core/vector';
import { Domain } from '../indyplot/scene';

const OFFSET: number = 1.0; // Default time offset in seconds.
const DURATION: number = 1.0; // Default transition duration in seconds.

export class BubbleSortAnimation extends Animation {
  private values: Array<number> = [];
  private barKeys: Array<string> = [];
  private barPositions: Array<Vector> = [];

  constructor() {
    const domain = new Domain(0.0, 0.0, 100.0, 50.0);
    super(domain);

    this.plot.getScene('main').addLayer(); // Indices.
  }

  // TODO4: Show semitransparent frame on top of the bars to highlight the bars
  //        which are still using in the sorting process. Width of this frame
  //        should be reducing after each iteration of the outer sorting loop.
  initialize() {
    const bars = this.plot.getActor<GroupActor>('bars');
    for (let i = 0; i < this.values.length; ++i) {
      const bar = bars.get(this.barKeys[i]).setFillColor(Colors.WHITE, 0.0);
      bar.setTranslation(this.barPositions[i], 0.0).setScale([1.0, 0.0], 0.0).setScale([1.0, 1.0], 3.0);
    }
  }

  // This method resets the state to those one before the last animation running.
  reset() {
    const values = [...this.values];
    this.clear();
    this.setValues(values);
  }

  // This method resets the state to the moment when the animation just was created.
  // It must call the base `clear` method first.
  clear() {
    super.clear();
    this.values = [];
    this.barKeys = [];
    this.barPositions = [];
  }

  setValues(values: Array<number>) {
    this.clear();
    this.values = values;

    const domain = this.plot.getScene('main').getDomain();
    this.plot.addBox(domain.leftTop, domain.size, { fc: Colors.DARK_GREY, sc: null });

    const [leftLimit, rightLimit] = [domain.left + 0.1 * domain.width, domain.right - 0.1 * domain.width];
    const barWidth = (rightLimit - leftLimit) / values.length;
    const [minBarHeight, maxBarHeight] = [0.1 * domain.height, 0.9 * domain.height];
    const [minValue, maxValue] = [Math.min(...values), Math.max(...values)];
    for (const [index, value] of this.values.entries()) {
      const barRelativeHeight = (value - minValue) / (maxValue - minValue);
      const barHeight = barRelativeHeight * (maxBarHeight - minBarHeight) + minBarHeight;
      const barPosition = new Vector(leftLimit + index * barWidth, domain.top);
      const indexPosition = [barPosition.x + 0.5 * barWidth, 0.99 * domain.bottom];
      const barSize = [barWidth, barHeight];
      const barKey = `bar-${index}`;

      this.plot.addBox([0.0, 0.0], barSize, { ak: barKey, t: barPosition, fc: Colors.WHITE, sc: 'none' });
      this.plot.addText(`${index}`, indexPosition, { li: 1, fc: Colors.BLACK, size: 5.0, alignment: 'cb' });

      this.barKeys[index] = barKey;
      this.barPositions[index] = barPosition;
    }

    this.plot.group(this.barKeys, { ak: 'bars' }).setTranslation([0.0, -domain.height]).setScale([1.0, -1.0]);
  }

  selectValue(index: number, select: boolean, offset: number = OFFSET, duration: number = DURATION) {
    const time = this.plot.getPlaybackDuration() + offset;
    const bars = this.plot.getActor<GroupActor>('bars');
    const color = select ? Colors.YELLOW : Colors.WHITE;
    bars.get(this.barKeys[index]).setFillColor(color, time, duration);
  }

  compareValues(index1: number, index2: number, areValuesArranged: boolean, offset?: number, duration?: number) {
    const time = this.plot.getPlaybackDuration() + (offset ?? OFFSET);
    const bars = this.plot.getActor<GroupActor>('bars');
    const color = areValuesArranged ? Colors.GREEN : Colors.RED;
    duration = duration ?? DURATION;
    bars.get(this.barKeys[index1]).setFillColor(color, time, duration);
    bars.get(this.barKeys[index2]).setFillColor(color, time, duration);
  }

  swapValues(index1: number, index2: number, offset: number = OFFSET, duration: number = DURATION) {
    const time = this.plot.getPlaybackDuration() + offset;
    const bars = this.plot.getActor<GroupActor>('bars');
    bars.get(this.barKeys[index1]).setTranslation(this.barPositions[index2], time, duration);
    bars.get(this.barKeys[index2]).setTranslation(this.barPositions[index1], time, duration);

    const key = this.barKeys[index1];
    this.barKeys[index1] = this.barKeys[index2];
    this.barKeys[index2] = key;
  }
}
