import { Size } from '../core/size';
import { SVGElementsFactory } from '../svg_elements_factory';
import { BoxOrArray, NullOr, SizeOrVectorOrArray, VectorOrArrayOrNumber } from '../core/types';
import { Vector } from '../core/vector';
import { Box } from '../core/box';
import { Actor, IActor } from './actor';

export interface IBoxActor extends IActor {
  box?: NullOr<Box>;
  position?: NullOr<VectorOrArrayOrNumber>;
  size?: NullOr<SizeOrVectorOrArray>;
}

export class BoxActor extends Actor {
  private box: SVGRectElement;

  constructor(parentContainer: SVGGElement) {
    super(parentContainer);
    this.box = SVGElementsFactory.createRectangle(this.container);
  }

  setParameters(parameters: IBoxActor) {
    super.setParameters(parameters);

    if (parameters.box !== undefined) {
      this.setBox(parameters.box);
    }

    if (parameters.position !== undefined) {
      this.setPosition(parameters.position);
    }

    if (parameters.size !== undefined) {
      this.setSize(parameters.size);
    }
  }

  setBox(box: NullOr<BoxOrArray>, time?: number, duration?: number): BoxActor {
    box = box !== null ? Box.from(box) : box;
    const [position, size] = box ? [box.leftTop, box.size] : [null, null];
    this.setPosition(position, time, duration);
    this.setSize(size, time, duration);
    return this;
  }

  setPosition(position: NullOr<VectorOrArrayOrNumber>, time?: number, duration?: number): BoxActor {
    position = position !== null ? Vector.from(position) : position;
    const [x, y] = position ? [position.x, position.y] : [null, null];
    this.setAttribute(this.box, 'x', 'position', x, time, duration);
    this.setAttribute(this.box, 'y', 'position', y, time, duration);
    return this;
  }

  setSize(size: NullOr<SizeOrVectorOrArray>, time?: number, duration?: number): BoxActor {
    size = size !== null ? Size.from(size) : size;
    const [width, height] = size ? [size.width, size.height] : [null, null];
    this.setAttribute(this.box, 'width', 'size', width, time, duration);
    this.setAttribute(this.box, 'height', 'size', height, time, duration);
    return this;
  }
}
