import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Accordion, AccordionDetails, AccordionSummary, Box, Paper, Stack } from '@mui/material';
import React from 'react';
import COMPANIES_DATA from '../../assets/companies/companies.json';
import WORK_EXPERIENCE_DATA from '../../assets/pages/home/work_experience/work_experience.json';
import { Image } from '../../components/image';
import { Text } from '../../components/text';
import { CONTEXT } from '../../context';
import { CompaniesData, TranslatableString } from '../../types';

type WorkExperienceItem = {
  period: TranslatableString;
  companyId: string;
  position: TranslatableString;
  descriptionFilePath: TranslatableString;
};

// Represents data storing in the `work_experience.json` file.
type WorkExperienceData = {
  title: TranslatableString;
  descriptionFilePath: TranslatableString;
  items: Array<WorkExperienceItem>;
};

export const WorkExperience: React.FC = () => {
  const [expandedPanelIndex, setExpandedPanelIndex] = React.useState<number>(0);

  const { language } = React.useContext(CONTEXT);

  const companiesData = React.useMemo((): CompaniesData => COMPANIES_DATA, []);

  const workExperienceData = React.useMemo((): WorkExperienceData => WORK_EXPERIENCE_DATA, []);

  const handleExpandedPanelChange = (panelIndex: number) => (event: React.SyntheticEvent, isExpanded: boolean) => {
    setExpandedPanelIndex(isExpanded ? panelIndex : -1);
  };

  const panelNodes = React.useMemo((): React.ReactNode => {
    const panelNodes = workExperienceData.items.map((v, i) => {
      const company = companiesData[v.companyId];
      return (
        <Accordion
          key={i}
          expanded={expandedPanelIndex === i}
          disableGutters={true}
          TransitionProps={{ timeout: 0 }}
          onChange={handleExpandedPanelChange(i)}
          sx={{ width: '100%', backgroundColor: 'background.card', boxShadow: 3 }}
        >
          <AccordionSummary expandIcon={<ExpandMoreIcon />}>
            <Image width='64px' imageLink={company.logoFilePath} />
            <Text marginLeft='10px'>{v.period[language]}</Text>
          </AccordionSummary>
          <AccordionDetails sx={{ display: 'flex', flexDirection: 'column' }}>
            <Text variant='h2' link={company.websiteURL}>
              {company.name[language]}
            </Text>
            <Text variant='h3'>{v.position[language]}</Text>
            <Text textFilePath={v.descriptionFilePath[language]} useMarkdown={true} />
          </AccordionDetails>
        </Accordion>
      );
    });

    return panelNodes;
  }, [workExperienceData, companiesData, language, expandedPanelIndex]);

  return (
    <Paper sx={{ width: '100%' }}>
      <Box display='flex' flexDirection='column' margin='10px'>
        <Text variant='h1' align='center'>
          {workExperienceData.title[language]}
        </Text>
        <Text textFilePath={workExperienceData.descriptionFilePath[language]} useMarkdown={true} />
        <Stack width='100%' spacing={1}>
          {panelNodes}
        </Stack>
      </Box>
    </Paper>
  );
};
