import { SVGElementsFactory } from './svg_elements_factory';
import { Alignment, FitSide } from './core/types';

export interface LayerInterface {
  isScreen?: boolean;
  isRelative?: boolean;
  isPannable?: boolean;
  isScalable?: boolean;
  fitSide?: FitSide;
  alignment?: Alignment;
}

export class Layer {
  readonly container: SVGGElement;

  // This layer type defined the screen CS. It combines features of the relative and non pannable/scalable layers.
  readonly isScreen: boolean;

  // TODO4: Do we need this `isRelative` flag? We now have an option to set relative positions for all actors
  //        using the `Actor.setIsRelativeAttribute` method.

  // Relative layer means all its elements have coordinates which are relative to the screen size.
  // Top left corner is always have (0, 0) coordinates and bottom right corner is always have (1, 1) coordinates.
  // Domain, panning and scaling are not applicable to the relative layer.
  readonly isRelative: boolean;

  // TODO4: Do we need these pannable and scalable flags? They seems strange and out of concept.
  readonly isPannable: boolean; // Using only when `isRelative` is `false`.
  readonly isScalable: boolean; // Using only when `isRelative` is `false`.

  readonly fitSide: FitSide; // Using only when `isRelative` is `true`.
  readonly alignment: Alignment; // Using only when `isRelative` is `true`.

  constructor(parentContainer: SVGGElement, parameters?: LayerInterface) {
    this.container = SVGElementsFactory.createGroup(parentContainer);
    parameters = parameters ?? {};
    this.isScreen = parameters.isScreen ?? false;
    this.isRelative = parameters.isRelative ?? false;
    this.isPannable = parameters.isPannable ?? true;
    this.isScalable = parameters.isScalable ?? true;
    this.fitSide = parameters.fitSide ?? 'min';
    this.alignment = parameters.alignment ?? 'start';
  }

  remove() {
    this.container.remove();
  }
}
