import { NullOr } from './core/types';

type OnTimeChangeHandler = (time: number) => void;

export class Timer {
  private updateInterval: number = 1.0; // Interval in seconds between the `time` updates.
  private timeLimit: NullOr<number> = null; // If `null` then `time` will be increasing infinitely.
  private isLooped: boolean = false; // If `true` then `time` will be reset to 0 when it exceeded the `timeLimit`.
  private timeScale: number = 1.0; // Time scaling factor.
  private onTimeChange: NullOr<OnTimeChangeHandler> = null; // Fires after each `time` value change.
  private isRunning: boolean = false; // Indicates that `Timer`'s internal update loop is working.
  private isPaused: boolean = false; // If `true` then `time` won't be increasing even the `Timer` is running.
  private time: number = 0.0; // Current time value. Can be changed internally by the `Timer` or externally by user.

  constructor(updateInterval: number = 1.0, timeLimit: NullOr<number> = null, isLooped: boolean = false) {
    this.setUpdateInterval(updateInterval);
    this.setTimeLimit(timeLimit);
    this.setIsLooped(isLooped);
  }

  setUpdateInterval(updateInterval: number) {
    if (!Number.isFinite(updateInterval) || updateInterval < 0.001) {
      throw Error('`updateInterval` must be a real number greater than or equal to 0.001 seconds');
    }

    this.updateInterval = updateInterval;
  }

  setTimeLimit(timeLimit: NullOr<number>) {
    if (timeLimit !== null && (!Number.isFinite(timeLimit) || timeLimit < 0.0)) {
      throw Error('`timeLimit` must be either `null` or a non-negative real number');
    }

    this.timeLimit = timeLimit;
  }

  getTimeLimit(): NullOr<number> {
    return this.timeLimit;
  }

  setIsLooped(isLooped: boolean) {
    if (isLooped && this.timeLimit === null) {
      throw Error('Looped mode can be activated only when `timeLimit` is set');
    }

    this.isLooped = isLooped;
  }

  setTimeScale(timeScale: number) {
    if (!Number.isFinite(timeScale) || timeScale <= 0.0) {
      throw Error('`timeScale` must be a positive real number');
    }

    this.timeScale = timeScale;
  }

  setOnTimeChangeHandler(onTimeChange: NullOr<OnTimeChangeHandler>) {
    this.onTimeChange = onTimeChange;
  }

  setTime(time: number) {
    if (!Number.isFinite(time) || time < 0.0) {
      throw Error('`time` must be a non-negative real number');
    }

    if (this.timeLimit !== null && time > this.timeLimit) {
      time = this.isLooped ? 0.0 : this.timeLimit;
    }

    const isTimeChanged = Math.abs(this.time - time) > Number.EPSILON;
    if (isTimeChanged) {
      this.time = time;
      this.notify();
    }
  }

  getTime(): number {
    return this.time;
  }

  start() {
    if (!this.isRunning) {
      this.run();
    }

    this.isPaused = false;
  }

  pause() {
    this.isPaused = true;
  }

  stop() {
    this.pause();
    this.isRunning = false;
    this.setTime(0.0);
  }

  private async run() {
    if (this.isRunning) {
      console.warn("Can not run the `Timer` because it's already running");
      return;
    }

    this.isRunning = true;
    while (this.isRunning) {
      await new Promise((v) => setTimeout(v, this.updateInterval * 1000.0));

      if (!this.isPaused) {
        const newTime = this.getTime() + this.updateInterval * this.timeScale;
        this.setTime(newTime);
      }
    }

    this.setTime(0.0);
  }

  private async notify() {
    if (this.onTimeChange != null) {
      this.onTimeChange(this.getTime());
    }
  }
}
