export type OnChangeHandler<T> = (value: T) => void;

type CompareHandler<T> = (value0: T, value1: T) => boolean;

type InterpolateHandler<T> = (startValue: T, startTime: number, endValue: T, endTime: number, time: number) => T;

class KeyFrame<T> {
  readonly value: T;
  readonly time: number;
  readonly duration?: number; // Transition duration from the previous key frame to this one.

  constructor(value: T, time: number, duration?: number) {
    if (!Number.isFinite(time) || time < 0.0) {
      throw Error('`time` must be a non-negative real number');
    }

    if (duration !== undefined && (!Number.isFinite(duration) || duration < 0.0)) {
      throw Error('`duration` must be either `undefined` or a non-negative real number');
    }

    this.value = value;
    this.time = time;
    this.duration = duration;
  }
}

export class Track<T> {
  private onChange: OnChangeHandler<T>;
  private compare?: CompareHandler<T>;
  private interpolate?: InterpolateHandler<T>;
  private keyFrames: Array<KeyFrame<T>> = [];
  private lastValue?: T;

  constructor(onChange: OnChangeHandler<T>, compare?: CompareHandler<T>, interpolate?: InterpolateHandler<T>) {
    this.onChange = onChange;
    this.compare = compare;
    this.interpolate = interpolate;
  }

  addValue(value: T, time: number, duration?: number) {
    if (!Number.isFinite(time) || time < 0.0) {
      throw Error('`time` must be a non-negative real number');
    }

    const keyFrame = new KeyFrame<T>(value, time, duration);

    // If key frame with specified time already exists then simply replace it with the new one.
    const timeDeltas = this.keyFrames.map((v) => Math.abs(time - v.time));
    const index = timeDeltas.indexOf(Math.min(...timeDeltas));
    if (timeDeltas[index] <= Number.EPSILON) {
      this.keyFrames[index] = keyFrame;
      return;
    }

    this.keyFrames.push(keyFrame);
    this.keyFrames.sort((v0, v1) => v0.time - v1.time);
  }

  getValue(time: number): T {
    if (!Number.isFinite(time) || time < 0.0) {
      throw Error('`time` must be a non-negative real number');
    }

    if (this.keyFrames.length === 0) {
      throw Error('`getValue` method requires the track to contain at least one value');
    }

    // The first value is extrapolating to -infinity, so it's returning as a result
    // when there are no values to the left of `time`.
    const leftIndex = this.findIndexOfNearestLeftKeyFrame(time);
    if (leftIndex < 0) {
      return this.keyFrames[0].value;
    }

    // Same, the last value is extrapolating to +infinity, so it will be returned if
    // there are no values to the right of `time` or interpolation method is not set.
    const leftValue = this.keyFrames[leftIndex].value;
    if (leftIndex === this.keyFrames.length - 1 || !this.interpolate) {
      return leftValue;
    }

    const leftTime = this.keyFrames[leftIndex].time;
    const rightIndex = leftIndex + 1;
    const rightTime = this.keyFrames[rightIndex].time;

    // If `duration` is not set by user then transition will be performing smoothly
    // during all time between the two key frames.
    const duration = this.keyFrames[rightIndex].duration ?? rightTime - leftTime;

    const middleTime = Math.max(rightTime - duration, leftTime);
    if (time < middleTime) {
      return leftValue;
    }

    const rightValue = this.keyFrames[rightIndex].value;
    if (rightTime - middleTime <= Number.EPSILON) {
      return rightValue;
    }

    const value = this.interpolate(leftValue, middleTime, rightValue, rightTime, time);

    return value;
  }

  removeAllValues() {
    this.keyFrames = [];
    this.lastValue = undefined;
  }

  getDuration(): number {
    return this.keyFrames.length ? (this.keyFrames.at(-1) as KeyFrame<T>).time : 0.0;
  }

  setTime(time: number) {
    const value = this.getValue(time);
    if (this.compare && this.lastValue !== undefined) {
      if (this.compare(this.lastValue, value)) {
        return;
      }
    }

    this.onChange(value);
    this.lastValue = value;
  }

  private findIndexOfNearestLeftKeyFrame(time: number): number {
    if (!Number.isFinite(time) || time < 0.0) {
      throw Error('`time` must be a non-negative real number');
    }

    let index = -1;
    while (index + 1 < this.keyFrames.length && this.keyFrames[index + 1].time <= time) {
      index++;
    }

    return index;
  }
}
