export class SVGElementsFactory {
  static createSVG(parent?: HTMLElement | SVGElement): SVGElement {
    return SVGElementsFactory.createElement('svg', parent) as SVGElement;
  }

  static createGroup(parent?: SVGElement): SVGGElement {
    return SVGElementsFactory.createElement('g', parent) as SVGGElement;
  }

  static createLine(parent?: SVGElement): SVGLineElement {
    return SVGElementsFactory.createElement('line', parent) as SVGLineElement;
  }

  static createRectangle(parent?: SVGElement): SVGRectElement {
    return SVGElementsFactory.createElement('rect', parent) as SVGRectElement;
  }

  static createCircle(parent?: SVGElement): SVGCircleElement {
    return SVGElementsFactory.createElement('circle', parent) as SVGCircleElement;
  }

  static createPath(parent?: SVGElement): SVGPathElement {
    return SVGElementsFactory.createElement('path', parent) as SVGPathElement;
  }

  static createText(parent?: SVGElement): SVGTextElement {
    return SVGElementsFactory.createElement('text', parent) as SVGTextElement;
  }

  // The SVG <tspan> element defines a subtext within a <text> element or another <tspan> element.
  // It allows for adjustment of the style and/or position of that subtext as needed.
  // https://developer.mozilla.org/en-US/docs/Web/SVG/Element/tspan
  static createTextSpan(parent?: SVGElement): SVGTSpanElement {
    return SVGElementsFactory.createElement('tspan', parent) as SVGTSpanElement;
  }

  static createMarker(parent?: SVGElement): SVGMarkerElement {
    return SVGElementsFactory.createElement('marker', parent) as SVGMarkerElement;
  }

  static createDefinitions(parent?: SVGElement): SVGDefsElement {
    return SVGElementsFactory.createElement('defs', parent) as SVGDefsElement;
  }

  static createClipPath(parent?: SVGElement): SVGClipPathElement {
    return SVGElementsFactory.createElement('clipPath', parent) as SVGClipPathElement;
  }

  private static createElement(elementType: string, parent?: Element): SVGElement {
    const element = document.createElementNS('http://www.w3.org/2000/svg', elementType);
    if (parent) {
      parent.appendChild(element);
    }
    return element;
  }
}
