export function checkImageLink(value: string): boolean {
  // TODO5: Need to additionally check that the `value` either a file path or a file URL (use `checkFileURL`).
  const imageExtensions = ['.jpg', '.png', '.svg', '.webp', '.gif'];
  const isImageLink = imageExtensions.some((v) => value.endsWith(v));
  return isImageLink;
}

export function checkURL(value: string): boolean {
  // TODO5: It's better to use regular expressions here.
  //        Google for RegExp patterns for URLs.
  return value.startsWith('http://') || value.startsWith('https://');
}

export function checkFileURL(value: string): boolean {
  // TODO5: It's better to use regular expressions here.
  //        Be careful. URL must be starting with protocol, otherwise we can't distinguish it from a local file name.
  //        Google for RegExp patterns for file URLs with protocol required.
  return value.startsWith('http://') || value.startsWith('https://');
}

export function checkYouTubeVideoURL(value: string): boolean {
  // TODO5: It's better to replace this method with `getYouTubeVideoID`.
  //        It must parse the `value` with RegEx and return the video ID if it's presented.
  //        Otherwise it must return undefined.
  //        This method also would be used in the `getYouTubeVideoPreviewURL`.
  const prefix = getYouTubeVideoURLPrefix();
  if (!value.startsWith(prefix)) {
    return false;
  }

  const idLength = getYouTubeVideoIDLength();
  const maxStartTimeParameterLength = 6; // ?t=nnn
  const minLength = prefix.length + idLength;
  const maxLength = minLength + maxStartTimeParameterLength;
  if (value.length < minLength || value.length > maxLength) {
    return false;
  }

  return true;
}

export function getYouTubeVideoPreviewURL(youTubeVideoURL: string): string {
  const prefixLength = getYouTubeVideoURLPrefix().length;
  const idLength = getYouTubeVideoIDLength();
  const id = youTubeVideoURL.substring(prefixLength, prefixLength + idLength);
  const previewURL = `https://img.youtube.com/vi/${id}/default.jpg`;
  return previewURL;
}

export async function sendPOSTRequest(url: string, parameters: Record<string, string>): Promise<any> {
  const request = { method: 'POST', headers: { 'Content-Type': 'application/json' }, body: JSON.stringify(parameters) };
  const response = await fetch(url, request);
  if (!response.ok) {
    throw new Error(`Failed to fetch response: ${response.status}`);
  }

  return response.json();
}

function getYouTubeVideoURLPrefix(): string {
  return 'https://www.youtube.com/watch?v=';
}

function getYouTubeVideoIDLength(): number {
  return 11;
}
