import { Box, Paper } from '@mui/material';
import React from 'react';
import PROFESSIONAL_SKILLS_DATA from '../../assets/pages/home/professional_skills/professional_skills.json';
import { Text } from '../../components/text';
import { CONTEXT } from '../../context';
import { TranslatableString } from '../../types';

// Represents data storing in the `professional_skills.json` file.
type ProfessionalSkillsData = {
  title: TranslatableString;
  descriptionFilePath: TranslatableString;
};

export const ProfessionalSkills: React.FC = () => {
  const { language } = React.useContext(CONTEXT);

  const professionalSkillsData = React.useMemo((): ProfessionalSkillsData => PROFESSIONAL_SKILLS_DATA, []);

  return (
    <Paper sx={{ width: '100%' }}>
      <Box display='flex' flexDirection='column' margin='10px'>
        <Text variant='h1' align='center'>
          {professionalSkillsData.title[language]}
        </Text>
        <Text textFilePath={professionalSkillsData.descriptionFilePath[language]} useMarkdown={true} />
      </Box>
    </Paper>
  );
};
