import { Link as MUILink } from '@mui/material';
import React from 'react';
import { Link as ReactRouterLink } from 'react-router-dom';
import { checkURL } from '../utils';

type LinkProps = {
  [key: string]: any;
};

export const Link: React.FC<LinkProps> = ({ ...props }) => {
  if (props.to !== undefined && props.href !== undefined) {
    console.error(`Wrong \`props\`: to="${props.to}", href="${props.href}"`);
    throw Error('One and only one of the `to` and `href` props must be defined');
  }

  const link = props.to || props.href;
  if (!link) {
    console.error(`Wrong \`props\`: to="${props.to}", href="${props.href}"`);
    throw Error('`props` must contain either `to` or `href` non empty string');
  }

  const linkElement = React.useMemo((): React.ReactElement => {
    const isUrl = checkURL(link);
    // Use the MaterialUI Link component for external URLs and the React Router Link component for page routing.
    if (isUrl) {
      return (
        <MUILink {...props} href={link}>
          {props.children}
        </MUILink>
      );
    } else {
      return (
        <MUILink {...props} component={ReactRouterLink} to={link}>
          {props.children}
        </MUILink>
      );
    }
  }, [link, props]);

  return linkElement;
};
