import { NumberTrack } from './number_track';

export class SoundTrack extends NumberTrack {
  private keys: Array<string>; // Maps a key index to a sound key.
  private sounds: Map<string, HTMLAudioElement>; // Maps a sound key to an actual audio object.
  private isMuted: boolean = false;

  constructor() {
    super((v) => this.play(v), false);
    this.keys = [];
    this.sounds = new Map<string, HTMLAudioElement>();
    this.addValue(-1, 0.0);
  }

  loadSound(fileName: string, key: string) {
    if (key === '') {
      throw Error('`key` must be a non-empty string');
    }

    if (this.sounds.has(key)) {
      throw Error('`key` must be a unique sound key');
    }

    const sound = new Audio(fileName);
    this.sounds.set(key, sound);
  }

  addSound(key: string, time: number) {
    if (!this.sounds.has(key)) {
      throw Error('`key` must be an existing sound key');
    }

    // The `Track` base class invokes its callback method only when value was changed.
    // So we can't just add a user defined sound key to the track, because the callback
    // won't be fired if there are a number of identical keys in a row. Instead we add
    // this key to the `keys` array and use its corresponding unique index as a track
    // value, which ensures the callback will be called for every added sound.
    this.keys.push(key);
    this.addValue(this.keys.length - 1, time);
  }

  removeAllSounds() {
    this.keys = [];
    this.sounds.clear();
    this.removeAllValues();
    this.addValue(-1, 0.0);
  }

  mute() {
    this.isMuted = true;
  }

  unmute() {
    this.isMuted = false;
  }

  private async play(keyIndex: number) {
    if (this.isMuted || keyIndex < 0 || keyIndex >= this.keys.length) {
      return;
    }

    const key = this.keys[keyIndex] as string;
    const sound = this.sounds.get(key) as HTMLAudioElement;
    sound.play();
  }
}
