import { Box, Button, Slider, Typography } from '@mui/material';
import React from 'react';

interface IGrahamScanControls {
  pointsNumber: number;
  maxPointsNumber: number;
  onAddRandomPointsButtonClick: (numberOfPointsToAdd: number) => void;
  onClearButtonClick: () => void;
}

export function GrahamScanControls({
  pointsNumber,
  maxPointsNumber,
  onAddRandomPointsButtonClick,
  onClearButtonClick,
}: IGrahamScanControls) {
  const [numberOfPointsToAdd, setNumberOfPointsToAdd] = React.useState(1);

  return (
    <Box sx={{ maxWidth: '250px', padding: '25px', display: 'flex', flexFlow: 'column', gap: '5px' }}>
      <Typography color={'text.primary'}>
        {`Add points by clicking with your mouse or press the "Add random points" button. `}
        {`Currently there are ${pointsNumber}/${maxPointsNumber} points.`}
      </Typography>
      <Slider min={1} max={maxPointsNumber} onChange={(e, v) => setNumberOfPointsToAdd(Array.isArray(v) ? v[0] : v)} />
      <Button variant='outlined' onClick={() => onAddRandomPointsButtonClick(numberOfPointsToAdd)}>
        {`Add ${numberOfPointsToAdd} random point${numberOfPointsToAdd > 1 ? 's' : ''}`}
      </Button>
      <Button variant='outlined' onClick={onClearButtonClick}>
        Clear
      </Button>
    </Box>
  );
}
