import { Close as CloseIcon, Image as ImageIcon, Movie as VideoIcon } from '@mui/icons-material';
import { Box, Fab, Modal } from '@mui/material';
import React from 'react';
import ReactPlayer from 'react-player';
import { Navigation } from 'swiper';
import 'swiper/css/bundle';
import { Swiper, SwiperSlide } from 'swiper/react';
import { TranslatableString } from '../../types';
import { checkFileURL, checkImageLink, checkYouTubeVideoURL, getYouTubeVideoPreviewURL } from '../../utils';
import { Image } from '../image';

export type MediaFile = {
  link: string;
  caption?: TranslatableString;
};

type MediaSliderProps = {
  mediaFiles: Array<MediaFile>;
  directoryPath?: string;
  slidesPerView: number;
  [key: string]: any;
};

export const MediaSlider: React.FC<MediaSliderProps> = ({ mediaFiles, directoryPath, slidesPerView, ...props }) => {
  const [isSliderOpen, setIsSliderOpen] = React.useState<boolean>(false);
  const [clickedSlideIndex, setClickedSlideIndex] = React.useState<number>(0);

  const imageStyle = React.useMemo(
    () => ({
      width: '100%',
      height: '100%',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      cursor: 'pointer'
    }),
    []
  );

  const iconStyle = React.useMemo(
    () => ({
      width: '50%',
      height: '50%',
      position: 'absolute',
      fill: 'white',
      stroke: 'black',
      strokeWidth: 0.15,
      opacity: '50%',
      ':hover': { opacity: '100%' }
    }),
    []
  );

  const getOpenImageNode = React.useCallback(
    (link: string): React.ReactNode => <Image imageLink={link} sx={imageStyle} />,
    [imageStyle]
  );

  const getPreviewImageNode = React.useCallback(
    (link: string): React.ReactNode => {
      return (
        <Image imageLink={link} sx={imageStyle}>
          <ImageIcon sx={iconStyle} />
        </Image>
      );
    },
    [imageStyle, iconStyle]
  );

  const getOpenYouTubeVideoNode = React.useCallback((url: string): React.ReactNode => <ReactPlayer url={url} />, []);

  const getPreviewYouTubeVideoNode = React.useCallback(
    (url: string): React.ReactNode => {
      const previewURL = getYouTubeVideoPreviewURL(url);
      return (
        <Image imageLink={previewURL} sx={imageStyle}>
          <VideoIcon sx={iconStyle} />
        </Image>
      );
    },
    [imageStyle, iconStyle]
  );

  const getSlideNodes = React.useCallback(
    (isOpen: boolean): Array<React.ReactNode> => {
      const slideNodes: Array<React.ReactNode> = [];
      for (const mediaFile of mediaFiles) {
        const isImageLink = checkImageLink(mediaFile.link);
        const isYouTubeVideoURL = checkYouTubeVideoURL(mediaFile.link);
        let content: React.ReactNode;
        if (isImageLink && !isYouTubeVideoURL) {
          const isURL = checkFileURL(mediaFile.link);
          const link = isURL || !directoryPath ? mediaFile.link : `${directoryPath}/${mediaFile.link}`;
          content = isOpen ? getOpenImageNode(link) : getPreviewImageNode(link);
        } else if (isYouTubeVideoURL && !isImageLink) {
          content = isOpen ? getOpenYouTubeVideoNode(mediaFile.link) : getPreviewYouTubeVideoNode(mediaFile.link);
        } else {
          console.error(`Wrong media file link: "${mediaFile.link}"`);
          throw Error('`mediaLinks` must contain only image links or YouTube video URLs');
        }

        const slideNode = (
          <SwiperSlide key={mediaFile.link} style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
            {content}
          </SwiperSlide>
        );
        slideNodes.push(slideNode);
      }

      return slideNodes;
    },
    [
      mediaFiles,
      directoryPath,
      getOpenImageNode,
      getPreviewImageNode,
      getOpenYouTubeVideoNode,
      getPreviewYouTubeVideoNode
    ]
  );

  const openSlideNodes = React.useMemo(() => getSlideNodes(true), [getSlideNodes]);

  const previewSlideNodes = React.useMemo(() => getSlideNodes(false), [getSlideNodes]);

  const handleSwiperClick = React.useCallback((swiper: any) => {
    if (swiper.clickedIndex === undefined) {
      return;
    }

    setClickedSlideIndex(swiper.clickedIndex);
    setIsSliderOpen(true);
  }, []);

  return (
    <Box width='100%' height='100%'>
      <Swiper
        {...props}
        style={{ height: '100%' }}
        slidesPerView={slidesPerView}
        spaceBetween={5}
        navigation={true}
        modules={[Navigation]}
        onClick={handleSwiperClick}
      >
        {previewSlideNodes}
      </Swiper>
      <Modal open={isSliderOpen} onClose={() => setIsSliderOpen(false)} sx={{ display: 'flex', alignItems: 'center' }}>
        <>
          <Swiper
            style={{ width: '95%', height: '95%' }}
            initialSlide={clickedSlideIndex}
            navigation={true}
            modules={[Navigation]}
          >
            {openSlideNodes}
          </Swiper>
          <Fab sx={{ position: 'absolute', right: '5%', top: '5%', zIndex: 1 }} onClick={() => setIsSliderOpen(false)}>
            <CloseIcon />
          </Fab>
        </>
      </Modal>
    </Box>
  );
};
