import { Animation } from '../animation';
import { Colors } from '../colors';
import { BoxActor } from '../indyplot/actors/box_actor';
import { CircleActor } from '../indyplot/actors/circle_actor';
import { GroupActor } from '../indyplot/actors/group_actor';
import { LineActor } from '../indyplot/actors/line_actor';
import { TextActor } from '../indyplot/actors/text_actor';
import { Box } from '../indyplot/core/box';
import { BoxOrArray, ColorOrStringOrArray, VectorOrArrayOrNumber } from '../indyplot/core/types';
import { Vector } from '../indyplot/core/vector';
import { Domain } from '../indyplot/scene';

const OFFSET: number = 1.0; // Default time offset in seconds.
const DURATION: number = 1.0; // Default transition duration in seconds.

export class KDTreeAnimation extends Animation {
  private points: Array<Vector> = [];

  constructor() {
    const domain = new Domain(0.0, 0.0, 100.0, 0.0, 'height');
    super(domain);

    const scene = this.plot.getScene('main');
    scene.setIsYFlipped(true);
    scene.addLayer(); // Search circle.
    scene.addLayer(); // Points.
    scene.addLayer(); // Cell.
    scene.addLayer(); // Axis.
  }

  initialize() {
    this.plot.addBox(0.0, [0.0, 0.0], { ak: 'cell-bounds', fc: Colors.YELLOW, sc: 'none' }).setOpacity(0.0, 0.0);
    this.plot.addText('', 1.0, { ak: 'cell-index', fc: Colors.WHITE, size: 5.0, alignment: 'lt', isYFlipped: true });
    this.plot.getActor('cell-index').setStrokeColor(Colors.BLACK).setStrokeWidth(0.075).setOpacity(0.0, 0.0);
    this.plot.group(['cell-bounds', 'cell-index'], { li: 3, ak: 'cell' }).setOpacity(0.0);
    this.plot.addLine(0.0, 0.0, { li: 4, ak: 'axis', c: Colors.BLUE, o: 0.0, isArrowCap: true }).setOpacity(0.0, 0.0);
    this.plot.addCircle(0.0, 0.0, { li: 1, ak: 'search-circle', fc: 'none', sc: Colors.BLUE }).setOpacity(0.0, 0.0);
    this.plot.addLine(0.0, 0.0, { li: 1, ak: 'search-radius', sc: Colors.BLUE }).setOpacity(0.0, 0.0);
  }

  // This method resets the state to those one before the last animation running.
  reset() {
    const points = this.points.map((v) => v.copy());
    this.clear();
    points.forEach((v) => this.addPoint(v));
  }

  // This method resets the state to the moment when the animation just was created.
  // It must call the base `clear` method.
  clear() {
    this.points = [];
    super.clear();
  }

  // This method removes the `Plot` and must be called just before the unmounting.
  // It must call the base `remove` method.
  remove(): void {
    this.points = [];
    super.remove();
  }

  addPoint(point: VectorOrArrayOrNumber) {
    point = Vector.from(point);
    this.points.push(point);

    const pointIndex = this.points.length - 1;
    const label = `[${Math.round(point.x)}, ${Math.round(point.y)}]`;
    this.plot.addCircle(point, 1.5, { ak: 'circle', c: Colors.WHITE, sw: 0.0, so: 0.0 });
    this.plot.addText(pointIndex.toString(), point, { ak: 'index', fc: Colors.BLACK, size: 1.5, alignment: 'cc' });
    this.plot.addText(label, [point.x, point.y + 1.5], { ak: 'label', fc: Colors.WHITE, size: 1.5, alignment: 'ct' });
    this.plot.getActor('circle').setColor(Colors.WHITE, 0.0).setStrokeWidth(0.0, 0.0).setStrokeOpacity(0.0, 0.0);
    this.plot.getActor<TextActor>('index').setIsYFlipped(true);
    this.plot.getActor<TextActor>('label').setIsYFlipped(true);
    this.plot.group(['circle', 'index', 'label'], { li: 2, ak: `point${pointIndex}` }).setOpacity(0.0, 0.0);
  }

  setQueryPoint(queryPoint: VectorOrArrayOrNumber) {
    queryPoint = Vector.from(queryPoint);

    const label = `[${Math.round(queryPoint.x)}, ${Math.round(queryPoint.y)}]`;
    if (this.plot.isActorExist('query-point')) {
      const queryPointActor = this.plot.getActor<GroupActor>('query-point');
      queryPointActor.setTranslation(queryPoint);
      queryPointActor.get<TextActor>('label').setText(label);
    } else {
      this.plot.addCircle(0.0, 1.5, { ak: 'circle', fc: Colors.CYAN, sc: 'none' });
      this.plot.addText(label, [0.0, 1.5], { ak: 'label', fc: Colors.WHITE, size: 1.5, alignment: 'ct' });
      this.plot.getActor<TextActor>('label').setIsYFlipped(true);
      const queryPointActor = this.plot.group(['circle', 'label'], { li: 2, ak: 'query-point' });
      queryPointActor.setTranslation(queryPoint).setOpacity(0.0, 0.0);
    }
  }

  showPoints(offset: number = OFFSET, duration: number = DURATION) {
    const showPointDuration = duration / this.points.length;
    let time = this.plot.getPlaybackDuration() + offset - duration + showPointDuration;
    for (const pointIndex of this.points.keys()) {
      this.plot.getActor(`point${pointIndex}`).setOpacity(1.0, time, showPointDuration);
      time += showPointDuration;
    }
  }

  showCell(show: boolean, offset: number = OFFSET, duration: number = DURATION) {
    const time = this.plot.getPlaybackDuration() + offset;
    const cellActor = this.plot.getActor<GroupActor>('cell');
    cellActor.get('cell-bounds').setOpacity(show ? 0.25 : 0.0, time, duration);
    cellActor.get('cell-index').setOpacity(show ? 1.0 : 0.0, time, duration);
  }

  setCell(cellBounds: BoxOrArray, cellIndex: number, offset: number = OFFSET, duration: number = DURATION) {
    cellBounds = Box.from(cellBounds);

    const time = this.plot.getPlaybackDuration() + offset;
    const cellActor = this.plot.getActor<GroupActor>('cell');
    cellActor.setTranslation(cellBounds.leftTop, time, duration);
    cellActor.get<BoxActor>('cell-bounds').setSize(cellBounds.size, time, duration);
    cellActor.get<TextActor>('cell-index').setText(cellIndex.toString(), time);
  }

  showCellPoints(pointIndices: Array<number>, show: boolean, offset: number = OFFSET, duration: number = DURATION) {
    this.markPoints(pointIndices, show ? Colors.YELLOW : Colors.WHITE, offset, duration);
  }

  showLeafPoints(pointIndices: Array<number>, show: boolean, offset: number = OFFSET, duration: number = DURATION) {
    this.markPoints(pointIndices, show ? Colors.GREEN : Colors.WHITE, offset, duration);
  }

  showMedianPoint(pointIndex: number, show: boolean, offset: number = OFFSET, duration: number = DURATION) {
    this.markPoints([pointIndex], show ? Colors.RED : Colors.WHITE, offset, duration);
  }

  showQueryPoint(show: boolean, offset: number = OFFSET, duration: number = DURATION) {
    const time = this.plot.getPlaybackDuration() + offset;
    this.plot.getActor('query-point').setOpacity(show ? 1.0 : 0.0, time, duration);
  }

  showClosestPoint(pointIndex: number, show: boolean, offset: number = OFFSET, duration: number = DURATION) {
    const time = this.plot.getPlaybackDuration() + offset;
    const pointActorCircle = this.plot.getActor<GroupActor>(`point${pointIndex}`).get<CircleActor>('circle');
    pointActorCircle.setColor(show ? Colors.DARK_GREEN : Colors.WHITE, time, duration);
    pointActorCircle.setStrokeWidth(show ? 1.0 : 0.0, time, duration);
    pointActorCircle.setStrokeOpacity(show ? 1.0 : 0.0, time, duration);
  }

  showAxis(cellBounds: BoxOrArray, axisIndex: number, offset: number = OFFSET, duration: number = DURATION) {
    cellBounds = Box.from(cellBounds);

    const time = this.plot.getPlaybackDuration();
    const axisActor = this.plot.getActor<LineActor>('axis');
    let startPosition, endPosition;
    if (axisIndex === 0) {
      startPosition = [cellBounds.left + 0.2 * cellBounds.width, cellBounds.center.y];
      endPosition = [cellBounds.right - 0.25 * cellBounds.width, cellBounds.center.y];
    } else {
      startPosition = [cellBounds.center.x, cellBounds.top + 0.2 * cellBounds.height];
      endPosition = [cellBounds.center.x, cellBounds.bottom - 0.25 * cellBounds.height];
    }
    axisActor.setStartPosition(startPosition, time, 0.0);
    axisActor.setEndPosition(endPosition, time, 0.0);
    axisActor.setOpacity(1.0, time + offset, duration);
  }

  hideAxis(offset: number = OFFSET, duration: number = DURATION) {
    const time = this.plot.getPlaybackDuration();
    this.plot.getActor<LineActor>('axis').setOpacity(0.0, time + offset, duration);
  }

  createSplittingEdge(bounds: BoxOrArray, medianValue: number, axisIndex: number, offset?: number, duration?: number) {
    bounds = Box.from(bounds);

    const time = this.plot.getPlaybackDuration() + (offset ?? OFFSET);
    const startPosition = axisIndex ? [bounds.left, medianValue] : [medianValue, bounds.top];
    const endPosition = axisIndex ? [bounds.right, medianValue] : [medianValue, bounds.bottom];
    const splittingEdgeActor = this.plot.addLine(startPosition, endPosition, { c: Colors.RED, sw: 0.5, o: 0.0 });
    splittingEdgeActor.setOpacity(0.0, 0.0).setOpacity(1.0, time, duration ?? DURATION);
  }

  showPointIndices(pointIndices: Array<number>, show: boolean, offset: number = OFFSET, duration: number = DURATION) {
    const time = this.plot.getPlaybackDuration() + offset;
    pointIndices.forEach((v) => {
      const opacity = show ? 1.0 : 0.0;
      this.plot.getActor<GroupActor>(`point${v}`).get('index').setOpacity(opacity, time, duration);
    });
  }

  setPointIndices(pointIndices: Array<number>, offset: number = OFFSET, duration: number = DURATION) {
    const time = this.plot.getPlaybackDuration() + offset;
    for (const [index, value] of pointIndices.entries()) {
      this.plot.getActor<GroupActor>(`point${value}`).get<TextActor>('index').setText(index.toString(), time);
    }
  }

  showSearchCircle(show: boolean, offset: number = OFFSET, duration: number = DURATION) {
    const time = this.plot.getPlaybackDuration() + offset;
    this.plot.getActor('search-circle').setOpacity(show ? 1.0 : 0.0, time, duration);
    this.plot.getActor('search-radius').setOpacity(show ? 1.0 : 0.0, time, duration);
  }

  setSearchCircle(center: VectorOrArrayOrNumber, point: VectorOrArrayOrNumber, offset?: number, duration?: number) {
    const time = this.plot.getPlaybackDuration() + (offset ?? OFFSET);
    const radius = Vector.from(center).distance(point);
    const searchCircleActor = this.plot.getActor<CircleActor>('search-circle');
    const searchRadiusActor = this.plot.getActor<LineActor>('search-radius');
    duration = duration ?? DURATION;
    searchCircleActor.setPosition(center, time, duration).setRadius(radius, time, duration);
    searchRadiusActor.setStartPosition(center, time).setEndPosition(point, time, duration);
  }

  private markPoints(pointIndices: Array<number>, color: ColorOrStringOrArray, offset?: number, duration?: number) {
    const time = this.plot.getPlaybackDuration() + (offset ?? OFFSET);
    duration = duration ?? DURATION;
    pointIndices.forEach((v) =>
      this.plot.getActor<GroupActor>(`point${v}`).get('circle').setColor(color, time, duration)
    );
  }
}
