import Dialog from '@mui/material/Dialog';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import { useTheme } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';

interface IModalMessage {
  title: string;
  message: string;
  isShown: boolean;
  onHide: () => void;
}

export function ModalMessage({ title, message, isShown, onHide }: IModalMessage) {
  const theme = useTheme();
  const isFullScreen = useMediaQuery(theme.breakpoints.down('md'));
  const fontSize = isFullScreen ? 24 : 16;

  return (
    <Dialog open={isShown} fullScreen={isFullScreen} onClick={onHide}>
      <DialogTitle id='responsive-dialog-title'>{title}</DialogTitle>
      <DialogContent sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
        <DialogContentText sx={{ fontSize: fontSize }}>{message}</DialogContentText>
      </DialogContent>
    </Dialog>
  );
}
