import { VectorOrArrayOrNumber } from './types';

export class Vector {
  readonly [index: number]: number;
  private _x: number = 0.0;
  private _y: number = 0.0;

  constructor(x: number = 0.0, y: number = 0.0) {
    this.x = x;
    this.y = y;
  }

  set x(x: number) {
    if (!Number.isFinite(x)) {
      throw Error('`x` must be a real number');
    }

    this._x = x;
  }

  get x(): number {
    return this._x;
  }

  set y(y: number) {
    if (!Number.isFinite(y)) {
      throw Error('`y` must be a real number');
    }

    this._y = y;
  }

  get y(): number {
    return this._y;
  }

  from(value: VectorOrArrayOrNumber) {
    if (Array.isArray(value)) {
      this.fromArray(value);
    } else if (typeof value === 'number') {
      this.fromArray([value, value]);
    } else {
      this.x = value.x;
      this.y = value.y;
    }
  }

  copy(): Vector {
    return new Vector(this.x, this.y);
  }

  toString(): string {
    return `translate(${this.x}, ${this.y})`;
  }

  toArray(): Array<number> {
    return [this.x, this.y];
  }

  toTuple(): [number, number] {
    return [this.x, this.y];
  }

  isEqual(vector: VectorOrArrayOrNumber, tolerance: number = Number.EPSILON): boolean {
    if (!Number.isFinite(tolerance) || tolerance < 0.0) {
      throw Error('`tolerance` must be a non-negative real number');
    }

    vector = Vector.from(vector);
    const dx = this.x - vector.x;
    const dy = this.y - vector.y;
    const distance = Math.sqrt(dx ** 2.0 + dy ** 2.0);
    const isEqual = distance <= tolerance;

    return isEqual;
  }

  lerp(endVector: VectorOrArrayOrNumber, s: number): Vector {
    if (!Number.isFinite(s) || s < 0.0 || s > 1.0) {
      throw Error('`s` must be a real number from range [0.0, 1.0]');
    }

    endVector = Vector.from(endVector);
    const x = this.x + s * (endVector.x - this.x);
    const y = this.y + s * (endVector.y - this.y);
    const vector = new Vector(x, y);

    return vector;
  }

  add(value: VectorOrArrayOrNumber): Vector {
    return typeof value !== 'number' ? this.addVector(value) : this.addVector([value, value]);
  }

  sub(value: VectorOrArrayOrNumber): Vector {
    return typeof value !== 'number' ? this.subVector(value) : this.subVector([value, value]);
  }

  mul(value: VectorOrArrayOrNumber): Vector {
    return typeof value !== 'number' ? this.mulVector(value) : this.mulVector([value, value]);
  }

  div(vector: VectorOrArrayOrNumber): Vector {
    vector = Vector.from(vector);
    this.x /= vector.x;
    this.y /= vector.y;
    return this;
  }

  negate(): Vector {
    this.x *= -1.0;
    this.y *= -1.0;
    return this;
  }

  normalize(): Vector {
    const length = this.norm();
    this.x /= length;
    this.y /= length;
    return this;
  }

  fromPolarCoordinates(angle: number, radius: number): Vector {
    if (!Number.isFinite(angle)) {
      throw Error('`angle` must be a real number');
    }

    if (!Number.isFinite(radius) || radius < 0.0) {
      throw Error('`radius` must be a non-negative real number');
    }

    angle *= Math.PI / 180.0;
    this.x = radius * Math.cos(angle);
    this.y = radius * Math.sin(angle);

    return this;
  }

  negative(): Vector {
    return new Vector(-this.x, -this.y);
  }

  norm(): number {
    return Math.sqrt(this.x ** 2.0 + this.y ** 2.0);
  }

  dot(vector: VectorOrArrayOrNumber): number {
    vector = Vector.from(vector);
    const dot = this.x * vector.x + this.y * vector.y;
    return dot;
  }

  distance(vector: VectorOrArrayOrNumber): number {
    return Vector.sub(vector, this).norm();
  }

  angle(vector: VectorOrArrayOrNumber): number {
    vector = Vector.from(vector);
    const angle = (Math.acos(this.dot(vector) / (this.norm() * vector.norm())) * 180.0) / Math.PI;
    return angle;
  }

  min(): number {
    return Math.min(this.x, this.y);
  }

  minVector(): Vector {
    const min = Math.min(this.x, this.y);
    return new Vector(min, min);
  }

  max(): number {
    return Math.max(this.x, this.y);
  }

  maxVector(): Vector {
    const max = Math.max(this.x, this.y);
    return new Vector(max, max);
  }

  static add(vector: VectorOrArrayOrNumber, value: VectorOrArrayOrNumber): Vector {
    return Vector.from(vector).add(value);
  }

  static sub(vector: VectorOrArrayOrNumber, value: VectorOrArrayOrNumber): Vector {
    return Vector.from(vector).sub(value);
  }

  static mul(vector: VectorOrArrayOrNumber, value: VectorOrArrayOrNumber): Vector {
    return Vector.from(vector).mul(value);
  }

  static div(vector: VectorOrArrayOrNumber, value: VectorOrArrayOrNumber): Vector {
    return Vector.from(vector).div(value);
  }

  static dot(vector0: VectorOrArrayOrNumber, vector1: VectorOrArrayOrNumber): number {
    return Vector.from(vector0).dot(vector1);
  }

  static zeros(): Vector {
    return new Vector(0.0, 0.0);
  }

  static ones(): Vector {
    return new Vector(1.0, 1.0);
  }

  static unitX(): Vector {
    return new Vector(1.0, 0.0);
  }

  static unitY(): Vector {
    return new Vector(0.0, 1.0);
  }

  static isVector(value: unknown): value is Vector {
    return value instanceof Vector;
  }

  static from(value: VectorOrArrayOrNumber): Vector {
    const vector = new Vector();
    vector.from(value);
    return vector;
  }

  private fromArray(array: Array<number>) {
    if (array.length !== 2) {
      throw Error('`array` must be an array of length 2');
    }

    [this.x, this.y] = array;
  }

  private addVector(vector: VectorOrArrayOrNumber): Vector {
    vector = Vector.from(vector);
    this.x += vector.x;
    this.y += vector.y;
    return this;
  }

  private subVector(vector: VectorOrArrayOrNumber): Vector {
    vector = Vector.from(vector);
    this.x -= vector.x;
    this.y -= vector.y;
    return this;
  }

  private mulVector(vector: VectorOrArrayOrNumber): Vector {
    vector = Vector.from(vector);
    this.x *= vector.x;
    this.y *= vector.y;
    return this;
  }

  private divVector(vector: VectorOrArrayOrNumber): Vector {
    vector = Vector.from(vector);
    this.x /= vector.x;
    this.y /= vector.y;
    return this;
  }
}
