import { getInterpolationParameter } from '../utils';
import { Vector } from '../core/vector';
import { OnChangeHandler, Track } from './track';

export class VectorTrack extends Track<Vector> {
  constructor(onChange: OnChangeHandler<Vector>) {
    const compare = (value0: Vector, value1: Vector) => value0.isEqual(value1, Number.EPSILON);

    const interpolate = (startValue: Vector, startTime: number, endValue: Vector, endTime: number, time: number) => {
      const s = getInterpolationParameter(startTime, endTime, time);
      const value = startValue.lerp(endValue, s);
      return value;
    };

    super(onChange, compare, interpolate);
  }
}
