import { SVGElementsFactory } from '../svg_elements_factory';
import { IActor } from './actor';
import { GroupActor } from './group_actor';
import { LineActor } from './line_actor';
import { BoxActor } from './box_actor';

export interface IGridActor extends IActor {
  widths?: Array<number>;
  heights?: Array<number>;
}

// TODO4: Need to refactor this class. Need to split `setGrid()` to a number of small private methods.

export class GridActor extends GroupActor {
  private cells: Array<Array<BoxActor>> = [];
  private rowBorders: Array<LineActor> = [];
  private columnBorders: Array<LineActor> = [];
  private bounds: BoxActor;
  private cellsLayer: SVGGElement;
  private bordersLayer: SVGGElement;
  private boundsLayer: SVGGElement;

  constructor(parentContainer: SVGGElement) {
    super(parentContainer);
    this.cellsLayer = SVGElementsFactory.createGroup(this.container);
    this.bordersLayer = SVGElementsFactory.createGroup(this.container);
    this.boundsLayer = SVGElementsFactory.createGroup(this.container);
    this.bounds = new BoxActor(this.boundsLayer);
    this.bounds.setFillColor('none');
  }

  setGrid(rowHeights: Array<number>, columnWidths: Array<number>): GridActor {
    // Add missing row cells.
    while (this.cells.length < rowHeights.length) {
      const cellsRow = [];
      for (let columnIndex = 0; columnIndex < columnWidths.length; ++columnIndex) {
        const cell = new BoxActor(this.cellsLayer);
        cell.setParameters({ fc: 'none', sc: 'none' });
        cellsRow.push(cell);
      }
      this.cells.push(cellsRow);
    }

    // Add missing row borders.
    while (this.rowBorders.length < rowHeights.length - 1) {
      const border = new LineActor(this.bordersLayer);
      this.rowBorders.push(border);
    }

    // Remove redundant row cells.
    while (this.cells.length > rowHeights.length) {
      const lastRow = this.cells.pop() as Array<BoxActor>;
      lastRow.forEach((v) => v.remove());
    }

    // Remove redundant row borders.
    while (this.rowBorders.length > rowHeights.length - 1) {
      const lastRow = this.rowBorders.pop() as LineActor;
      lastRow.remove();
    }

    for (let rowIndex = 0; rowIndex < this.cells.length; ++rowIndex) {
      // Add missing column cells.
      while (this.cells[rowIndex].length < columnWidths.length) {
        const cell = new BoxActor(this.cellsLayer);
        cell.setParameters({ fc: 'none', sc: 'none' });
        this.cells[rowIndex].push(cell);
      }

      // Remove redundant column cells.
      while (this.cells[rowIndex].length > columnWidths.length) {
        const lastColumn = this.cells[rowIndex].pop() as BoxActor;
        lastColumn.remove();
      }
    }

    // Add missing column borders.
    while (this.columnBorders.length < columnWidths.length - 1) {
      const border = new LineActor(this.bordersLayer);
      this.columnBorders.push(border);
    }

    // Remove redundant column borders.
    while (this.columnBorders.length > columnWidths.length - 1) {
      const lastColumn = this.columnBorders.pop() as LineActor;
      lastColumn.remove();
    }

    // Adjust positions and dimensions of cells.
    let x = 0.0;
    let y = 0.0;
    for (let rowIndex = 0; rowIndex < rowHeights.length; ++rowIndex) {
      const height = rowHeights[rowIndex];
      x = 0.0;
      for (let columnIndex = 0; columnIndex < columnWidths.length; ++columnIndex) {
        const width = columnWidths[columnIndex];
        this.cells[rowIndex][columnIndex].setPosition([x, y]);
        this.cells[rowIndex][columnIndex].setSize([width, height]);
        x += width;
      }
      y += height;
    }

    // Adjust positions of row borders.
    const gridWidth = columnWidths.reduce((sum, value) => sum + value, 0.0);
    y = 0.0;
    for (let rowIndex = 0; rowIndex < rowHeights.length - 1; ++rowIndex) {
      y += rowHeights[rowIndex];
      this.rowBorders[rowIndex].setParameters({ startPosition: [0.0, y], endPosition: [gridWidth, y] });
    }

    // Adjust positions of column borders.
    const gridHeight = rowHeights.reduce((sum, value) => sum + value, 0.0);
    x = 0.0;
    for (let columnIndex = 0; columnIndex < columnWidths.length - 1; ++columnIndex) {
      x += columnWidths[columnIndex];
      this.columnBorders[columnIndex].setParameters({ startPosition: [x, 0.0], endPosition: [x, gridHeight] });
    }

    // Adjust bounds size.
    this.bounds.setSize([gridWidth, gridHeight]);

    return this;
  }
}
