import { KDTreeAnimation } from './k_d_tree_animation';

// TODO4: Add tree visualization.

type Point = [number, number]; // x, y
type Box = [number, number, number, number]; // x (left), y (top), width, height

export class KDTree {
  static build(points: Array<Point>, animation: KDTreeAnimation, maxPointsNumberPerLeafCell: number = 1) {
    let cellIndex = -1;

    const doBuildStep = (pointIndices: Array<number>, cellBounds: Box, levelIndex: number): Map<string, any> => {
      // TODO4: Display current level index.

      cellIndex++; // Increase index of the current cell. Each cell of the tree has its own unique index.

      animation.setCell(cellBounds, cellIndex);
      animation.showCellPoints(pointIndices, true, 2.0);

      // Make an object representing the current cell.
      const cell = new Map<string, any>([
        ['cellIndex', cellIndex],
        ['cellBounds', cellBounds],
      ]);

      // If number of points in the current cell is more than `maxPointsNumberPerLeafCell` then split it into half.
      // Otherwise consider these points as leaf points.
      if (pointIndices.length > maxPointsNumberPerLeafCell) {
        animation.showComment(
          `Current cell contains ${pointIndices.length} ${pointIndices.length > 1 ? 'points' : 'point'}, ` +
            `which is more than ${maxPointsNumberPerLeafCell}. Need to split the cell.`,
          5.0
        );
        animation.hideComment(5.0);

        // Define axis along which the current cell will be splitted. X - 0, Y - 1.
        const axisIndex = levelIndex % 2;
        animation.showComment(`Sort the points by the ${axisIndex ? 'Y' : 'X'} coordinate.`, 5.0);
        animation.setPointIndices(pointIndices);
        animation.showPointIndices(pointIndices, true);
        animation.showAxis(cellBounds, axisIndex);
        animation.hideAxis(2.0);
        animation.hideComment(5.0);

        // Sort points along the current axis to define index of median point and median value.
        pointIndices.sort((v1, v2) => points[v1][axisIndex] - points[v2][axisIndex]);
        animation.setPointIndices(pointIndices);

        const medianPointIndex = Math.floor(pointIndices.length / 2);
        animation.showMedianPoint(pointIndices[medianPointIndex], true, 2.0);
        animation.showComment('Find the median point.', 5.0);
        animation.hideComment(5.0);

        // Split the current point set and corresponding bounding rectangle by median value to "left" and "right".
        const medianValue = points[pointIndices[medianPointIndex]][axisIndex];
        const leftPointsSubset = pointIndices.slice(0, medianPointIndex);
        const rightPointsSubset = pointIndices.slice(medianPointIndex);
        let leftCellBounds, rightCellBounds;
        if (axisIndex === 0) {
          leftCellBounds = [cellBounds[0], cellBounds[1], medianValue - cellBounds[0], cellBounds[3]];
          rightCellBounds = [medianValue, cellBounds[1], cellBounds[2] - leftCellBounds[2], cellBounds[3]];
        } else {
          leftCellBounds = [cellBounds[0], cellBounds[1], cellBounds[2], medianValue - cellBounds[1]];
          rightCellBounds = [cellBounds[0], medianValue, cellBounds[2], cellBounds[3] - leftCellBounds[3]];
        }
        animation.showCellPoints(pointIndices, false);
        animation.createSplittingEdge(cellBounds, medianValue, axisIndex);
        animation.showPointIndices(pointIndices, false);
        animation.showComment('Split the cell into "left" and "right" subcells at the median point.', 5.0);
        animation.hideComment(5.0);

        // Call doBuildStep recursively for the "left" and "right" subsets.
        // Resulting cells will be kept to the left and right branches of the current cell.
        cell.set('axisIndex', axisIndex);
        cell.set('medianValue', medianValue);

        animation.showComment('Go to the "left" subcell.', 5.0);
        animation.hideComment(5.0);

        cell.set('leftBranch', doBuildStep(leftPointsSubset, leftCellBounds as Box, levelIndex + 1));
        animation.setCell(cellBounds, cell.get('cellIndex'), 3.0, 1.0);

        animation.showComment('Go to the "right" subcell.', 5.0);
        animation.hideComment(5.0);

        cell.set('rightBranch', doBuildStep(rightPointsSubset, rightCellBounds as Box, levelIndex + 1));
        animation.setCell(cellBounds, cell.get('cellIndex'), 3.0, 1.0);
      } else {
        const leafPoints = pointIndices.map((v) => points[v]);
        cell.set('leafPoints', leafPoints); // TODO: Remove. We don't need it.
        cell.set('leafPointIndices', pointIndices);

        animation.showComment(
          `Current cell contains ${pointIndices.length} ${pointIndices.length > 1 ? 'points' : 'point'}, ` +
            `which is no more than ${maxPointsNumberPerLeafCell}. This is a leaf cell.`,
          5.0
        );
        animation.hideComment(5.0);
        animation.showLeafPoints(pointIndices, true);
      }

      return cell;
    };

    // Show an initial set of points.
    animation.showComment(`This is an initial set of ${points.length} points.`, 5.0);
    animation.showPoints(5.0, 5.0);
    animation.showComment('These points are placed in the most top level cell of the k-d tree called root cell.', 5.0);
    animation.hideComment(5.0);

    // Initialize required variables and call the `doBuildStep` method to recursively build the k-d tree.
    animation.showCell(true);
    const pointIndices = Array.from(Array(points.length).keys());
    const bounds = KDTree.getPointsBoundingBox(points);
    const kdTree = doBuildStep(pointIndices, bounds, 0);
    animation.showCell(false);

    animation.showComment('K-D tree complete.', 0.0);
    animation.hideComment(5.0);

    return kdTree;
  }

  static search(kdTree: Map<string, any>, points: Array<Point>, queryPoint: Point, animation: KDTreeAnimation): number {
    const getDistance = (p1: Point, p2: Point) => Math.sqrt((p1[0] - p2[0]) ** 2.0 + (p1[1] - p2[1]) ** 2.0);
    let closestPointIndex: number = -1;
    let closestPointDistance: number = Number.POSITIVE_INFINITY;
    let visitedCellIndices: Array<number> = [];

    const doSearchStep = (cell: Map<string, any>) => {
      // If the current cell is the leaf cell then search it for the new closest point.
      if (cell.has('leafPointIndices')) {
        const closestCandidateIndices = cell.get('leafPointIndices');
        const distances = closestCandidateIndices.map((v: number) => getDistance(points[v], queryPoint));
        const closestCandidateIndex = distances.indexOf(Math.min(...distances));
        const closestCandidateDistance = distances[closestCandidateIndex];
        if (closestCandidateDistance < closestPointDistance) {
          closestPointIndex = closestCandidateIndices[closestCandidateIndex];
          closestPointDistance = closestCandidateDistance;
        }

        animation.setCell(cell.get('cellBounds'), cell.get('cellIndex'));
        animation.showCell(true);
        animation.setSearchCircle(queryPoint, points[closestPointIndex]);
        animation.showSearchCircle(true);
        animation.showComment('Current cell is a leaf cell. Loop through its points and find the closest one.', 5.0);
        animation.hideComment(5.0);
      }

      // If closest point still not defined, i.e. no one leaf cells has not been visited yet, then just keep going down
      // the tree choosing left or right branches in accordance to the median values.
      if (closestPointIndex < 0) {
        const axisIndex = cell.get('axisIndex');
        const medianValue = cell.get('medianValue');
        const isLower = queryPoint[axisIndex] < medianValue;

        animation.setCell(cell.get('cellBounds'), cell.get('cellIndex'));
        animation.showCell(true);
        animation.showComment(
          'Current cell has no points. ' +
            `It was splitted by the ${axisIndex ? 'Y' : 'X'} axis at the value ${Math.round(medianValue)}. ` +
            `Corresponding coordinate of the search point is ${Math.round(queryPoint[axisIndex])}, ` +
            `which is ${isLower ? 'lower' : 'greater'} than median value. ` +
            `Go to the ${isLower ? 'left' : 'right'} cell.`,
          5.0
        );
        animation.hideComment(5.0);

        const branch = isLower ? 'leftBranch' : 'rightBranch';
        doSearchStep(cell.get(branch));
      }

      animation.showCell(false, 3.0);
      animation.setSearchCircle(queryPoint, points[closestPointIndex]);

      // If closest point was defined then explore the neighboring leaf cells since they can have closer points.
      for (const branch of ['leftBranch', 'rightBranch']) {
        if (!cell.has(branch)) {
          continue;
        }

        const childCell = cell.get(branch);
        if (visitedCellIndices.includes(childCell.get('cellIndex'))) {
          continue;
        }

        // If bounding rectangle of the childCell is intersecting the circle with center at the queryPoint and
        // radius equal to closestPointDistance than this childCell should be visited too.
        const closestBoundingRectPoint = this.getClosestBoxPoint(childCell.get('cellBounds') as Box, queryPoint);
        const closestBoundingRectPointDistance = getDistance(closestBoundingRectPoint, queryPoint);
        if (closestBoundingRectPointDistance < closestPointDistance) {
          doSearchStep(childCell);
        }
      }

      // Include index of the current cell to visitedCellIndices to skip this cell in the future.
      visitedCellIndices.push(cell.get('cellIndex'));
    };

    // Show the search point.
    animation.showQueryPoint(true);
    animation.showComment("This is a search point. Let's find the nearest neighbor for it.", 5.0);
    animation.hideComment(5.0);

    // Initialize required variables and call do_search_step method to find point closest to the queryPoint.
    doSearchStep(kdTree);

    animation.showSearchCircle(false);
    animation.showClosestPoint(closestPointIndex, true);
    animation.showComment('Closest point found', 5.0);
    animation.hideComment(5.0);

    return closestPointIndex;
  }

  private static getClosestBoxPoint(b: Box, p: Point): Point {
    return [0, 1].map((i) => (p[i] <= b[i] ? b[i] : p[i] >= b[i] + b[i + 2] ? b[i] + b[i + 2] : p[i])) as Point;
  }

  private static getPointsBoundingBox(points: Array<Point>, marginPercent: number = 20.0): Box {
    const [x, y] = [points.map((v) => v[0]), points.map((v) => v[1])];
    const [right, bottom] = [Math.max(...x), Math.max(...y)];
    let [left, top] = [Math.min(...x), Math.min(...y)];
    let [width, height] = [right - left, bottom - top];

    const [widthMargin, heightMargin] = [(width * marginPercent) / 100.0, (height * marginPercent) / 100.0];
    left -= 0.5 * widthMargin;
    top -= 0.5 * heightMargin;
    width += widthMargin;
    height += heightMargin;

    const bounds: Box = [left, top, width, height];

    return bounds;
  }
}
