import { Animation } from '../animation';
import { Colors } from '../colors';
import { GroupActor } from '../indyplot/actors/group_actor';
import { LineActor } from '../indyplot/actors/line_actor';
import { PathActor } from '../indyplot/actors/path_actor';
import { TableActor } from '../indyplot/actors/table_actor';
import { TextActor } from '../indyplot/actors/text_actor';
import { VectorOrArrayOrNumber } from '../indyplot/core/types';
import { Vector } from '../indyplot/core/vector';
import { Domain } from '../indyplot/scene';

const OFFSET: number = 1.0; // Default time offset in seconds.
const DURATION: number = 1.0; // Default transition duration in seconds.

export class GrahamScanAnimation extends Animation {
  private points: Array<Vector> = [];

  constructor() {
    const domain = new Domain(0.0, 0.0, 100.0, 0.0, 'height');
    super(domain);

    const scene = this.plot.getScene('main');
    scene.setIsYFlipped(true);
    scene.addLayer(); // Points.
    scene.addLayer({ isScreen: true }); // Turn text.
    scene.addLayer({ isRelative: true, fitSide: 'min', alignment: 'end' }); // Table.
    scene.addLayer({ isScalable: false }); // Coordinate axes.
  }

  initialize() {
    this.plot.addLine(0.0, 0.0, { ak: 'axis-vector', c: Colors.RED, o: 0.0, sw: 0.25, isArrowCap: true });
    this.plot.addLine(0.0, 0.0, { ak: 'radius-vector', c: Colors.YELLOW, o: 0.0, sw: 0.25, isArrowCap: true });
    this.plot.addText('', 0.0, { ak: 'turn-text', li: 2, size: 36.0, alignment: 'ct', isRelativePosition: true });
    this.plot.addPath([], { ak: 'turn-lines', sc: Colors.YELLOW, sw: 0.5 }).setOpacity(0.0, 0.0);
    this.plot.addPath([], { ak: 'hull', sc: Colors.WHITE, sw: 0.25 }).setVertices([], 0.0);
    this.plot.addTable(['hull', 'inner', 'undefined'], [], [0.75, 0.98], { ak: 'table', li: 3, o: 0.0 });

    this.plot.addLine(0.0, [5.0, 0.0], { li: 4, c: Colors.RED, sw: 0.25, isArrowCap: true });
    this.plot.addLine(0.0, [0.0, 5.0], { li: 4, c: Colors.GREEN, sw: 0.25, isArrowCap: true });
    this.plot.addCircle(0.0, 0.25, { li: 4, fc: Colors.WHITE, sc: 'none' });

    this.plot.getActor('axis-vector').setOpacity(0.0, 0.0);
    this.plot.getActor('radius-vector').setOpacity(0.0, 0.0);
    this.plot.getActor('table').setOpacity(0.0, 0.0);
    this.plot.getActor<TableActor>('table').setFontSize(0.025).setGridStrokeWidth(0.0025).setIsYFlipped(true);
    this.plot.getActor<TextActor>('turn-text').setIsYFlipped(true).setPosition([50.0, 90.0]).setOpacity(0.0, 0.0);

    this.plot.loadSound(require('./assets/sounds/accept.mp3'), 'accept');
    this.plot.loadSound(require('./assets/sounds/check.mp3'), 'check');
    this.plot.loadSound(require('./assets/sounds/reject.mp3'), 'reject');
    this.plot.loadSound(require('./assets/sounds/select.mp3'), 'select');
  }

  // This method resets the state to those one before the last animation running.
  reset() {
    const points = this.points.map((v) => v.copy());
    this.clear();
    points.forEach((v) => this.addPoint(v));
  }

  // This method resets the state to the moment when the animation just was created.
  // It must call the base `clear` method.
  clear() {
    this.points = [];
    super.clear();
  }

  // This method removes the `Plot` and must be called just before the unmounting.
  // It must call the base `remove` method.
  remove(): void {
    this.points = [];
    super.remove();
  }

  addPoint(point: VectorOrArrayOrNumber) {
    point = Vector.from(point);
    this.points.push(point);

    const pointIndex = this.points.length - 1;
    this.plot.addCircle(point, 1.5, { ak: 'circle', sc: 'none' });
    this.plot.addText(pointIndex.toString(), point, { ak: 'index', fc: Colors.BLACK, size: 1.5, alignment: 'cc' });
    this.plot.addText('', [point.x + 1.5, point.y + 1.5], { ak: 'label', fc: Colors.WHITE, size: 1.5 });
    this.plot.getActor('circle').setFillColor(Colors.WHITE, 0.0);
    this.plot.getActor<TextActor>('index').setIsYFlipped(true);
    this.plot.getActor<TextActor>('label').setIsYFlipped(true).setText('', 0.0);
    this.plot.group(['circle', 'index', 'label'], { li: 1, ak: `point${pointIndex}` }).setOpacity(0.0, 0.0);
  }

  showPoints(offset: number = OFFSET, duration: number = DURATION) {
    const showPointDuration = duration / this.points.length;
    let time = this.plot.getPlaybackDuration() + offset - duration + showPointDuration;
    for (const pointIndex of this.points.keys()) {
      this.plot.getActor(`point${pointIndex}`).setOpacity(1.0, time, showPointDuration);
      time += showPointDuration;
    }
  }

  showOriginPoint(pointIndex: number, offset: number = OFFSET, duration: number = DURATION) {
    const time = this.plot.getPlaybackDuration() + offset;
    this.plot.getActor<GroupActor>(`point${pointIndex}`).get('circle').setFillColor(Colors.YELLOW, time, duration);

    const origin = this.points[pointIndex];
    const axisVectorActor = this.plot.getActor<LineActor>('axis-vector');
    axisVectorActor.setStartPosition(origin).setEndPosition(Vector.add(origin, [15.0, 0.0]));
    axisVectorActor.setOpacity(1.0, time, duration);

    this.plot.getActor<LineActor>('radius-vector').setStartPosition(origin).setEndPosition(origin, time);
  }

  hideOriginPoint(pointIndex: number, offset: number = OFFSET, duration: number = DURATION) {
    const time = this.plot.getPlaybackDuration() + offset;
    this.plot.getActor<GroupActor>(`point${pointIndex}`).get('circle').setFillColor(Colors.WHITE, time, duration);
    this.plot.getActor<LineActor>('axis-vector').setOpacity(0.0, time, duration);
  }

  showAngles(angles: Array<number>, offset: number = OFFSET, duration: number = DURATION) {
    const showAngleDuration = duration / this.points.length;
    let time = this.plot.getPlaybackDuration() + offset - duration + showAngleDuration;

    const radiusVectorActor = this.plot.getActor<LineActor>('radius-vector');
    radiusVectorActor.setOpacity(1.0, time, showAngleDuration);
    for (const pointIndex of this.points.keys()) {
      const label = `${Math.round(angles[pointIndex])}`;
      this.plot.getActor<GroupActor>(`point${pointIndex}`).get<TextActor>('label').setText(label, time);
      radiusVectorActor.setEndPosition(this.points[pointIndex], time, showAngleDuration);
      this.plot.addSound('select', time);
      time += showAngleDuration;
    }

    radiusVectorActor.setOpacity(0.0, time, showAngleDuration);
  }

  showTurn(offset: number = OFFSET, duration: number = DURATION) {
    const time = this.plot.getPlaybackDuration() + offset;
    this.plot.getActor('turn-text').setOpacity(1.0, time, duration);
    this.plot.getActor('turn-lines').setOpacity(1.0, time, duration);
    for (const pointIndex of this.points.keys()) {
      this.plot.getActor<GroupActor>(`point${pointIndex}`).get<TextActor>('label').setText('', time);
    }
  }

  checkTurn(indices: Array<number>, offset: number = OFFSET, duration: number = DURATION) {
    const time = this.plot.getPlaybackDuration() + offset;
    const turnText = `CHECK TURN FOR POINT ${indices[1]}`;
    const vertices = indices.map((v) => this.points[v]);
    this.plot.getActor<TextActor>('turn-text').setText(turnText, time).setFillColor(Colors.YELLOW, time, duration);
    this.plot.getActor<PathActor>('turn-lines').setVertices(vertices, time);
    this.plot.getActor<GroupActor>(`point${indices[1]}`).get('circle').setFillColor(Colors.YELLOW, time, duration);
    this.plot.addSound('check', time);
  }

  setTurn(isLeftTurn: boolean, offset: number = OFFSET, duration: number = DURATION) {
    const time = this.plot.getPlaybackDuration() + offset;
    const turnText = isLeftTurn ? 'LEFT - ACCEPT' : `RIGHT - REJECT`;
    const turnColor = isLeftTurn ? Colors.GREEN : Colors.RED;
    const soundKey = isLeftTurn ? `accept` : 'reject';
    this.plot.getActor<TextActor>('turn-text').setText(turnText, time).setFillColor(turnColor, time, duration);
    this.plot.addSound(soundKey, time);
  }

  hideTurn(offset: number = OFFSET, duration: number = DURATION) {
    const time = this.plot.getPlaybackDuration() + offset;
    this.plot.getActor('turn-text').setOpacity(0.0, time, duration);
    this.plot.getActor('turn-lines').setOpacity(0.0, time, duration);
  }

  showTable(offset: number = OFFSET, duration: number = DURATION) {
    const time = this.plot.getPlaybackDuration() + offset;
    this.plot.getActor('table').setOpacity(1.0, time, duration);
  }

  updateHull(hull: number[], inner: number[], undef: number[], closed: boolean, offset?: number, duration?: number) {
    const time = this.plot.getPlaybackDuration() + (offset ?? OFFSET);
    duration = duration ?? DURATION;
    for (const pointIndex of this.points.keys()) {
      const color = hull.includes(pointIndex) ? Colors.GREEN : inner.includes(pointIndex) ? Colors.RED : Colors.WHITE;
      this.plot.getActor<GroupActor>(`point${pointIndex}`).get('circle').setFillColor(color, time, duration);
    }

    const hullPoints = hull.map((v) => this.points[v]);
    closed && hullPoints.length && hullPoints.push(hullPoints[0]);
    this.plot.getActor<PathActor>('hull').setVertices(hullPoints, time);

    const data: Array<Array<string>> = [];
    const rowsNumber = Math.max(hull.length, inner.length, undef.length);
    for (let rowIndex = 0; rowIndex < rowsNumber; ++rowIndex) {
      data[rowIndex] = [String(hull[rowIndex] ?? ''), String(inner[rowIndex] ?? ''), String(undef[rowIndex] ?? '')];
    }

    this.plot.getActor<TableActor>('table').setData(data, time);
  }
}
