import React from 'react';
import PERSONAL_PROJECTS_DATA from '../assets/pages/personal_projects/personal_projects.json';
import { Page } from '../components/page/page';
import { ProjectsList } from '../components/projects_list/projects_list';

export const PersonalProjects: React.FC = () => {
  return (
    <Page>
      <ProjectsList projectsData={PERSONAL_PROJECTS_DATA} projectsPerPage={5} useReverseProjectIds={true} />
    </Page>
  );
};
