import { getInterpolationParameter } from '../utils';
import { OnChangeHandler, Track } from './track';

export class NumberTrack extends Track<number> {
  constructor(onChange: OnChangeHandler<number>, useLinearInterpolation: boolean = true) {
    const compare = (value0: number, value1: number) => Math.abs(value1 - value0) <= Number.EPSILON;

    let interpolate;
    if (useLinearInterpolation) {
      interpolate = (startValue: number, startTime: number, endValue: number, endTime: number, time: number) => {
        const s = getInterpolationParameter(startTime, endTime, time);
        const value = startValue + s * (endValue - startValue);
        return value;
      };
    }

    super(onChange, compare, interpolate);
  }
}
