import { Box } from '@mui/material';
import React from 'react';
import PAGE_DATA from '../../assets/components/page/page.json';
import { Image } from '../image';
import { Footer } from './footer';
import { Menu, menuHeight } from './menu';

// TODO4: Probably need to add `ScrollToTop` button here.
//        It must have a fixed positioning at the bottom right corner.

// TODO5: It's possible to prepare a number of different background images and load them randomly.

// Represents data storing in the `page.json` file.
type PageData = {
  backgroundImageFilePath: string;
};

type PageProps = {
  children: React.ReactNode;
};

export const pageTopMargin: number = menuHeight + 10;

export const Page: React.FC<PageProps> = ({ children }) => {
  const pageData = React.useMemo((): PageData => PAGE_DATA, []);

  return (
    <Image
      imageLink={pageData.backgroundImageFilePath}
      style={{ backgroundSize: '100% auto', backgroundAttachment: 'fixed' }}
    >
      <Box
        width='100vw'
        height='100vh'
        sx={{ position: 'fixed', backgroundColor: 'background.default', opacity: '0.9' }}
      />
      <Box display='flex' flexDirection='column' alignItems='center'>
        <Menu />
        <Box
          width='96%'
          maxWidth='1280px'
          display='flex'
          flexDirection='column'
          rowGap={5}
          zIndex='1'
          marginTop={`${pageTopMargin}px`} // Move the container down by height of the menu to prevent overlapping.
        >
          {children}
        </Box>
        <Footer />
      </Box>
    </Image>
  );
};
