export function generateRandomId(length: number = 8): string {
  if (!Number.isInteger(length) || length < 1 || length > 16) {
    throw Error('`length` must be an integer number from range [1, 16]');
  }

  const radix = 16;
  const limitValue = radix ** length;
  const randomValue = Math.floor(Math.random() * limitValue);
  const randomId = randomValue.toString(radix).padStart(length, '0');
  
  return randomId;
}

export function setElementAttributes(element: Element, keyValuePairs: { [key: string]: string | number }) {
  for (const [key, value] of Object.entries(keyValuePairs)) {
    element.setAttribute(key, String(value));
  }
}

export function getInterpolationParameter(startTime: number, endTime: number, time: number): number {
  if (!Number.isFinite(startTime) || startTime < 0.0) {
    throw Error('`startTime` must be a non-negative real number');
  }

  if (!Number.isFinite(endTime) || endTime < 0.0) {
    throw Error('`endTime` must be a non-negative real number');
  }

  if (!Number.isFinite(time) || time < 0.0) {
    throw Error('`time` must be a non-negative real number');
  }

  const timeDelta = endTime - startTime;
  if (timeDelta <= Number.EPSILON) {
    throw Error('`endTime` must be greater than `startTime`');
  }

  if (time < startTime || time > endTime) {
    throw Error('`time` must be between the `startTime` and `endTime`');
  }

  const s = (time - startTime) / timeDelta;

  return s;
}
