import { BubbleSortAnimation } from './bubble_sort_animation';

export class BubbleSort {
  static sort(values: Array<number>, animation: BubbleSortAnimation): Array<number> {
    for (let i = 0; i < values.length - 1; ++i) {
      let isArraySorted = true;
      for (let j = 1; j < values.length - i; ++j) {
        const areValuesArranged = values[j - 1] <= values[j];
        animation.showComment(`Compare values at positions ${j - 1} and ${j}.`);
        animation.selectValue(j - 1, true);
        animation.selectValue(j, true, 0.0);
        animation.compareValues(j - 1, j, areValuesArranged, 2.0);
        animation.selectValue(j - 1, false);
        animation.selectValue(j, false, 0.0);
        animation.hideComment(0.0);

        if (!areValuesArranged) {
          const value = values[j - 1];
          values[j - 1] = values[j];
          values[j] = value;
          isArraySorted = false;
          animation.swapValues(j - 1, j);
          animation.showComment(`Left value is greater than right value - swap them.`, 0.0);
          animation.hideComment(5.0);
        } else {
          animation.showComment(`They are already arranged - leave them.`);
          animation.hideComment(5.0);
        }
      }

      if (isArraySorted && i < values.length - 1) {
        animation.showComment('No swaps during the last pass. Stop execution.');
        animation.hideComment(5.0);
        break;
      }
    }

    animation.showComment('Sorting complete.');
    animation.hideComment(5.0);

    return values;
  }
}
