import { Vector } from './vector';
import { SizeOrVectorOrArray } from './types';

export class Size {
  private _width: number = 0.0;
  private _height: number = 0.0;

  constructor(width: number = 0.0, height: number = 0.0) {
    this.width = width;
    this.height = height;
  }

  set width(width: number) {
    if (!Number.isFinite(width) || width < 0.0) {
      throw Error('`width` must be a non-negative real number');
    }

    this._width = width;
  }

  get width(): number {
    return this._width;
  }

  set height(height: number) {
    if (!Number.isFinite(height) || height < 0.0) {
      throw Error('`height` must be a non-negative real number');
    }

    this._height = height;
  }

  get height(): number {
    return this._height;
  }

  from(value: SizeOrVectorOrArray) {
    if (Vector.isVector(value)) {
      this.fromVector(value);
    } else if (Array.isArray(value)) {
      this.fromArray(value);
    } else {
      this.width = value.width;
      this.height = value.height;
    }
  }

  copy(): Size {
    return new Size(this.width, this.height);
  }

  toVector(): Vector {
    return new Vector(this.width, this.height);
  }

  toArray(): Array<number> {
    return [this.width, this.height];
  }

  isEqual(size: SizeOrVectorOrArray, tolerance: number = Number.EPSILON): boolean {
    size = Size.from(size);
    const isEqual = this.toVector().isEqual(size.toVector(), tolerance);
    return isEqual;
  }

  getArea(): number {
    return this.width * this.height;
  }

  static isSize(value: unknown): value is Size {
    return value instanceof Size;
  }

  static from(value: SizeOrVectorOrArray) {
    const size = new Size();
    size.from(value);
    return size;
  }

  private fromVector(vector: Vector) {
    this.width = vector.x;
    this.height = vector.y;
  }

  private fromArray(array: Array<number>) {
    if (array.length !== 2) {
      throw Error('`array` must be an array of length 2');
    }

    [this.width, this.height] = array;
  }
}
