import { Size } from './size';
import { Transform } from './transform';
import { Alignment, BoxOrArray, FitSide, SizeOrVectorOrArray, VectorOrArrayOrNumber } from './types';
import { Vector } from './vector';

export class Box {
  private _x: number = 0.0;
  private _y: number = 0.0;
  private _width: number = 0.0;
  private _height: number = 0.0;

  constructor(x: number = 0.0, y: number = 0.0, width: number = 0.0, height: number = 0.0) {
    this.x = x;
    this.y = y;
    this.width = width;
    this.height = height;
  }

  set x(x: number) {
    if (!Number.isFinite(x)) {
      throw Error('`x` must be a real number');
    }

    this._x = x;
  }

  get x(): number {
    return this._x;
  }

  set y(y: number) {
    if (!Number.isFinite(y)) {
      throw Error('`y` must be a real number');
    }

    this._y = y;
  }

  get y(): number {
    return this._y;
  }

  set width(width: number) {
    if (!Number.isFinite(width) || width < 0.0) {
      throw Error('`width` must be a non-negative real number');
    }

    this._width = width;
  }

  get width(): number {
    return this._width;
  }

  set height(height: number) {
    if (!Number.isFinite(height) || height < 0.0) {
      throw Error('`height` must be a non-negative real number');
    }

    this._height = height;
  }

  get height(): number {
    return this._height;
  }

  set size(size: SizeOrVectorOrArray) {
    size = Size.from(size);
    this.width = size.width;
    this.height = size.height;
  }

  get size(): Size {
    return new Size(this.width, this.height);
  }

  get left(): number {
    return this.x;
  }

  get right(): number {
    return this.x + this.width;
  }

  get top(): number {
    return this.y;
  }

  get bottom(): number {
    return this.y + this.height;
  }

  get leftTop(): Vector {
    return new Vector(this.left, this.top);
  }

  get rightTop(): Vector {
    return new Vector(this.right, this.top);
  }

  get leftBottom(): Vector {
    return new Vector(this.left, this.bottom);
  }

  get rightBottom(): Vector {
    return new Vector(this.right, this.bottom);
  }

  set center(center: VectorOrArrayOrNumber) {
    const delta = Vector.from(center).sub(this.center);
    this.x += delta.x;
    this.y += delta.y;
  }

  get center(): Vector {
    return new Vector(this.x + 0.5 * this.width, this.y + 0.5 * this.height);
  }

  from(value: BoxOrArray) {
    if (Array.isArray(value)) {
      this.fromArray(value);
    } else {
      this.x = value.x;
      this.y = value.y;
      this.width = value.width;
      this.height = value.height;
    }
  }

  copy(): Box {
    return new Box(this.x, this.y, this.width, this.height);
  }

  toArray(): Array<number> {
    return [this.x, this.y, this.width, this.height];
  }

  toTuple(): [number, number, number, number] {
    return [this.x, this.y, this.width, this.height];
  }

  isEqual(box: BoxOrArray, tolerance: number = Number.EPSILON) {
    if (!Number.isFinite(tolerance) || tolerance < 0.0) {
      throw Error('`tolerance` must be a non-negative real number');
    }

    box = Box.from(box);
    const isEqualLeftTop = this.leftTop.isEqual(box.leftTop, tolerance);
    const isEqualSize = this.size.isEqual(box.size, tolerance);
    const isEqual = isEqualLeftTop && isEqualSize;

    return isEqual;
  }

  getArea(): number {
    return this.width * this.height;
  }

  getPerimeter(): number {
    return 2.0 * (this.width + this.height);
  }

  getDiagonal(): number {
    return Math.sqrt(this.width ** 2.0 + this.height ** 2.0);
  }

  // This method returns coordinates of a point of the box, which is closest to the query `point`.
  // Resulting point will be either inside the box or will be lying on its border.
  getClosestPoint(point: VectorOrArrayOrNumber): Vector {
    point = Vector.from(point);
    const x = point.x <= this.left ? this.left : point.x >= this.right ? this.right : point.x;
    const y = point.y <= this.top ? this.top : point.y >= this.bottom ? this.bottom : point.y;
    const closestPoint = new Vector(x, y);
    return closestPoint;
  }

  // This method returns a transformation matrix aligning this box (source) with the specified `target` box in according
  // with the `fitSide` and `alignment` flags.
  // Meanings of the `fitSide` flag:
  //  'min' - match dimensions with a smaller target-to-source ratio. This will keep aspect ratio of the box.
  //  'max' - match dimensions with a larger target-to-source ratio. This will keep aspect ratio of the box.
  //  'all' - match both sides. Preserving of the aspect ratio is not guaranteed.
  // Meanings of the `alignment` flag:
  //  'start' - match left sides for horizontal aligning and top sides for vertical aligning.
  //  'end' - match right sides for horizontal aligning and bottom sides for vertical aligning.
  //  'center' - match centers.
  getMappingTransform(target: BoxOrArray, fitSide: FitSide = 'all', alignment: Alignment = 'start'): Transform {
    target = Box.from(target);

    const scale = Vector.div(target.size.toVector(), this.size.toVector());
    const factor = fitSide === 'min' ? scale.min() : fitSide === 'max' ? scale.max() : scale;
    const gaps = Vector.sub(target.size.toVector(), Vector.mul(this.size.toVector(), factor));
    const offset = alignment === 'center' ? gaps.div(2.0) : alignment === 'end' ? gaps : 0.0;
    const transform = Transform.fromTSRT(this.leftTop.negative(), factor, 0.0, Vector.add(target.leftTop, offset));

    return transform;
  }

  static isBox(value: unknown): value is Box {
    return value instanceof Box;
  }

  static from(value: BoxOrArray): Box {
    const box = new Box();
    box.from(value);
    return box;
  }

  private fromArray(array: Array<number>) {
    if (array.length !== 4) {
      throw Error('`array` must be an array of length 4');
    }

    [this.x, this.y, this.width, this.height] = array;
  }
}
