import React from 'react';
import PROFESSIONAL_PROJECTS_DATA from '../assets/pages/professional_projects/professional_projects.json';
import { Page } from '../components/page/page';
import { ProjectsList } from '../components/projects_list/projects_list';

export const ProfessionalProjects: React.FC = () => {
  return (
    <Page>
      <ProjectsList projectsData={PROFESSIONAL_PROJECTS_DATA} projectsPerPage={5} useReverseProjectIds={true} />
    </Page>
  );
};
