import { Box, Chip } from '@mui/material';
import React from 'react';

type TagCloudProps = {
  tags: Array<string>;
  view?: 'size' | 'count';
  sort?: {
    key: 'tag' | 'count';
    order: 'ascending' | 'descending';
  };
  sizeRange?: [number, number]; // Font size range in `em`. Using only if `view === 'size'`.
};

export const TagCloud: React.FC<TagCloudProps> = ({ tags, view, sort, sizeRange = [1.0, 2.0] }) => {
  const tagCounter = React.useMemo((): Map<string, number> => {
    const tagCounter = new Map<string, number>();
    tags.forEach((v) => {
      const currentValue = tagCounter.get(v) ?? 0;
      tagCounter.set(v, currentValue + 1);
    });

    return tagCounter;
  }, [tags]);

  const tagNodes = React.useMemo((): Array<React.ReactNode> => {
    const tagCountPairs = Array.from(tagCounter.entries());

    if (sort) {
      type StringNumberPair = [string, number];
      const compareStrings = (a: StringNumberPair, b: StringNumberPair) => (a[0] > b[0] ? 1 : a[0] < b[0] ? -1 : 0);
      const compareNumbers = (a: StringNumberPair, b: StringNumberPair) => a[1] - b[1];
      const compare = sort.key === 'tag' ? compareStrings : compareNumbers;
      if (sort.order === 'ascending') {
        tagCountPairs.sort((v0, v1) => compare(v0, v1));
      } else if (sort.order === 'descending') {
        tagCountPairs.sort((v0, v1) => compare(v1, v0));
      }
    }

    let minCount: number, maxCount: number;
    if (view === 'size') {
      const counterValues = tagCountPairs.map((v) => v[1]);
      [minCount, maxCount] = [Math.min(...counterValues), Math.max(...counterValues)];
    }

    const tagNodes = tagCountPairs.map(([tag, count]) => {
      let sx = {};
      let label = tag;
      if (view === 'count') {
        label += count > 1 ? `:${count}` : '';
      } else if (view === 'size') {
        const s = (count - minCount) / (maxCount - minCount);
        const size = s * (sizeRange[1] - sizeRange[0]) + sizeRange[0];
        sx = { fontSize: `${size}em` };
      }

      const tagNode = <Chip key={tag} label={label} sx={sx} />;
      return tagNode;
    });

    return tagNodes;
  }, [tagCounter, view, sort, sizeRange]);

  return (
    <Box width='100%' display='flex' flexWrap='wrap' gap={1}>
      {tagNodes}
    </Box>
  );
};
