import { Grid } from '@mui/material';
import React from 'react';
import ABOUT_ME_DATA from '../../assets/pages/home/about_me/about_me.json';
import { Image } from '../../components/image';
import { Text } from '../../components/text';
import { CONTEXT } from '../../context';
import { TranslatableString } from '../../types';

// Represents data storing in the `about_me.json` file.
type AboutMeData = {
  photoFilePath: string;
  title: TranslatableString;
  descriptionFilePath: TranslatableString;
};

export const AboutMe: React.FC = () => {
  const { language } = React.useContext(CONTEXT);

  const aboutMeData = React.useMemo((): AboutMeData => ABOUT_ME_DATA, []);

  return (
    <Grid container width='100%' height='100%'>
      <Grid item xs={12} md={5} lg={4} xl={3} height={{ xs: '30%', md: '100%' }} padding='10px'>
        <Image imageLink={aboutMeData.photoFilePath} width='100%' height='100%' />
      </Grid>
      <Grid
        item
        xs={12}
        md={7}
        lg={8}
        xl={9}
        height={{ xs: '70%', md: '100%' }}
        sx={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: { xs: 'center', md: 'start' },
          justifyContent: { xs: 'start', md: 'center' }
        }}
      >
        <Text variant='h1'>{aboutMeData.title[language]}</Text>
        <Text textFilePath={aboutMeData.descriptionFilePath[language]} useMarkdown={true} />
      </Grid>
    </Grid>
  );
};
