import { DarkMode as DarkThemeIcon, Language as LanguageIcon, LightMode as LightThemeIcon } from '@mui/icons-material';
import { Menu as MenuIcon } from '@mui/icons-material';
import { AppBar, Box, Button, Drawer, List, ListItem, ListItemButton, Theme, useMediaQuery } from '@mui/material';
import React from 'react';
import MENU_DATA from '../../assets/components/page/menu/menu.json';
import { CONTEXT } from '../../context';
import { TranslatableString } from '../../types';
import { Image } from '../image';
import { Link } from '../link';
import { Text } from '../text';

type MenuItem = {
  name: TranslatableString;
  iconFilePath: string;
  link: string;
};

// Represents data storing in the `menu.json` file.
type MenuData = Array<MenuItem>;

export const menuHeight: number = 50; // Height of the menu bar in pixels.

export const Menu: React.FC = () => {
  const [isDrawerOpen, setIsDrawerOpen] = React.useState<boolean>(false);

  const { theme, setTheme } = React.useContext(CONTEXT);
  const { language, setLanguage } = React.useContext(CONTEXT);

  const isWiderThanMD = useMediaQuery((theme: Theme) => theme.breakpoints.up('md'));

  const menuData = React.useMemo((): MenuData => MENU_DATA, []);

  const barMenuNodes = React.useMemo((): Array<React.ReactNode> => {
    return menuData.map((v, i) => (
      <Link key={i} to={v.link} style={{ textDecoration: 'none' }}>
        <Button sx={{ height: '100%', borderRadius: 0, fontWeight: 'bold' }}>{v.name[language]}</Button>
      </Link>
    ));
  }, [menuData, language]);

  const drawerMenuNodes = React.useMemo((): Array<React.ReactNode> => {
    return menuData.map((v, i) => (
      <ListItem key={i} disablePadding={true}>
        <ListItemButton>
          <Link to={v.link} style={{ textDecoration: 'none' }}>
            <Box display='flex'>
              <Image imageLink={v.iconFilePath} width='32px' height='32px' marginRight='10px' />
              <Text sx={{ marginY: 'auto', fontWeight: 'bold' }}>{v.name[language].toUpperCase()}</Text>
            </Box>
          </Link>
        </ListItemButton>
      </ListItem>
    ));
  }, [menuData, language]);

  const handleSwitchThemeButtonClick = React.useCallback(() => {
    setTheme(theme === 'light' ? 'dark' : 'light');
  }, [theme, setTheme]);

  const handleSwitchLanguageButtonClick = React.useCallback(() => {
    setLanguage(language === 'en' ? 'ru' : 'en');
  }, [language, setLanguage]);

  return (
    <>
      <AppBar sx={{ height: `${menuHeight}px`, position: 'fixed', backgroundColor: 'background.default' }}>
        <Box height='100%' display='flex' justifyContent='space-between'>
          <Box height='100%' display={isWiderThanMD ? 'flex' : 'none'}>
            {barMenuNodes}
          </Box>

          <Box height='100%' display={isWiderThanMD ? 'none' : 'flex'}>
            <Button onClick={() => setIsDrawerOpen(true)}>{<MenuIcon />}</Button>
          </Box>

          <Box height='100%' display='flex'>
            <Button onClick={handleSwitchThemeButtonClick}>
              {theme === 'light' ? <LightThemeIcon /> :  <DarkThemeIcon />}
            </Button>
            <Button onClick={handleSwitchLanguageButtonClick} startIcon={<LanguageIcon />} sx={{ fontWeight: 'bold' }}>
              {language.toUpperCase()}
            </Button>
          </Box>
        </Box>
      </AppBar>

      <Drawer anchor='right' open={isDrawerOpen} onClose={() => setIsDrawerOpen(false)}>
        <List>{drawerMenuNodes}</List>
      </Drawer>
    </>
  );
};
