import { Actor, IActor } from './actors/actor';
import { BoxActor, IBoxActor } from './actors/box_actor';
import { CircleActor, ICircleActor } from './actors/circle_actor';
import { GridActor } from './actors/grid_actor';
import { GroupActor } from './actors/group_actor';
import { ILineActor, LineActor } from './actors/line_actor';
import { IPathActor, PathActor } from './actors/path_actor';
import { ITableActor, TableActor } from './actors/table_actor';
import { ITextActor, TextActor } from './actors/text_actor';
import { Size } from './core/size';
import { ColorOrStringOrArray, NullOr, SizeOrVectorOrArray, VectorOrArrayOrNumber } from './core/types';
import { Domain, Scene } from './scene';
import { SVGElementsFactory } from './svg_elements_factory';
import { Timer } from './timer';
import { SoundTrack } from './tracks/sound_track';
import { generateRandomId } from './utils';

const MAX_GRID_SIZE: number = 5;
const DEFAULT_PLAYBACK_FPS: number = 10;

export const DEFAULT_SCENE_KEY: string = 'main';

interface IAddActor {
  ak?: string; // actorKey
  sk?: string; // sceneKey
  li?: number; // layerIndex
}

class SceneItem {
  readonly scene: Scene;
  readonly rowIndex: number;
  readonly columnIndex: number;
  readonly rowSpan: number;
  readonly columnSpan: number;

  constructor(scene: Scene, rowIndex: number, columnIndex: number, rowSpan: number, columnSpan: number) {
    if (!Number.isInteger(rowIndex) || rowIndex < 0) {
      throw Error('`rowIndex` must be a non-negative integer number');
    }

    if (!Number.isInteger(columnIndex) || columnIndex < 0) {
      throw Error('`columnIndex` must be a non-negative integer number');
    }

    if (!Number.isInteger(rowSpan) || rowSpan <= 0) {
      throw Error('`rowSpan` must be a positive integer number');
    }

    if (!Number.isInteger(columnSpan) || columnSpan <= 0) {
      throw Error('`columnSpan` must be a positive integer number');
    }

    this.scene = scene;
    this.rowIndex = rowIndex;
    this.columnIndex = columnIndex;
    this.rowSpan = rowSpan;
    this.columnSpan = columnSpan;
  }
}

// TODO5: Think about 3D support. It's possible to use perspective transformation matrix to transform 3D scene to 2D
//        and draw it using polygons. Also google for `axisZ` attribute:
//        <svg xmlns="http://www.w3.org/2000/svg" axisZ="true">

export class Plot {
  private container: SVGElement;
  private scenesContainer: SVGElement;
  private comment: TextActor;
  private rowHeights: Array<number> = [1.0];
  private columnWidths: Array<number> = [1.0];
  private sceneItems: Map<string, SceneItem> = new Map<string, SceneItem>();
  private actors: Map<string, Actor> = new Map<string, Actor>();
  private soundTrack: SoundTrack = new SoundTrack();
  private timer: Timer = new Timer();
  private onPlaybackTimeChange: NullOr<(time: number) => void> = null;

  // TODO4: HTML standard supports `onresize` event for window only and does not support it for other HTML elements.
  //        This is a temporary workaround to watch the Plot size and update scenes if size was changed.
  //        It's need to find a way to get `onresize` event for the SVG container and update scenes only when needed.
  //        One way to do it is using SVG inside the Scene class and using relative positioning ans sizing:
  //        <svg x="30%" y="50%" width="20%" height="100%"><svg>
  //        In this case we can pass relative position and size to the methods `Scene.setPosition` and `Scene.setSize`
  //        and we will no need to wat the Plot size anymore.
  private isPlotSizeWatcherRunning: boolean = true;
  private lastSize: Size = new Size(0, 0); // Using to update scenes on size change.

  constructor(
    parentContainerId: NullOr<string> = null,
    size: NullOr<SizeOrVectorOrArray> = null,
    sceneDomain?: NullOr<Domain>,
    sceneBackgroundColor?: ColorOrStringOrArray
  ) {
    this.container = SVGElementsFactory.createSVG();
    this.container.onselectstart = () => false; // Make text unselectable.
    this.container.setAttribute('cursor', 'default'); // Disable text selection cursor.
    this.container.setAttribute('shape-rendering', 'geometricPrecision'); // Enable anti-aliasing.
    this.setParentContainerId(parentContainerId);

    this.scenesContainer = SVGElementsFactory.createSVG(this.container);
    this.scenesContainer.setAttribute('width', '100%');
    this.scenesContainer.setAttribute('height', '100%');

    const commentLayer = SVGElementsFactory.createGroup(this.container);
    this.comment = new TextActor(commentLayer);
    this.comment.setSize(24).setAlignment('cb').setIsRelativePosition([true, true]).setPosition([50.0, 95.0]);

    this.setSize(size);
    this.setGrid([1.0], [1.0]);
    this.addScene(DEFAULT_SCENE_KEY, 0, 0, 1, 1, sceneDomain, sceneBackgroundColor);

    this.timer.setOnTimeChangeHandler((v) => this.handleOnPlaybackTimeChange(v));
    this.setPlaybackFPS(DEFAULT_PLAYBACK_FPS);

    this.runPlotSizeWatcher();
  }

  // ================================ PLOT ================================

  remove() {
    this.isPlotSizeWatcherRunning = false;
    this.stopPlayback();
    // TODO4: Need to use `Promise` here and remove container only after the playback was stopped.
    this.container.remove();
  }

  setParentContainerId(parentContainerId: NullOr<string>) {
    if (!(this.container instanceof SVGElement)) {
      throw Error('`setParentContainerId` requires that `container` was a valid `SVGElement`');
    }

    if (parentContainerId !== null) {
      const parentContainer = document.getElementById(parentContainerId);
      if (!parentContainer) {
        throw Error('`parentContainerId` must be an ID attribute of an existing `HTMLElement`');
      }

      parentContainer.append(this.container);
    } else {
      const parentContainer = this.container.parentElement;
      if (parentContainer) {
        parentContainer.removeChild(this.container);
      }
    }
  }

  setSize(size: NullOr<SizeOrVectorOrArray>) {
    if (size !== null) {
      size = Size.from(size);
      this.container.style.width = `${size.width}px`;
      this.container.style.height = `${size.height}px`;
    } else {
      this.container.style.width = '100%';
      this.container.style.height = '100%';
    }

    this.updateScenesPositionAndSize();
  }

  setGrid(rowHeights: Array<number>, columnWidths: Array<number>) {
    if (rowHeights.length === 0 || columnWidths.length === 0) {
      throw Error('`rowHeights` and `columnWidths` must be non-empty arrays');
    }

    if (rowHeights.length > MAX_GRID_SIZE || columnWidths.length > MAX_GRID_SIZE) {
      throw Error(`\`rowHeights\` and \`columnWidths\` must not exceed ${MAX_GRID_SIZE} elements length`);
    }

    const isNonNegativeReal = (v: number) => Number.isFinite(v) && v >= 0.0;
    if (!rowHeights.every(isNonNegativeReal) || !columnWidths.every(isNonNegativeReal)) {
      throw Error('`rowHeights` and `columnWidths` must have only non-negative real numbers');
    }

    const sum = (v0: number, v1: number) => v0 + v1;
    if (rowHeights.reduce(sum) !== 1.0 || columnWidths.reduce(sum) !== 1.0) {
      throw Error('`rowHeights` and `columnWidths` both must have a sum of their elements equal to 1.0');
    }

    for (const sceneItem of this.sceneItems.values()) {
      if (sceneItem.rowIndex + sceneItem.rowSpan > rowHeights.length) {
        throw Error('`rowHeights` must have enough elements to hold all existing scenes');
      }

      if (sceneItem.columnIndex + sceneItem.columnSpan > columnWidths.length) {
        throw Error('`columnWidths` must have enough elements to hold all existing scenes');
      }
    }

    this.rowHeights = rowHeights;
    this.columnWidths = columnWidths;
    this.updateScenesPositionAndSize();
  }

  // ================================ SCENE ================================

  addScene(
    key: string,
    rowIndex: number,
    columnIndex: number,
    rowSpan: number,
    columnSpan: number,
    domain?: NullOr<Domain>,
    backgroundColor?: ColorOrStringOrArray
  ): Scene {
    if (key === '') {
      throw Error('`key` must be a non-empty string');
    }

    if (this.sceneItems.has(key)) {
      throw Error('`key` must be a unique scene key');
    }

    if (rowIndex + rowSpan > this.rowHeights.length) {
      throw Error('`rowIndex + rowSpan` must not exceed a number of rows');
    }

    if (columnIndex + columnSpan > this.columnWidths.length) {
      throw Error('`columnIndex + columnSpan` must not exceed a number of columns');
    }

    const scene = new Scene(this.scenesContainer, domain, backgroundColor);
    const sceneItem = new SceneItem(scene, rowIndex, columnIndex, rowSpan, columnSpan);
    this.sceneItems.set(key, sceneItem);
    this.updateScenesPositionAndSize();

    return scene;
  }

  getScene(key: string): Scene {
    if (!this.sceneItems.has(key)) {
      throw Error('`key` must be an existing scene key');
    }

    return (this.sceneItems.get(key) as SceneItem).scene;
  }

  removeScene(key: string) {
    const scene = this.getScene(key);
    scene.remove();
    this.sceneItems.delete(key);
  }

  removeAllScenes() {
    this.sceneItems.forEach((_, key: string) => this.removeScene(key));
  }

  // ================================ ACTORS ================================

  isActorExist(key: string): boolean {
    return this.actors.has(key);
  }

  addActor<IActorType extends IActor = IActor, ActorType extends Actor = Actor>(
    actorConstructor: new (parentContainer: SVGGElement) => ActorType,
    parameters?: IAddActor & IActorType
  ): ActorType {
    const sceneKey = parameters && parameters.sk ? parameters.sk : DEFAULT_SCENE_KEY;
    const layerIndex = parameters && parameters.li ? parameters.li : 0;
    const scene = this.getScene(sceneKey);
    const parentContainer = scene.getLayer(layerIndex).container;

    let actor: ActorType;
    try {
      actor = new actorConstructor(parentContainer);
      actor.setParameters(parameters ?? {});
    } catch {
      throw Error('Exception occurred while creating an actor');
    }

    const actorKey = parameters && parameters.ak ? parameters.ak : generateRandomId();
    if (this.isActorExist(actorKey)) {
      throw Error('`ak` must be a unique actor key');
    }

    this.actors.set(actorKey, actor);

    return actor;
  }

  getActor<ActorType extends Actor = Actor>(key: string): ActorType {
    if (!this.isActorExist(key)) {
      throw Error('`key` must be an existing actor key');
    }

    return this.actors.get(key) as unknown as ActorType;
  }

  removeActor(key: string) {
    const actor = this.getActor(key);
    actor.remove();
    this.actors.delete(key);
  }

  removeAllActors() {
    this.actors.forEach((_, key: string) => this.removeActor(key));
  }

  group(actorKeys: Array<string>, parameters?: IAddActor): GroupActor {
    const groupActor = this.addActor(GroupActor, parameters);
    for (const actorKey of actorKeys) {
      const actor = this.getActor(actorKey);
      groupActor.add(actor, actorKey);
      this.actors.delete(actorKey);
    }

    return groupActor;
  }

  addLine(
    startPosition: VectorOrArrayOrNumber,
    endPosition: VectorOrArrayOrNumber,
    parameters?: IAddActor & ILineActor
  ): LineActor {
    return this.addActor<ILineActor, LineActor>(LineActor, { startPosition, endPosition, ...parameters });
  }

  addBox(position: VectorOrArrayOrNumber, size: SizeOrVectorOrArray, parameters?: IAddActor & IBoxActor): BoxActor {
    return this.addActor<IBoxActor, BoxActor>(BoxActor, { position, size, ...parameters });
  }

  addCircle(position: VectorOrArrayOrNumber, radius: number, parameters?: IAddActor & ICircleActor): CircleActor {
    return this.addActor<ICircleActor, CircleActor>(CircleActor, { position, radius, ...parameters });
  }

  addPath(vertices: Array<VectorOrArrayOrNumber>, parameters?: IAddActor & IPathActor): PathActor {
    return this.addActor<IPathActor, PathActor>(PathActor, { vertices, ...parameters });
  }

  addText(text: string, position: VectorOrArrayOrNumber, parameters?: IAddActor & ITextActor): TextActor {
    return this.addActor<ITextActor, TextActor>(TextActor, { text, position, ...parameters });
  }

  addGrid(parameters?: IAddActor): GridActor {
    return this.addActor<IActor, GridActor>(GridActor, parameters);
  }

  addTable(
    header: Array<string>,
    data: Array<Array<string>>,
    position: VectorOrArrayOrNumber,
    parameters?: IAddActor & ITableActor
  ): TableActor {
    return this.addActor<ITableActor, TableActor>(TableActor, { header, data, position, ...parameters });
  }

  // ================================ COMMENTS ================================

  showComment(comment: string, time: number = 1.0, isRelativeTime: boolean = true, duration: number = 1.0) {
    if (!Number.isFinite(time)) {
      throw Error('`time` must be a real number');
    }

    if (time < 0.0 || !isRelativeTime) {
      throw Error('`time` is allowed to be negative only when `isRelativeTime` is `true`');
    }

    if (!Number.isFinite(duration) || duration < 0.0) {
      throw Error('`duration` must be a non-negative real number');
    }

    time += isRelativeTime ? this.getPlaybackDuration() : 0.0;
    const startTime = Math.max(time - duration, 0.0);
    this.comment.setText(comment, startTime, 0.0).setOpacity(0.0, startTime, duration).setOpacity(1.0, time, duration);
  }

  hideComment(time: number = 1.0, isRelativeTime: boolean = true, duration: number = 1.0) {
    if (!Number.isFinite(time)) {
      throw Error('`time` must be a real number');
    }

    if (time < 0.0 || !isRelativeTime) {
      throw Error('`time` is allowed to be negative only when `isRelativeTime` is `true`');
    }

    if (!Number.isFinite(duration) || duration < 0.0) {
      throw Error('`duration` must be a non-negative real number');
    }

    time += isRelativeTime ? this.getPlaybackDuration() : 0.0;
    const startTime = Math.max(time - duration, 0.0);
    this.comment.setText('', time, 0.0).setOpacity(0.0, time, duration).setOpacity(1.0, startTime, duration);
  }

  removeAllComments() {
    this.comment.removeAllTracks();
    this.comment.setText('');
  }

  // ================================ SOUNDS ================================

  loadSound(fileName: string, key: string) {
    this.soundTrack.loadSound(fileName, key);
  }

  addSound(key: string, time: number) {
    this.soundTrack.addSound(key, time);
  }

  removeAllSounds() {
    this.soundTrack.removeAllSounds();
  }

  // ================================ PLAYBACK ================================

  startPlayback(playbackTimeLimit?: number) {
    playbackTimeLimit = playbackTimeLimit ?? this.getPlaybackDuration();
    if (!Number.isFinite(playbackTimeLimit) || playbackTimeLimit <= 0.0) {
      throw Error('`playbackTimeLimit` must be a positive real number');
    }

    this.setPlaybackTimeLimit(playbackTimeLimit);
    this.timer.setIsLooped(true);
    this.timer.start();
  }

  pausePlayback() {
    this.timer.pause();
  }

  stopPlayback() {
    this.timer.stop();
  }

  setPlaybackFPS(playbackFPS: number) {
    if (!Number.isInteger(playbackFPS) || playbackFPS <= 0) {
      throw Error('`playbackFPS` must be a positive integer number');
    }

    this.timer.setUpdateInterval(1.0 / playbackFPS);
  }

  setPlaybackTimeLimit(playbackTimeLimit: NullOr<number>) {
    this.timer.setTimeLimit(playbackTimeLimit);
  }

  getPlaybackTimeLimit(): NullOr<number> {
    return this.timer.getTimeLimit();
  }

  setPlaybackTimeScale(playbackTimeScale: number) {
    this.timer.setTimeScale(playbackTimeScale);
  }

  setOnPlaybackTimeChangeHandler(onPlaybackTimeChange: NullOr<(time: number) => void>) {
    this.onPlaybackTimeChange = onPlaybackTimeChange;
  }

  setPlaybackTime(playbackTime: number) {
    this.timer.setTime(playbackTime);
  }

  getPlaybackDuration(): number {
    const actorsDuration = Math.max(...Array.from(this.actors.values()).map((v) => v.getTrackDuration()), 0.0);
    const commentsDuration = this.comment.getTrackDuration();
    // TODO4: Do we need to take sounds into account here?
    const playbackDuration = Math.max(actorsDuration, commentsDuration);
    return playbackDuration;
  }

  mutePlaybackSound() {
    this.soundTrack.mute();
  }

  unmutePlaybackSound() {
    this.soundTrack.unmute();
  }

  defineActor<Actor>(actor: Actor, key: string) {
    // TODO4: Implement `defineActor<Actor>()` method, which must insert actor's template to the <defs> block to use it
    //        in any place later. Also it's need to implement useActor(key: string) method which must use <use> block to
    //        use actor from the <defs> block. If actor template will change it will change in all places automatically.
  }

  addStyle(style: string, key: string) {
    // TODO4: Implement `addStyle(style, key)` method, which must allow to define and store sets of named styles.
    //        At the DOM level this can be implemented using the CSS classes, e.g:
    //        `addStyle(s, 'style1')` adds a class named `style1` to the <style> block:
    //        <style>
    //          .style1 {
    //            fill: green;
    //            stroke-width: 3;
    //            ...
    //          }
    //        </style>
    //        To apply this style to specific group (scene or actor) need to set its class attribute to the 'style1'.
    //        For this purpose Plot, Scene and Actor classes must have setStyle(key) method.
  }

  setStyle(key: string) {
    // TODO4: Implement `setStyle(key)` method in the Plot, Scene  and Actor classes.
    //        It will make possible to set styles inheriting by multiple scenes/actors.
    //        Every `setStyle` method must set the `class` attribute of its own `container` to the `key` class name.
  }

  private handleOnPlaybackTimeChange(time: number) {
    this.actors.forEach((v) => v.setTrackTime(time));
    this.comment.setTrackTime(time);
    this.soundTrack.setTime(time);
    if (this.onPlaybackTimeChange !== null) {
      this.onPlaybackTimeChange(time);
    }
  }

  private updateScenesPositionAndSize() {
    const plotSize = this.getSize();
    if (plotSize.getArea() <= Number.EPSILON) {
      return;
    }

    const sum = (a: number, b: number) => a + b;
    for (const item of this.sceneItems.values()) {
      const relativeX = this.columnWidths.slice(0, item.columnIndex).reduce(sum, 0.0);
      const relativeY = this.rowHeights.slice(0, item.rowIndex).reduce(sum, 0.0);
      const relativeWidth = this.columnWidths.slice(item.columnIndex, item.columnIndex + item.columnSpan).reduce(sum);
      const relativeHeight = this.rowHeights.slice(item.rowIndex, item.rowIndex + item.rowSpan).reduce(sum);
      item.scene.setPosition([relativeX * plotSize.width, relativeY * plotSize.height]);
      item.scene.setSize([relativeWidth * plotSize.width, relativeHeight * plotSize.height]);
    }
  }

  private getSize(): Size {
    return new Size(this.container.clientWidth, this.container.clientHeight);
  }

  private async runPlotSizeWatcher() {
    while (this.isPlotSizeWatcherRunning) {
      const size = this.getSize();
      if (!size.isEqual(this.lastSize)) {
        this.updateScenesPositionAndSize();
        // TODO5: When all scenes are removed size will be 0 since Plot is empty.
        //        Probably it's need to add background rectangle to the Plot,
        //        so it newer be empty.
        size.width > 0 && this.comment.setWidthLimit(size.width);
        this.lastSize = size;
      }

      await new Promise((v) => setTimeout(v, 500));
    }
  }
}
