import { Box, Button, Slider, Typography } from '@mui/material';
import React from 'react';

interface IBubbleSortControls {
  minValuesNumber: number;
  maxValuesNumber: number;
  initialValuesNumber: number;
  onGenerateRandomValuesButtonClick: (numberOfValuesToGenerate: number) => void;
}

export function BubbleSortControls({
  minValuesNumber,
  maxValuesNumber,
  initialValuesNumber,
  onGenerateRandomValuesButtonClick,
}: IBubbleSortControls) {
  const [numberOfValuesToGenerate, setNumberOfValuesToGenerate] = React.useState(initialValuesNumber);

  return (
    <Box sx={{ maxWidth: '250px', padding: '25px', display: 'flex', flexFlow: 'column', gap: '5px' }}>
      <Typography color={'text.primary'}>
        {`Set desired number of values using the slider and press the "Generate" button.`}
      </Typography>
      <Slider
        min={minValuesNumber}
        max={maxValuesNumber}
        defaultValue={initialValuesNumber}
        onChange={(e, v) => setNumberOfValuesToGenerate(Array.isArray(v) ? v[0] : v)}
      />
      <Button variant='outlined' onClick={() => onGenerateRandomValuesButtonClick(numberOfValuesToGenerate)}>
        {`Generate ${numberOfValuesToGenerate} random values`}
      </Button>
    </Box>
  );
}
