import { Box, Paper } from '@mui/material';
import React from 'react';
import ADDITIONAL_INFORMATION_DATA from '../../assets/pages/home/additional_information/additional_information.json';
import { Text } from '../../components/text';
import { CONTEXT } from '../../context';
import { TranslatableString } from '../../types';

// Represents data storing in the `additional_information.json` file.
type AdditionalInformationData = {
  title: TranslatableString;
  descriptionFilePath: TranslatableString;
};

export const AdditionalInformation: React.FC = () => {
  const { language } = React.useContext(CONTEXT);

  const additionalInformationData = React.useMemo((): AdditionalInformationData => ADDITIONAL_INFORMATION_DATA, []);

  return (
    <Paper sx={{ width: '100%' }}>
      <Box display='flex' flexDirection='column' margin='10px'>
        <Text variant='h1' align='center'>
          {additionalInformationData.title[language]}
        </Text>
        <Text textFilePath={additionalInformationData.descriptionFilePath[language]} useMarkdown={true} />
      </Box>
    </Paper>
  );
};
