import { createTheme } from '@mui/material';

declare module '@mui/material/styles' {
  interface TypeBackground {
    card: string;
  }
}

const palette = {
  background: {
    card: '#5050500A'
  }
};

const components = {
  MuiLink: {
    defaultProps: {
      style: {
        color: 'inherit'
      }
    }
  }
};

const typography = {
  h1: {
    fontSize: '6.0vmin',
    marginBottom: '1.00em'
  },
  h2: {
    fontSize: '5.0vmin',
    marginBottom: '0.75em'
  },
  h3: {
    fontSize: '4.0vmin',
    marginBottom: '0.50em'
  },
  body1: {
    fontSize: '3.0vmin',
    marginBottom: '0.25em'
  }
};

export namespace Themes {
  export const LIGHT_THEME = createTheme({
    palette: {
      mode: 'light',
      ...palette
    },
    components: components,
    typography: typography
  });

  export const DARK_THEME = createTheme({
    palette: {
      mode: 'dark',
      ...palette
    },
    components: components,
    typography: typography
  });
}
