import { NullOr, VectorOrArrayOrNumber } from '../core/types';
import { Vector } from '../core/vector';
import { SVGElementsFactory } from '../svg_elements_factory';
import { Track } from '../tracks/track';
import { setElementAttributes } from '../utils';
import { Actor, IActor } from './actor';
import { GridActor } from './grid_actor';

export interface ITableActor extends IActor {
  header?: Array<string>;
  data?: Array<Array<string>>;
  isYFlipped?: boolean;
  position?: VectorOrArrayOrNumber;
  fontSize?: number;
}

// TODO4: Add an option to change table orientation.

// TODO5: ??? This class should be derived from the GroupActor, because each element of the table will be Actor.
//        It needed to make it possible to control style of each element (cells, borders), and they tracks.
//        Probably deriving from the Group is not best solution, since it will require to use string keys to obtain
//        components, such as text, border, cell, etc. It's better to derive from the Actor and add handful methods
//        like getText(row, column), getCell(row, column) etc to give the user better experience.
//        Same for the GridActor class.

export class TableActor extends Actor {
  private position: Vector = new Vector(0.0, 0.0);
  private fontSize: number = 10.0;
  private cellSizeRelativeMargin: number = 0.1;
  private cellText: Array<Array<SVGTextElement>> = [[]]; // TODO4: Use `TextActor`.
  private grid: GridActor;

  constructor(parentContainer: SVGGElement) {
    super(parentContainer);
    this.setStrokeColor('none');
    this.grid = new GridActor(this.container);
  }

  setParameters(parameters: ITableActor) {
    super.setParameters(parameters);

    if (parameters.header !== undefined) {
      this.setHeader(parameters.header);
    }

    if (parameters.data !== undefined) {
      this.setData(parameters.data);
    }

    if (parameters.position !== undefined) {
      this.setPosition(parameters.position);
    }

    if (parameters.fontSize !== undefined) {
      this.setFontSize(parameters.fontSize);
    }
  }

  setHeader(header: Array<string>) {
    this.setSize(this.cellText.length, header.length);
    for (let columnIndex = 0; columnIndex < header.length; ++columnIndex) {
      this.cellText[0][columnIndex].textContent = header[columnIndex];
    }
    this.adjustCellPositions();
  }

  setData(data: Array<Array<string>>, time?: number, duration?: number) {
    const columnsNumber = this.cellText[0].length;
    if (data.some((v) => v.length !== columnsNumber)) {
      throw Error('Every row in `data` must be the same size as header');
    }

    if (time !== undefined) {
      if (!this.tracks.has('data')) {
        const track = new Track<Array<Array<string>>>((data: Array<Array<string>>) => this.setData(data));
        this.tracks.set('data', track);
      }
      (this.tracks.get('data') as Track<Array<Array<string>>>).addValue(data, time, duration);
    } else {
      this.setSize(data.length + 1, columnsNumber);
      for (let rowIndex = 0; rowIndex < data.length; ++rowIndex) {
        for (let columnIndex = 0; columnIndex < data[rowIndex].length; ++columnIndex) {
          const cell = this.cellText[rowIndex + 1][columnIndex];
          cell.textContent = data[rowIndex][columnIndex];
        }
      }
      this.adjustCellPositions();
    }
  }

  setIsYFlipped(isYFlipped: NullOr<boolean>): TableActor {
    if (isYFlipped) {
      this.container.setAttribute('style', 'transform-box: fill-box; transform-origin: 0 0; transform: scaleY(-1)');
    } else {
      this.container.removeAttribute('style');
    }

    return this;
  }

  setPosition(position: VectorOrArrayOrNumber) {
    this.position = Vector.from(position);
    this.grid.setTranslation(this.position);
    this.adjustCellPositions();
  }

  setFontSize(fontSize: number): TableActor {
    if (!Number.isFinite(fontSize) || fontSize < 0.0) {
      throw Error('`fontSize` must be a non-negative real number');
    }

    this.fontSize = fontSize;
    this.cellText.forEach((row) => row.forEach((cell) => cell.setAttribute('font-size', fontSize.toString())));

    return this;
  }

  setGridStrokeWidth(gridStrokeWidth: number): TableActor {
    this.grid.setStrokeWidth(gridStrokeWidth);
    return this;
  }

  private setSize(newRowsNumber?: number, newColumnsNumber?: number) {
    if (newRowsNumber !== undefined) {
      this.setRowsNumber(newRowsNumber);
    }

    if (newColumnsNumber !== undefined) {
      this.setColumnsNumber(newColumnsNumber);
    }
  }

  private createCellText(): SVGTextElement {
    const text = SVGElementsFactory.createText(this.container);
    setElementAttributes(text, { 'font-size': this.fontSize, 'text-anchor': 'middle', 'alignment-baseline': 'middle' });
    return text;
  }

  private setRowsNumber(newRowsNumber: number) {
    const rowsNumber = this.getRowsNumber();
    if (newRowsNumber > rowsNumber) {
      this.addRows(newRowsNumber - rowsNumber);
    } else if (newRowsNumber < rowsNumber) {
      this.removeRows(rowsNumber - newRowsNumber);
    }
  }

  private getRowsNumber(): number {
    const rowsNumber = this.cellText.length;
    return rowsNumber;
  }

  private setColumnsNumber(newColumnsNumber: number) {
    const columnsNumber = this.getColumnsNumber();
    if (newColumnsNumber > columnsNumber) {
      this.addColumns(newColumnsNumber - columnsNumber);
    }
    if (newColumnsNumber < columnsNumber) {
      this.removeColumns(columnsNumber - newColumnsNumber);
    }
  }

  private getColumnsNumber(): number {
    // There is always at least header row in the table (even if it's empty), so we can safely indexing the first row.
    const columnsNumber = this.cellText[0].length;
    return columnsNumber;
  }

  // Add new row to the end.
  private addRow() {
    const columnsNumber = this.getColumnsNumber();
    const arrayOfNulls = Array(columnsNumber).fill(null);
    const row = arrayOfNulls.map(() => this.createCellText());
    this.cellText.push(row);
  }

  private addRows(rowsNumberToAdd: number) {
    for (rowsNumberToAdd; rowsNumberToAdd > 0; --rowsNumberToAdd) {
      this.addRow();
    }
  }

  // Remove the last row.
  private removeRow() {
    const row = this.cellText.pop() as Array<SVGTextElement>;
    row.forEach((v) => v.remove());
  }

  private removeRows(rowsNumberToRemove: number) {
    for (rowsNumberToRemove; rowsNumberToRemove > 0; --rowsNumberToRemove) {
      this.removeRow();
    }
  }

  // Add new column to the end.
  private addColumn() {
    const rowsNumber = this.getRowsNumber();
    for (let rowIndex = 0; rowIndex < rowsNumber; ++rowIndex) {
      const cell = this.createCellText();
      this.cellText[rowIndex] = [...this.cellText[rowIndex], cell];
    }
  }

  private addColumns(columnsNumberToAdd: number) {
    for (columnsNumberToAdd; columnsNumberToAdd > 0; --columnsNumberToAdd) {
      this.addColumn();
    }
  }

  // Remove the last column.
  private removeColumn() {
    const rowsNumber = this.getRowsNumber();
    for (let rowIndex = 0; rowIndex < rowsNumber; ++rowIndex) {
      const cell = this.cellText[rowIndex].pop() as SVGTextElement;
      cell.remove();
    }
  }

  private removeColumns(columnsNumberToRemove: number) {
    for (columnsNumberToRemove; columnsNumberToRemove > 0; --columnsNumberToRemove) {
      this.removeColumn();
    }
  }

  private adjustCellPositions() {
    const rowsNumber = this.cellText.length;
    const columnsNumber = this.cellText[0].length;
    const rowHeights = this.computeRowHeights();
    const columnWidths = this.computeColumnWidths();
    const sum = (s: number, v: number) => s + v;
    this.grid.setGrid(rowHeights, columnWidths);
    for (let rowIndex = 0; rowIndex < rowsNumber; ++rowIndex) {
      const y = this.position.y + rowHeights.slice(0, rowIndex).reduce(sum, 0) + 0.5 * rowHeights[rowIndex];
      for (let columnIndex = 0; columnIndex < columnsNumber; ++columnIndex) {
        const cell = this.cellText[rowIndex][columnIndex];
        const x = this.position.x + columnWidths.slice(0, columnIndex).reduce(sum, 0) + 0.5 * columnWidths[columnIndex];
        setElementAttributes(cell, { x, y });
      }
    }
  }

  private computeRowHeights(): Array<number> {
    const rowsNumber = this.cellText.length;
    const scaleFactor = 1.0 + this.cellSizeRelativeMargin;
    let rowHeights: Array<number> = [];
    for (let rowIndex = 0; rowIndex < rowsNumber; ++rowIndex) {
      const rowHeight = Math.max(...this.cellText[rowIndex].map((v) => v.getBBox().height), 0.0);
      rowHeights.push(scaleFactor * rowHeight);
    }
    return rowHeights;
  }

  private computeColumnWidths(): Array<number> {
    const rowsNumber = this.cellText.length;
    const columnsNumber = this.cellText[0].length;
    const scaleFactor = 1.0 + this.cellSizeRelativeMargin;
    let columnWidths: Array<number> = [];
    for (let columnIndex = 0; columnIndex < columnsNumber; ++columnIndex) {
      let columnWidth = 0.0;
      for (let rowIndex = 0; rowIndex < rowsNumber; ++rowIndex) {
        columnWidth = Math.max(columnWidth, this.cellText[rowIndex][columnIndex].getBBox().width);
      }
      columnWidths.push(scaleFactor * columnWidth);
    }
    return columnWidths;
  }
}
