import { Box } from '@mui/material';
import React from 'react';
import { checkFileURL, checkImageLink } from '../utils';

type ImageProps = {
  imageLink: string;
  children?: React.ReactNode;
  [key: string]: any;
};

export const Image: React.FC<ImageProps> = ({ imageLink, children, ...props }) => {
  const [backgroundImageURL, setBackgroundImageURL] = React.useState<string | null>(null);

  React.useEffect(() => {
    const isImageLink = checkImageLink(imageLink);
    if (!isImageLink) {
      console.error(`Wrong image link: "${imageLink}"`);
      throw Error('`imageLink` must be either an image file path of an image URL');
    }

    const isURL = checkFileURL(imageLink);
    if (isURL) {
      setBackgroundImageURL(`url(${imageLink})`);
    } else {
      const importFile = () => import(`../assets/${imageLink}`);
      const getBackgroundImageURL = (file: any) => `url(${file.default})`;
      const showError = (error: any) => console.error(`Error occurred while importing image file: ${error}`);
      importFile().then(getBackgroundImageURL).then(setBackgroundImageURL).catch(showError);
    }
  }, [imageLink]);

  props.style = props.style ?? {};

  const backgroundImageStyle = React.useMemo(() => {
    let style = {};
    if (backgroundImageURL) {
      style = {
        backgroundSize: props.style.backgroundSize ?? 'contain',
        backgroundPosition: props.style.backgroundPosition ?? 'center',
        backgroundRepeat: props.style.backgroundRepeat ?? 'no-repeat',
        backgroundImage: backgroundImageURL
      };
    }
    return style;
  }, [backgroundImageURL, props.style]);

  props.style = { ...props.style, ...backgroundImageStyle };

  return <Box {...props}>{children}</Box>;
};
