import { Color } from '../core/color';
import { Transform } from '../core/transform';
import { ColorOrStringOrArray, NullOr, VectorOrArrayOrNumber } from '../core/types';
import { Vector } from '../core/vector';
import { SVGElementsFactory } from '../svg_elements_factory';
import { ColorTrack } from '../tracks/color_track';
import { NumberTrack } from '../tracks/number_track';
import { StringTrack } from '../tracks/string_track';
import { Track } from '../tracks/track';
import { BooleanTrack } from '../tracks/boolean_track';
import { VectorTrack } from '../tracks/vector_track';

type OnMouseButtonClickHandler = (actor: Actor, x: number, y: number) => void;

// TODO4: Find a way to define actor's anchor point.
//        It have to make it possible to define relative origin point for translation, rotation and scale operations.
//        See the `transform-origin` and `transform-box` attributes.
//        https://developer.mozilla.org/en-US/docs/Web/SVG/Attribute/transform-origin
//        https://developer.mozilla.org/en-US/docs/Web/CSS/transform-box

// `null` attribute value means it must be removed from an SVG element.
export interface IActor {
  t?: VectorOrArrayOrNumber; // translation
  r?: number; // rotation
  s?: VectorOrArrayOrNumber; // scale
  c?: NullOr<ColorOrStringOrArray>; // color
  fc?: NullOr<ColorOrStringOrArray>; // fillColor
  sc?: NullOr<ColorOrStringOrArray>; // strokeColor
  o?: NullOr<number>; // opacity
  fo?: NullOr<number>; // fillOpacity
  so?: NullOr<number>; // strokeOpacity
  sw?: NullOr<number>; // strokeWidth
  // TODO4: Add `sp` parameter for `strokePattern`.
}

/**
 * Actor is a base class of all actors, creating by the Plot.
 * This class allows each actor to have any number of SVG primitives storing in the SVG group named `container`.
 * Note that `container` group can also contain other SVG groups.
 * All these primitives share a common style and a common set of tracks.
 * If you need a composite actor which elements can have different styles and independently controllable properties,
 * then you need to use the GroupActor.
 */
export class Actor {
  protected container: SVGGElement; // Container group for all elements of the actor.
  protected tracks: Map<string, Track<any>> = new Map<string, Track<any>>();
  private translation: Vector = Vector.zeros();
  private rotation: number = 0.0;
  private scale: Vector = Vector.ones();
  private relativeAttributes: Set<string> = new Set<string>();
  private onLeftMouseButtonClick: NullOr<OnMouseButtonClickHandler> = null;

  constructor(parentContainer: SVGGElement, isGroup: boolean = false) {
    this.container = SVGElementsFactory.createGroup(parentContainer);

    this.container.onclick = (v) => this.onMouseButtonClick(v);
    this.setColor('#fff'); // White is the default color for all actors.

    this.updateTransform();

    // TODO4: Probably all actors which have a fill should have `stroke: none` by default.
  }

  setParameters(parameters: IActor) {
    if (parameters.t !== undefined) {
      this.setTranslation(parameters.t);
    }

    if (parameters.r !== undefined) {
      this.setRotation(parameters.r);
    }

    if (parameters.s !== undefined) {
      this.setScale(parameters.s);
    }

    const fillColor = parameters.fc !== undefined ? parameters.fc : parameters.c;
    if (fillColor !== undefined) {
      this.setFillColor(fillColor);
    }

    const strokeColor = parameters.sc !== undefined ? parameters.sc : parameters.c;
    if (strokeColor !== undefined) {
      this.setStrokeColor(strokeColor);
    }

    const fillOpacity = parameters.fo !== undefined ? parameters.fo : parameters.o;
    if (fillOpacity !== undefined) {
      this.setFillOpacity(fillOpacity);
    }

    const strokeOpacity = parameters.so !== undefined ? parameters.so : parameters.o;
    if (strokeOpacity !== undefined) {
      this.setStrokeOpacity(strokeOpacity);
    }

    if (parameters.sw !== undefined) {
      this.setStrokeWidth(parameters.sw);
    }
  }

  setTranslation(translation: VectorOrArrayOrNumber, time?: number, duration?: number): Actor {
    translation = Vector.from(translation);

    if (time !== undefined) {
      let track = this.tracks.get('translation');
      if (!track) {
        track = new VectorTrack((v: Vector) => this.setTranslation(v));
        this.tracks.set('translation', track);
      }

      track.addValue(translation, time, duration);
    } else {
      this.translation = translation;

      // const transform = Transform.fromTSRT(this.translation, this.scale, this.rotation);
      // this.container.setAttribute('transform', transform.toString());

      this.updateTransform();
    }

    return this;
  }

  setRotation(rotation: number, time?: number, duration?: number): Actor {
    if (!Number.isFinite(rotation)) {
      throw Error('`rotation` must be a real number');
    }

    if (time !== undefined) {
      let track = this.tracks.get('rotation');
      if (!track) {
        track = new NumberTrack((v: number) => this.setRotation(v));
        this.tracks.set('rotation', track);
      }

      track.addValue(rotation, time, duration);
    } else {
      this.rotation = rotation;

      // const transform = Transform.fromTSRT(this.translation, this.scale, this.rotation);
      // this.container.setAttribute('transform', transform.toString());

      this.updateTransform();
    }

    return this;
  }

  setScale(scale: VectorOrArrayOrNumber, time?: number, duration?: number): Actor {
    scale = Vector.from(scale);

    if (time !== undefined) {
      let track = this.tracks.get('scale');
      if (!track) {
        track = new VectorTrack((v: Vector) => this.setScale(v));
        this.tracks.set('scale', track);
      }

      track.addValue(scale, time, duration);
    } else {
      this.scale = scale;

      // const transform = Transform.fromTSRT(this.translation, this.scale, this.rotation);
      // this.container.setAttribute('transform', transform.toString());

      this.updateTransform();
    }

    return this;
  }

  private updateTransform() {
    let transform = Transform.fromTSRT(this.translation, this.scale, this.rotation);
    this.container.setAttribute('transform', transform.toString());
  }

  setColor(color: NullOr<ColorOrStringOrArray>, time?: number, duration?: number): Actor {
    this.setFillColor(color, time, duration);
    this.setStrokeColor(color, time, duration);
    return this;
  }

  setFillColor(fillColor: NullOr<ColorOrStringOrArray>, time?: number, duration?: number): Actor {
    fillColor = fillColor !== null ? Color.from(fillColor) : fillColor;
    this.setAttribute(this.container, 'fill', 'fillColor', fillColor, time, duration);
    return this;
  }

  setStrokeColor(strokeColor: NullOr<ColorOrStringOrArray>, time?: number, duration?: number): Actor {
    strokeColor = strokeColor !== null ? Color.from(strokeColor) : strokeColor;
    this.setAttribute(this.container, 'stroke', 'strokeColor', strokeColor, time, duration);
    return this;
  }

  setOpacity(opacity: NullOr<number>, time?: number, duration?: number): Actor {
    this.setFillOpacity(opacity, time, duration);
    this.setStrokeOpacity(opacity, time, duration);
    return this;
  }

  setFillOpacity(fillOpacity: NullOr<number>, time?: number, duration?: number): Actor {
    if (fillOpacity !== null) {
      if (!Number.isFinite(fillOpacity) || fillOpacity < 0.0 || fillOpacity > 1.0) {
        throw Error('`fillOpacity` must be either `null` or a real number from range [0.0, 1.0]');
      }
    }

    this.setAttribute(this.container, 'fill-opacity', 'fillOpacity', fillOpacity, time, duration);

    return this;
  }

  setStrokeOpacity(strokeOpacity: NullOr<number>, time?: number, duration?: number): Actor {
    if (strokeOpacity !== null) {
      if (!Number.isFinite(strokeOpacity) || strokeOpacity < 0.0 || strokeOpacity > 1.0) {
        throw Error('`strokeOpacity` must be either `null` or a real number from range [0.0, 1.0]');
      }
    }

    this.setAttribute(this.container, 'stroke-opacity', 'strokeOpacity', strokeOpacity, time, duration);

    return this;
  }

  setStrokeWidth(strokeWidth: NullOr<number>, time?: number, duration?: number): Actor {
    if (strokeWidth !== null && (!Number.isFinite(strokeWidth) || strokeWidth < 0.0)) {
      throw Error('`strokeWidth` must be either `null` or a non-negative real number');
    }

    this.setAttribute(this.container, 'stroke-width', 'strokeWidth', strokeWidth, time, duration);

    return this;
  }

  setOnLeftMouseButtonClickHandler(onLeftMouseButtonClick: NullOr<OnMouseButtonClickHandler>): Actor {
    this.onLeftMouseButtonClick = onLeftMouseButtonClick;
    return this;
  }

  removeAllTracks() {
    this.tracks.clear();
  }

  // This method must not be used directly by user.
  remove() {
    this.container.remove();
  }

  // This method must not be used directly by user.
  setParentContainer(parentContainer: SVGGElement) {
    const currentParentContainer = this.container.parentElement as Element;
    currentParentContainer.removeChild(this.container);
    parentContainer.append(this.container);
  }

  // This method must not be used directly by user.
  setTrackTime(trackTime: number) {
    this.tracks.forEach((v) => v.setTime(trackTime));
  }

  // This method must not be used directly by user.
  getTrackDuration(): number {
    return Math.max(...Array.from(this.tracks.values()).map((v) => v.getDuration()), 0.0);
  }

  // TODO4: This won't work if the relative flag was set after the attribute itself.
  //        Need to fix attribute when the relative flag changes.
  protected setIsRelativeAttribute(key: string, isRelative: boolean) {
    const allowedRelativeAttributesList = ['x', 'y', 'x1', 'y1', 'x2', 'y2', 'cx', 'cy', 'width', 'height', 'r'];
    if (!allowedRelativeAttributesList.includes(key)) {
      throw Error(`\`key\` must be one of ${allowedRelativeAttributesList}`);
    }

    isRelative ? this.relativeAttributes.add(key) : this.relativeAttributes.delete(key);
  }

  protected setElementAttribute(element: SVGElement, key: string, value: NullOr<Color | number | string | boolean>) {
    if (value !== null) {
      const unit = this.relativeAttributes.has(key) ? '%' : '';
      element.setAttribute(key, value.toString() + unit);
    } else {
      element.removeAttribute(key);
    }
  }

  protected setProperty<T extends NullOr<Color | string | number | boolean>>(
    key: string, // This is a property key, NOT an attribute key. Not all properties have corresponding SVG attributes.
    value: T,
    callback: (value: T) => void, // TODO4: Probably `setter` or `handler`?
    time?: number,
    duration?: number
  ) {
    if (key.length === 0) {
      throw Error('`key` must be a non-empty string');
    }

    if (time !== undefined) {
      if (value === null) {
        throw Error(`${key} doesn't allowed to be \`null\` when \`time\` is passed`);
      }

      let track = this.tracks.get(key);
      if (!track) {
        if (Color.isColor(value)) {
          track = new ColorTrack((v) => callback(v as T));
        } else if (typeof value === 'string') {
          track = new StringTrack((v) => callback(v as T));
        } else if (typeof value === 'number') {
          track = new NumberTrack((v) => callback(v as T));
        } else {
          track = new BooleanTrack((v) => callback(v as T));
        }

        this.tracks.set(key, track);
      }

      track.addValue(value, time, duration);
    } else {
      callback(value);
    }
  }

  protected setAttribute(
    element: SVGElement,
    key: string,
    name: string,
    value: NullOr<Color | number | string>,
    time?: number,
    duration?: number
  ) {
    if (key.length === 0) {
      throw Error('`key` must be a non-empty string');
    }

    if (name.length === 0) {
      throw Error('`name` must be a non-empty string');
    }

    if (time !== undefined) {
      if (value === null) {
        throw Error(`${name} doesn't allowed to be \`null\` when \`time\` is passed`);
      }

      let track = this.tracks.get(key);
      if (!track) {
        if (value instanceof Color) {
          track = new ColorTrack((value: Color) => this.setAttribute(element, key, name, value));
        } else if (typeof value === 'number') {
          track = new NumberTrack((value: number) => this.setAttribute(element, key, name, value));
        } else {
          track = new StringTrack((value: string) => this.setAttribute(element, key, name, value));
        }

        this.tracks.set(key, track);
      }

      track.addValue(value, time, duration);
    } else {
      if (value !== null) {
        element.setAttribute(key, value.toString());
      } else {
        element.removeAttribute(key);
      }
    }
  }

  private onMouseButtonClick(mouseEvent: MouseEvent) {
    if (this.onLeftMouseButtonClick && mouseEvent.button === 0) {
      this.onLeftMouseButtonClick(this, mouseEvent.x, mouseEvent.y);
    }
  }
}
