import { Box } from './core/box';
import { Color } from './core/color';
import { Size } from './core/size';
import { Transform } from './core/transform';
import { ColorOrStringOrArray, Dimension, NullOr, SizeOrVectorOrArray, VectorOrArrayOrNumber } from './core/types';
import { Vector } from './core/vector';
import { Layer, LayerInterface } from './layer';
import { SVGElementsFactory } from './svg_elements_factory';
import { generateRandomId, setElementAttributes } from './utils';

export type OnMouseButtonClickHandler = (x: number, y: number) => void;

export class Domain extends Box {
  // If set to 'x' or 'y' then corresponding dimension will be computed automatically.
  readonly fitDimension: NullOr<Dimension> = null;

  constructor(x: number, y: number, width: number, height: number, fitDimension: NullOr<Dimension> = null) {
    super(x, y, width, height);
    this.fitDimension = fitDimension;
  }

  isValid(): boolean {
    const isValidWidth = this.width > Number.EPSILON || this.fitDimension === 'width';
    const isValidHeight = this.height > Number.EPSILON || this.fitDimension === 'height';
    const isValid = isValidWidth && isValidHeight;
    return isValid;
  }

  copy(): Domain {
    return new Domain(this.x, this.y, this.width, this.height, this.fitDimension);
  }
}

// TODO4: Add showAxis parameter.
//        Axis must be drawn at origin of the world CS, but should no be affected by scale.

export class Scene {
  private container: SVGGElement; // Contains all elements of the scene.
  private domain: NullOr<Domain> = null; // Defines relationship between the screen and scene coordinate systems.
  private isYFlipped: boolean = false;

  // TODO4: We do not need these background and clipping if using SVG as a container.
  private background: SVGRectElement; // Allows to change background and bounds color of the scene.
  private clipping: SVGRectElement; // Masks region to hide elements outside of the `container`.

  private layers: Array<Layer> = [];
  private onLeftMouseButtonClick: NullOr<OnMouseButtonClickHandler> = null;
  private mouseButtonDownPosition: Vector = new Vector();
  private isPanning: boolean = false; // Indicates that user panning the scene.
  private userPanning: Vector = Vector.zeros(); // User defined offset of the scene.
  private userScaling: number = 1.0; // User defined scale of the scene.
  private position: Vector = new Vector(); // Pixel position of the `container` on the plot.
  private size: Size = new Size(); // Pixel size of the `container`.

  constructor(
    parentContainer: SVGElement,
    domain: NullOr<Domain> = null,
    backgroundColor: ColorOrStringOrArray = '#000'
  ) {
    this.container = SVGElementsFactory.createGroup(parentContainer);

    this.setDomain(domain);

    this.background = SVGElementsFactory.createRectangle(this.container);
    setElementAttributes(this.background, { width: '100%', height: '100%' });
    this.setBackgroundColor(backgroundColor);

    const definitions = SVGElementsFactory.createDefinitions(this.container);
    const clipPath = SVGElementsFactory.createClipPath(definitions);
    const clipPathId = 'clip-path-id-' + generateRandomId();
    clipPath.setAttribute('id', clipPathId);
    this.clipping = SVGElementsFactory.createRectangle(clipPath);

    // TODO4: Probably clipping should be applied to each layer independently.
    //        This will make getContentBoundingBox() method more precise (see SpinningSpiral project).
    this.container.setAttribute('clip-path', `url(#${clipPathId})`);

    this.addLayer(); // Layers have to be created after the background.

    this.container.onmousedown = (v) => this.onMouseButtonDown(v);
    this.container.onclick = (v) => this.onMouseButtonClick(v);
    this.container.onauxclick = (v) => this.onMouseButtonClick(v);
    this.container.onmousemove = (v) => this.onMouseMove(v);
    this.container.onwheel = (v) => this.onMouseWheelRotate(v);
  }

  setDomain(domain: NullOr<Domain>) {
    this.domain = domain;
  }

  // This method returns a domain.
  // If the `fitAxis` is set to 'x' or 'y' then corresponding dimension will be changed
  // to make domain's aspect ratio same as aspect ratio of the screen's region of this scene.
  getDomain(): Domain {
    let domain = new Domain(0.0, 0.0, this.size.width, this.size.height);
    if (this.domain) {
      const aspectRatio = this.size.width / this.size.height;
      domain = this.domain.copy();
      domain.width = this.domain.fitDimension === 'width' ? this.domain.height * aspectRatio : this.domain.width;
      domain.height = this.domain.fitDimension === 'height' ? this.domain.width / aspectRatio : this.domain.height;
    }

    if (!domain.isValid()) {
      throw Error('`getDomain` method failed to return a valid domain');
    }

    return domain;
  }

  setBackgroundColor(backgroundColor: ColorOrStringOrArray) {
    backgroundColor = Color.from(backgroundColor);
    this.background.setAttribute('fill', backgroundColor.toString());
  }

  setBoundsColor(boundsColor: ColorOrStringOrArray) {
    boundsColor = Color.from(boundsColor);
    this.background.setAttribute('stroke', boundsColor.toString());
  }

  addLayer(parameters?: LayerInterface) {
    const layer = new Layer(this.container, parameters);
    this.layers.push(layer);
    this.updateLayersMapping(); // Need to update new layer's transform right after it was added.
  }

  setOnLeftMouseButtonClickHandler(onLeftMouseButtonClick: NullOr<OnMouseButtonClickHandler>) {
    this.onLeftMouseButtonClick = onLeftMouseButtonClick;
  }

  setIsYFlipped(isYFlipped: boolean) {
    this.isYFlipped = isYFlipped;
    this.updateTransform();
  }

  // Set user navigation transform to fit scene's content into the screen region corresponding to this scene.
  fitContent() {
    // Select only pannable and scalable layers as we need to use both panning and scaling to fit content.
    const layers = this.layers.filter((v) => !v.isScreen && !v.isRelative && v.isPannable && v.isScalable);
    if (layers.length === 0) {
      return;
    }

    // Get bounding box of the content of all selected layers without accounting any transforms applied to them.
    const sourceBox = this.getContentBounds(layers);
    if (sourceBox.getArea() <= Number.EPSILON) {
      return;
    }

    // Compute scale factor to fit content's bounding box into the domain box.
    const targetBox = this.getDomain();
    const scale = Vector.div(targetBox.size.toVector(), sourceBox.size.toVector()).min();

    // Set the appropriate scale with 5% margin for all sides.
    this.userScaling = 0.9 * scale;

    // Compute position of the `sourceBox` in the screen coordinate system.
    // Have a note that the `fromSceneToScreen` will use the new `userScaling` value which was set above.
    const sourceBoxScreenPosition = this.fromSceneToScreen(sourceBox.leftTop);

    // Subtract position of the left top corner of the content's bounding box from the current user panning
    // to make it placed at the origin of the screen's region.
    const margin = this.size.toVector().mul(0.05);
    this.userPanning.sub(sourceBoxScreenPosition).add(margin);

    this.updateLayersMapping();
  }

  // This method must not be used directly by user.
  remove() {
    this.container.remove();
  }

  // This method must not be used directly by user.
  getLayer(index: number): Layer {
    if (!Number.isInteger(index) || index < 0) {
      throw Error('`index` must be a non-negative integer number');
    }

    if (index >= this.layers.length) {
      throw Error('`index` must be an existing layer index');
    }

    const layer = this.layers[index];

    return layer;
  }

  // This method must not be used directly by user.
  setPosition(position: VectorOrArrayOrNumber) {
    this.position = Vector.from(position);
    this.updateTransform();
  }

  // This method must not be used directly by user.
  setSize(size: SizeOrVectorOrArray) {
    this.size = Size.from(size);
    this.updateTransform();
    this.updateLayersMapping();
    setElementAttributes(this.clipping, { width: this.size.width, height: this.size.height });
  }

  private updateTransform() {
    const scale = this.isYFlipped ? [1.0, -1.0] : [1.0, 1.0];
    const translation = Vector.add(this.position, [0.0, this.isYFlipped ? this.size.height : 0.0]);
    const transform = Transform.fromTSRT(0.0, scale, 0.0, translation);
    this.container.setAttribute('transform', transform.toString());
  }

  private getContentBounds(layers: Array<Layer>): Box {
    const [POS_INF, NEG_INF] = [Number.POSITIVE_INFINITY, Number.NEGATIVE_INFINITY];
    let [left, right, top, bottom] = [POS_INF, NEG_INF, POS_INF, NEG_INF];
    let bounds = new Box();
    for (const layer of layers) {
      if (!layer.container.hasChildNodes()) {
        continue;
      }

      // The `getBBox()` method returns the actual bounding box at the time the method was called,
      // even in case the element has not yet been rendered. It also does not account for any
      // transformation applied to the element or its parents.
      const box = layer.container.getBBox({ fill: true, stroke: true, markers: true, clipped: true });
      left = Math.min(left, box.x);
      right = Math.max(right, box.x + box.width);
      top = Math.min(top, box.y);
      bottom = Math.max(bottom, box.y + box.height);
      bounds = new Box(left, top, right - left, bottom - top);
    }

    return bounds;
  }

  // This method computes a so called "domain transform" which is intended to fit user defined domain (actually a box)
  // into the screen region, corresponding to this scene, in best way, i.e. filling the maximum possible area and
  // keeping aspect ratio. This transform maps user space to screen space and calculating as:
  // domainTransform == scaleTransform * offsetTransform.
  // Its matrix is:
  // S  0  -S*X
  // 0  S  -S*Y
  // 0  0     1
  // where S is a scale factor between the domain and screen space, (X, Y) - position of the domain's top left corner.
  private getDomainTransform(): Transform {
    if (this.size.getArea() <= Number.EPSILON) {
      throw Error('`getDomainTransform` method requires that scene has non-zero dimensions');
    }

    const domain = this.getDomain();
    const screen = Box.from([0.0, 0.0, this.size.width, this.size.height]);
    const transform = domain.getMappingTransform(screen, 'min');

    return transform;
  }

  // This method computes transform which is mapping user defined coordinate space to screen space.
  // It takes user navigation transform into account.
  // screenPoint = userTransform * domainTransform * scenePoint.
  // userTransform * domainTransform == sceneToScreenTransform.
  private getSceneToScreenTransform(): Transform {
    const domainTransform = this.getDomainTransform();
    const userTransform = Transform.fromTSRT(0.0, this.userScaling, 0.0, this.userPanning);
    const transform = userTransform.applyToTransform(domainTransform);
    return transform;
  }

  private fromSceneToScreen(scenePoint: Vector): Vector {
    return this.getSceneToScreenTransform().applyToVector(scenePoint);
  }

  private fromScreenToScene(screenPoint: Vector): Vector {
    return this.getSceneToScreenTransform().inverted().applyToVector(screenPoint);
  }

  private updateLayersMapping() {
    let domainTransform;
    for (const layer of this.layers) {
      let transform;

      if (layer.isScreen) {
        transform = Transform.identity();
      } else if (layer.isRelative) {
        if (this.size.getArea() <= Number.EPSILON) {
          console.warn('Scene has zero dimensions. Layer mapping will not be updated');
          continue;
        }

        const source = new Box(0.0, 0.0, 1.0, 1.0);
        const target = new Box(0.0, 0.0, this.size.width, this.size.height);
        transform = source.getMappingTransform(target, layer.fitSide, layer.alignment);
      } else {
        try {
          domainTransform = domainTransform ?? this.getDomainTransform();
        } catch (e) {
          console.warn('Can not get a valid domain transform. Layer mapping will not be updated');
          continue;
        }

        const userPanning = layer.isPannable ? this.userPanning : 0.0;
        const userScaling = layer.isScalable ? this.userScaling : 1.0;
        const userTransform = Transform.fromTSRT(0.0, userScaling, 0.0, userPanning);
        transform = userTransform.applyToTransform(domainTransform);
      }

      layer.container.setAttribute('transform', transform.toString());
    }
  }

  // TODO4: Do not use `this.position` and `this.size` directly. Use this method.
  //        It's also possible to make separate class ScreenRegion encapsulating the position and size of the
  //        Scene's screen region. It also can keep the `isYAxisFlipped` flag and handle it.
  // private getScreenRegion(): Box {
  //   const screenRegion = new Box();
  //   screenRegion.leftTop = this.position;
  //   screenRegion.setSize(this.size);
  //   return screenRegion;
  // }

  // TODO4: Implement this method and use it instead of the `getDomain`.
  // It's also possible to make separate class SceneRegion encapsulating.
  // private getSceneRegion(): Box {
  //   return new Box();
  // }

  private getMousePosition(event: MouseEvent | WheelEvent): Vector {
    // [event.offsetX, event.offsetY] - mouse position relative to the top left corner of the screen.
    const x = event.offsetX - this.position.x;
    const y = event.offsetY - this.position.y;
    const mousePosition = new Vector(x, this.isYFlipped ? this.size.height - y : y);
    return mousePosition;
  }

  private onMouseButtonDown(mouseEvent: MouseEvent) {
    this.mouseButtonDownPosition = this.getMousePosition(mouseEvent);
  }

  private onMouseButtonClick(mouseEvent: MouseEvent) {
    if (this.isPanning) {
      this.isPanning = false;
      return;
    }

    if (mouseEvent.button === 0) {
      // Left mouse button was pressed.
      if (this.onLeftMouseButtonClick) {
        const screenPoint = this.getMousePosition(mouseEvent);
        const scenePoint = this.fromScreenToScene(screenPoint);
        this.onLeftMouseButtonClick(scenePoint.x, scenePoint.y);
      }
    } else {
      this.fitContent();
    }
  }

  // TODO4: Panning doesn't work on mobile devices.
  private onMouseMove(mouseEvent: MouseEvent) {
    // Skip if left button is not pressed.
    if (mouseEvent.buttons !== 1) {
      return;
    }

    this.isPanning = true;
    const mousePosition = this.getMousePosition(mouseEvent);
    const delta = Vector.sub(mousePosition, this.mouseButtonDownPosition);
    this.mouseButtonDownPosition = mousePosition;
    this.userPanning.add(delta);
    this.updateLayersMapping();
  }

  // TODO4: Scaling doesn't work on mobile devices.
  private onMouseWheelRotate(wheelEvent: WheelEvent) {
    const mouseScreenPosition = this.getMousePosition(wheelEvent);

    // Convert mouse position to the scene coordinate system.
    const mouseScenePosition = this.fromScreenToScene(mouseScreenPosition);

    this.userScaling *= 1.0 - 0.03 * Math.sign(wheelEvent.deltaY);

    // After the scale was changed convert mouse position from the scene CS back to the screen CS.
    // This position is different though the mouse cursor was still, so we call it "fakeMouseScreenPosition".
    // Compute the difference and add this offset to the `userPanning` so scene point under the cursor will remain same.
    const fakeMouseScreenPosition = this.fromSceneToScreen(mouseScenePosition);
    const delta = Vector.sub(mouseScreenPosition, fakeMouseScreenPosition);
    this.userPanning.add(delta);

    this.updateLayersMapping();
  }
}
