import { Box, Typography } from '@mui/material';
import React from 'react';
import ReactMarkdown from 'react-markdown';
import rehypeRaw from 'rehype-raw'; // Need to support HTML in markdown text.
import TEXT_DATA from '../assets/components/text.json';
import { CONTEXT } from '../context';
import { TranslatableString } from '../types';
import { Link } from './link';

// Represents data storing in the `text.json` file.
type TextData = {
  expandText: TranslatableString;
  collapseText: TranslatableString;
};

type TextProps = {
  textFilePath?: string;
  useMarkdown?: boolean;
  link?: string;
  children?: string; // `children` has priority over the `textFilePath`.
  [key: string]: any;
};

export const Text: React.FC<TextProps> = ({ textFilePath, useMarkdown, link, children, ...props }) => {
  const [sourceText, setSourceText] = React.useState<string>('');
  const [isExpanded, setIsExpanded] = React.useState<boolean>(false);

  const { language } = React.useContext(CONTEXT);

  // Here we use the `useEffect` instead of `useMemo` because of the asynchronous text file reading.
  React.useEffect(() => {
    if (children !== undefined) {
      setSourceText(children);
    } else {
      const importFile = () => import(`../assets/${textFilePath}`);
      const getSourceText = (file: any) => fetch(file.default).then((response) => response.text());
      const showError = (error: any) => console.error(`Error occurred while importing text file: ${error}`);
      importFile().then(getSourceText).then(setSourceText).catch(showError);
    }
  }, [textFilePath, children]);

  const maxLength = props.maxLength ?? Number.MAX_VALUE;

  const isNeedToTruncate = React.useMemo((): boolean => sourceText.length > maxLength, [sourceText, maxLength]);

  const text = React.useMemo((): string => {
    return !isNeedToTruncate || isExpanded ? sourceText : sourceText.slice(0, maxLength) + '...';
  }, [isNeedToTruncate, isExpanded, sourceText, maxLength]);

  let textNode: React.ReactNode = <>{text}</>;

  if (useMarkdown) {
    textNode = (
      <ReactMarkdown rehypePlugins={[rehypeRaw]} components={{ a: Link, p: Typography }}>
        {text}
      </ReactMarkdown>
    );
  }

  if (link) {
    textNode = <Link to={link}>{textNode}</Link>;
  }

  const textData = React.useMemo((): TextData => TEXT_DATA, []);

  const handleExpandTextClick = () => setIsExpanded(!isExpanded);

  props.sx = props.sx ?? {};
  props.variant = props.variant ?? 'body1';
  props.color = props.color ?? props.sx.color ?? 'text.primary';

  return (
    <Typography {...props} component='div'>
      {textNode}
      <Box width='100%' display={isNeedToTruncate ? 'flex' : 'none'} justifyContent='right'>
        <Typography
          onClick={handleExpandTextClick}
          sx={{ color: 'text.secondary', fontWeight: 'bold', textDecoration: 'underline', cursor: 'pointer' }}
        >
          {isExpanded ? textData.collapseText[language] : textData.expandText[language]}
        </Typography>
      </Box>
    </Typography>
  );
};
