import { Color } from '../core/color';
import { getInterpolationParameter } from '../utils';
import { OnChangeHandler, Track } from './track';

export class ColorTrack extends Track<Color> {
  constructor(onChange: OnChangeHandler<Color>) {
    const compare = (value0: Color, value1: Color) => value0.isEqual(value1, Number.EPSILON);

    const interpolate = (startValue: Color, startTime: number, endValue: Color, endTime: number, time: number) => {
      const s = getInterpolationParameter(startTime, endTime, time);
      const value = startValue.lerp(endValue, s);
      return value;
    };

    super(onChange, compare, interpolate);
  }
}
