import { ColorOrStringOrArray, NullOr } from './indyplot/core/types';
import { DEFAULT_SCENE_KEY, Plot } from './indyplot/plot';
import { Domain, OnMouseButtonClickHandler } from './indyplot/scene';
import { Colors } from './colors';

export abstract class Animation {
  protected plot: Plot;

  constructor(domain: NullOr<Domain> = null, backgroundColor: ColorOrStringOrArray = Colors.BLACK) {
    this.plot = new Plot('plotContainer', null, domain, backgroundColor);
  }

  // TODO4: Probably there should be some abstract `setData` method,
  //        which will be derived and implemented in every project.
  //        In the GrahamScan and KDTree projects, where points are adding
  //        one by one, this method can remove all previously added points
  //        and actors and set the new points instead, like it implemented
  //        in the BubbleSort project. This approach will make animation
  //        classes architecture more unified.
  //        Also in this case we do not need the `initialize` method anymore.
  //        All initialization can be done in the `setData` method.
  //        The `reset` and `clear` methods are also could be non abstract
  //        and could be implemented right in the `Animation` class.
  //        Possible implementation:
  //        clear() {
  //          this.data = {};
  //          this.plot.removeAllActors();
  //          this.plot.removeAllComments();
  //          this.plot.removeAllSounds();
  //        }
  //        reset(){
  //          const data = deepCopy(this.data); // ?
  //          this.clear();
  //          this.setData(data);
  //        }

  abstract initialize(): void;

  // This method resets the state to those one before the last animation running.
  abstract reset(): void;

  // This method resets the state to the moment when the animation just was created.
  clear() {
    this.plot.removeAllActors();
    this.plot.removeAllComments();
    this.plot.removeAllSounds();
  }

  // This method removes the `Plot` and must be called just before the unmounting.
  remove() {
    this.plot.remove();
  }

  getDomain(sceneKey: string = DEFAULT_SCENE_KEY): Domain {
    return this.plot.getScene(sceneKey).getDomain();
  }

  setOnLeftMouseButtonClickHandler(
    onLeftMouseButtonClick: NullOr<OnMouseButtonClickHandler>,
    sceneKey: string = DEFAULT_SCENE_KEY
  ) {
    this.plot.getScene(sceneKey).setOnLeftMouseButtonClickHandler(onLeftMouseButtonClick);
  }

  // COMMENTS

  showComment(comment: string, time: number = 1.0, isRelativeTime: boolean = true, duration: number = 1.0) {
    this.plot.showComment(comment, time, isRelativeTime, duration);
  }

  hideComment(time: number = 1.0, isRelativeTime: boolean = true, duration: number = 1.0) {
    this.plot.hideComment(time, isRelativeTime, duration);
  }

  //  PLAYBACK CONTROL

  startPlayback(playbackTimeLimit?: number) {
    this.plot.startPlayback(playbackTimeLimit);
  }

  pausePlayback() {
    this.plot.pausePlayback();
  }

  stopPlayback() {
    this.plot.stopPlayback();
  }

  getPlaybackTimeLimit(): NullOr<number> {
    return this.plot.getPlaybackTimeLimit();
  }

  setPlaybackTimeScale(playbackTimeScale: number) {
    this.plot.setPlaybackTimeScale(playbackTimeScale);
  }

  setOnPlaybackTimeChangeHandler(onPlaybackTimeChange: NullOr<(time: number) => void>) {
    this.plot.setOnPlaybackTimeChangeHandler(onPlaybackTimeChange);
  }

  setPlaybackTime(playbackTime: number) {
    this.plot.setPlaybackTime(playbackTime);
  }

  getPlaybackDuration(): number {
    return this.plot.getPlaybackDuration();
  }

  mutePlaybackSound() {
    this.plot.mutePlaybackSound();
  }

  unmutePlaybackSound() {
    this.plot.unmutePlaybackSound();
  }
}
