import { Box, ThemeProvider } from '@mui/material';
import React from 'react';
import { Route, Routes } from 'react-router-dom';
import { DoublePendulumProject } from './personal_projects/react/double_pendulum/double_pendulum_project';
import { KDTreeProject } from './personal_projects/react/k_d_tree/k_d_tree_project';
import { BubbleSortProject } from './personal_projects/react/bubble_sort/bubble_sort_project';
import { GrahamScanProject } from './personal_projects/react/graham_scan/graham_scan_project';
import { CONTEXT } from './context';
import { Home } from './pages/home/home';
import { PersonalProjects } from './pages/personal_projects';
import { ProfessionalProjects } from './pages/professional_projects';
import { Themes } from './themes';
import { Language, Theme } from './types';

