export function Main() {
  const [theme, setTheme] = React.useState<Theme>('light');
  const [language, setLanguage] = React.useState<Language>('en');

  return (
    <CONTEXT.Provider value={{ theme, setTheme, language, setLanguage }}>
      <ThemeProvider theme={theme === 'light' ? Themes.LIGHT_THEME : Themes.DARK_THEME}>
        <Box sx={{ backgroundColor: 'background.default' }}>
          <Routes>
            <Route path='/' element={<Home />} />
            <Route path='/professional-projects' element={<ProfessionalProjects />} />
            <Route path='/personal-projects' element={<PersonalProjects />} />
            <Route path='/personal-projects/double-pendulum' element={<DoublePendulumProject />} />
            <Route path='/personal-projects/k-d-tree' element={<KDTreeProject />} />
            <Route path='/personal-projects/bubble-sort' element={<BubbleSortProject />} />
            <Route path='/personal-projects/graham-scan' element={<GrahamScanProject />} />
          </Routes>
        </Box>
      </ThemeProvider>
    </CONTEXT.Provider>
  );
}
