import PauseIcon from '@mui/icons-material/Pause';
import PlayIcon from '@mui/icons-material/PlayArrow';
import SpeedIcon from '@mui/icons-material/SlowMotionVideo';
import StopIcon from '@mui/icons-material/Stop';
import MutedSoundIcon from '@mui/icons-material/VolumeOff';
import UnmutedSoundIcon from '@mui/icons-material/VolumeUp';
import { Box, Button, IconButton, Slider, Typography } from '@mui/material';
import React from 'react';
import { Animation } from '../indyplot_projects/animation';

const TIME_FACTORS = [0.5, 0.75, 1.0, 1.5, 2.0];

interface IPlaybackController {
  animation: Animation;
  isRunning: boolean;
}

function getTimeString(timeInSeconds: number): string {
  const minutes = Math.floor(timeInSeconds / 60.0).toString();
  const seconds = (Math.floor(timeInSeconds) % 60).toString();
  const milliseconds = Math.floor((timeInSeconds - Math.floor(timeInSeconds)) * 10.0).toString();
  const timeString = minutes.padStart(2, '0') + ':' + seconds.padStart(2, '0') + '.' + milliseconds;
  return timeString;
}

export function PlaybackController({ animation, isRunning }: IPlaybackController) {
  const [time, setTime] = React.useState(0.0);
  const [isPaused, setIsPaused] = React.useState(false);
  const [isMuted, setIsMuted] = React.useState(false);
  const [timeFactorIndex, setTimeFactorIndex] = React.useState(2);

  const start = React.useCallback(() => {
    const duration = animation ? animation.getPlaybackDuration() : 0.0;
    if (duration > 0.0) {
      animation.startPlayback(duration);
      setIsPaused(false);
    }
  }, [animation]);

  const pause = React.useCallback(() => {
    animation.pausePlayback();
    setIsPaused(true);
  }, [animation]);

  const stop = React.useCallback(() => {
    if (animation) {
      animation.stopPlayback();
      setIsPaused(true);
    }
  }, [animation]);

  const timeLimit: number = animation ? animation.getPlaybackTimeLimit() ?? 0.0 : 0.0;

  const handlePositionSliderChange = React.useCallback(
    (event: Event, position: Array<number> | number) => {
      position = Array.isArray(position) ? position[0] : position;
      const newTime = position * timeLimit;
      animation.setPlaybackTime(newTime);
    },
    [animation, timeLimit]
  );

  const handleSpeedButtonClick = React.useCallback(() => {
    const newTimeFactorIndex = (timeFactorIndex + 1) % TIME_FACTORS.length;
    animation.setPlaybackTimeScale(TIME_FACTORS[newTimeFactorIndex]);
    setTimeFactorIndex(newTimeFactorIndex);
  }, [animation, timeFactorIndex]);

  const handleMuteButtonClick = React.useCallback(() => {
    isMuted ? animation.unmutePlaybackSound() : animation.mutePlaybackSound();
    setIsMuted((v) => !v);
  }, [animation, isMuted]);

  const isEnabled = animation && isRunning && timeLimit > 0.0;
  const position = isEnabled ? time / timeLimit : 0.0;
  const timeString = isEnabled ? getTimeString(time) : '-';
  const timeLimitString = isEnabled ? getTimeString(timeLimit) : '-';

  // Run once to pass the `setTime` callback to the `animation` instance.
  React.useEffect(() => {
    animation && animation.setOnPlaybackTimeChangeHandler(setTime);
  }, [animation]);

  // If state was changed externally then call the corresponding function to start or stop playback.
  React.useEffect(() => {
    isRunning ? start() : stop();
  }, [start, stop, isRunning]);

  return (
    <Box sx={{ display: 'flex', alignItems: 'center', gap: '15px', padding: '5px' }}>
      <Typography color={'text.primary'}>{timeString + '/' + timeLimitString}</Typography>
      <Slider disabled={!isEnabled} max={1.0} step={0.001} value={position} onChange={handlePositionSliderChange} />
      <Button disabled={!isEnabled} startIcon={<SpeedIcon />} onClick={handleSpeedButtonClick}>
        x{TIME_FACTORS[timeFactorIndex]}
      </Button>
      <IconButton disabled={!isEnabled} onClick={() => (isPaused ? start() : pause())}>
        {isPaused ? <PlayIcon /> : <PauseIcon />}
      </IconButton>
      <IconButton disabled={!isEnabled} onClick={stop}>
        <StopIcon />
      </IconButton>
      <IconButton disabled={!isEnabled} onClick={handleMuteButtonClick}>
        {isMuted ? <MutedSoundIcon /> : <UnmutedSoundIcon />}
      </IconButton>
    </Box>
  );
}
