import { SVGElementsFactory } from '../svg_elements_factory';
import { NullOr, VectorOrArrayOrNumber } from '../core/types';
import { generateRandomId, setElementAttributes } from '../utils';
import { Vector } from '../core/vector';
import { Actor, IActor } from './actor';

export interface ILineActor extends IActor {
  startPosition?: NullOr<VectorOrArrayOrNumber>;
  endPosition?: NullOr<VectorOrArrayOrNumber>;
  isArrowCap?: NullOr<boolean>;
}

export class LineActor extends Actor {
  private line: SVGLineElement;
  private arrowCap?: SVGMarkerElement;

  constructor(parentContainer: SVGGElement) {
    super(parentContainer);
    this.line = SVGElementsFactory.createLine(this.container);
  }

  setParameters(parameters: ILineActor) {
    super.setParameters(parameters);

    if (parameters.startPosition !== undefined) {
      this.setStartPosition(parameters.startPosition);
    }

    if (parameters.endPosition !== undefined) {
      this.setEndPosition(parameters.endPosition);
    }

    if (parameters.isArrowCap !== undefined) {
      this.setIsArrowCap(parameters.isArrowCap);
    }
  }

  setStartPosition(startPosition: NullOr<VectorOrArrayOrNumber>, time?: number, duration?: number): LineActor {
    startPosition = startPosition !== null ? Vector.from(startPosition) : startPosition;
    const [x, y] = startPosition ? [startPosition.x, startPosition.y] : [null, null];
    this.setAttribute(this.line, 'x1', 'startPosition', x, time, duration);
    this.setAttribute(this.line, 'y1', 'startPosition', y, time, duration);
    return this;
  }

  setEndPosition(endPosition: NullOr<VectorOrArrayOrNumber>, time?: number, duration?: number): LineActor {
    endPosition = endPosition !== null ? Vector.from(endPosition) : endPosition;
    const [x, y] = endPosition ? [endPosition.x, endPosition.y] : [null, null];
    this.setAttribute(this.line, 'x2', 'endPosition', x, time, duration);
    this.setAttribute(this.line, 'y2', 'endPosition', y, time, duration);
    return this;
  }

  setIsArrowCap(isArrowCap: NullOr<boolean>, time?: number, duration?: number): LineActor {
    const arrowCapId = isArrowCap !== null ? (isArrowCap ? `url(#${this.getArrowCapId()})` : '') : null;
    this.setAttribute(this.line, 'marker-end', 'arrowCapId', arrowCapId, time, duration);
    return this;
  }

  private getArrowCapId(): string {
    if (!this.arrowCap) {
      const arrowCapId = 'arrow-cap-id-' + generateRandomId();
      this.arrowCap = SVGElementsFactory.createMarker(this.container);
      setElementAttributes(this.arrowCap, { id: arrowCapId, viewBox: '0 0 10 10', orient: 'auto-start-reverse' });
      setElementAttributes(this.arrowCap, { refX: 0, refY: 5 });

      const arrowCapPath = SVGElementsFactory.createPath(this.arrowCap);
      arrowCapPath.setAttribute('d', 'M 0 0 L 10 5 L 0 10 z');
    }

    const arrowCapId = this.arrowCap.getAttribute('id') as string;

    return arrowCapId;
  }
}
