import React from 'react';
import { ModalMessage } from '../modal_message';
import { PlaybackController } from '../playback_controller';
import { ProjectViewer, ProjectViewerMode } from '../project_viewer';
import { makeRandomPoints } from '../../indyplot_projects/utils';
import { Vector } from '../../indyplot_projects/indyplot/core/vector';
import { GrahamScan } from '../../indyplot_projects/graham_scan/graham_scan';
import { GrahamScanAnimation } from '../../indyplot_projects/graham_scan/graham_scan_animation';
import { GrahamScanControls } from './graham_scan_controls';

const MIN_POINTS_NUMBER = 3;
const MAX_POINTS_NUMBER = 10;
const INITIAL_POINTS_NUMBER = 5;

let points: Array<Vector> = [];
let animation: GrahamScanAnimation;

function generateRandomPoints(pointsNumber: number): Array<Vector> {
  const domain = animation.getDomain();
  const minDistance = 0.01 * domain.getDiagonal();
  const points = makeRandomPoints(pointsNumber, domain, minDistance);
  return points;
}

export function GrahamScanProject() {
  const [pointsNumber, setPointsNumber] = React.useState(0);
  const [errorMessage, setErrorMessage] = React.useState('');
  const [isPlaybackRunning, setIsPlaybackRunning] = React.useState(false);

  // Points can be added either by pressing the "Add random points" button or by clicking the plot with the mouse.
  const addPoints = React.useCallback((newPoints: Array<Vector>) => {
    if (points.length + newPoints.length > MAX_POINTS_NUMBER) {
      setErrorMessage(`Maximum allowed number of points is ${MAX_POINTS_NUMBER}.`);
      return;
    }

    newPoints.forEach((v) => animation.addPoint(v));
    points = points.concat(newPoints);
    setPointsNumber(points.length);
  }, []);

  const addPoint = React.useCallback((x: number, y: number) => addPoints([new Vector(x, y)]), [addPoints]);

  const addRandomPoints = React.useCallback(
    (numberOfPointsToAdd: number) => addPoints(generateRandomPoints(numberOfPointsToAdd)),
    [addPoints]
  );

  const clear = React.useCallback(() => {
    points = [];
    animation.clear();
    setPointsNumber(0);
  }, []);

  const handleProjectViewerModeSwitch = React.useCallback(
    (mode: ProjectViewerMode): boolean => {
      if (mode === 'play') {
        if (points.length < MIN_POINTS_NUMBER) {
          setErrorMessage(`Minimum required number of points is ${MIN_POINTS_NUMBER}.`);
          return false;
        }

        animation.setOnLeftMouseButtonClickHandler(null);
        animation.initialize();
        const pointsTuples = points.map((v) => v.toTuple());
        GrahamScan.getConvexHull(pointsTuples, animation);
        setIsPlaybackRunning(true);
      } else {
        setIsPlaybackRunning(false);
        // TODO4: There is a bug caused the playback works in a separate thread and needs some time to stop.
        //        It leads the actors' state is changing (at least once) when the animation was already reset.
        //        So it's need to add small delay before resetting the animation to let the timer stop. Need to fix it.
        setTimeout(() => animation.reset(), 100);
        animation.setOnLeftMouseButtonClickHandler(addPoint);
      }

      return true;
    },
    [addPoint]
  );

  React.useEffect(() => {
    animation = new GrahamScanAnimation();
    animation.setOnLeftMouseButtonClickHandler(addPoint);
    addRandomPoints(INITIAL_POINTS_NUMBER);
    return () => {
      clear();
      animation.remove();
    };
  }, [addPoint, addRandomPoints, clear]);

  const playbackController = <PlaybackController animation={animation} isRunning={isPlaybackRunning} />;

  const projectControls = (
    <GrahamScanControls
      pointsNumber={pointsNumber}
      maxPointsNumber={MAX_POINTS_NUMBER}
      onAddRandomPointsButtonClick={addRandomPoints}
      onClearButtonClick={clear}
    />
  );

  return (
    <>
      {/* Display error message if needed. */}
      <ModalMessage
        title='ERROR'
        message={errorMessage}
        isShown={errorMessage !== ''}
        onHide={() => setErrorMessage('')}
      />

      {/* ProjectViewer with GrahamScan UI controls. */}
      <ProjectViewer
        playbackController={playbackController}
        projectControls={projectControls}
        onModeSwitch={handleProjectViewerModeSwitch}
      />
    </>
  );
}
