import { ColorOrStringOrArray, NullOr } from './types';

export class Color {
  private _r: number = 0.0;
  private _g: number = 0.0;
  private _b: number = 0.0;
  private _none: boolean = false; // The `_none` flag indicates that color is missing. It's not the same as `opacity=0`.

  constructor(r: number = 0.0, g: number = 0.0, b: number = 0.0, none: boolean = false) {
    this.r = r;
    this.g = g;
    this.b = b;
    this.none = none;
  }

  set r(r: number) {
    if (!Number.isFinite(r) || r < 0.0 || r > 1.0) {
      throw Error('`r` must be a real number from range [0.0, 1.0]');
    }

    this._r = r;
  }

  get r(): number {
    return this._r;
  }

  set g(g: number) {
    if (!Number.isFinite(g) || g < 0.0 || g > 1.0) {
      throw Error('`g` must be a real number from range [0.0, 1.0]');
    }

    this._g = g;
  }

  get g(): number {
    return this._g;
  }

  set b(b: number) {
    if (!Number.isFinite(b) || b < 0.0 || b > 1.0) {
      throw Error('`b` must be a real number from range [0.0, 1.0]');
    }

    this._b = b;
  }

  get b(): number {
    return this._b;
  }

  set none(none: boolean) {
    this._none = none;
  }

  get none(): boolean {
    return this._none;
  }

  from(value: NullOr<ColorOrStringOrArray | number>) {
    if (value === null) {
      this.none = true;
    } else if (typeof value === 'string') {
      this.fromString(value);
    } else if (Array.isArray(value)) {
      this.fromArray(value);
    } else if (typeof value === 'number') {
      this.fromArray([value, value, value]);
    } else {
      this.r = value.r;
      this.g = value.g;
      this.b = value.b;
      this.none = value.none;
    }
  }

  copy(): Color {
    return new Color(this.r, this.g, this.b, this.none);
  }

  toString(): string {
    if (this.none) {
      return 'none';
    }

    const r = Math.round(255.0 * this.r);
    const g = Math.round(255.0 * this.g);
    const b = Math.round(255.0 * this.b);
    const rs = r.toString(16).padStart(2, '0');
    const gs = g.toString(16).padStart(2, '0');
    const bs = b.toString(16).padStart(2, '0');
    const stringColor = '#' + rs + gs + bs;

    return stringColor;
  }

  toArray(): Array<number> {
    return this.none ? [] : [this.r, this.g, this.b];
  }

  isEqual(color: NullOr<ColorOrStringOrArray>, tolerance: number = Number.EPSILON): boolean {
    if (!Number.isFinite(tolerance) || tolerance < 0.0) {
      throw Error('`tolerance` must be a non-negative real number');
    }

    color = Color.from(color);

    if (this.none !== color.none) {
      return false;
    }

    if (this.none && color.none) {
      return true;
    }

    const dr = this.r - color.r;
    const dg = this.g - color.g;
    const db = this.b - color.b;
    const distance = Math.sqrt(dr ** 2.0 + dg ** 2.0 + db ** 2.0);
    const isEqual = distance <= tolerance;

    return isEqual;
  }

  /**
   * Linear interpolation between two colors.
   * @param endColor - desired end color.
   * @param s - linear combination parameter which value have to be from 0.0 to 1.0.
   * @returns interpolated color.
   */
  lerp(endColor: Color, s: number): Color {
    if (!Number.isFinite(s) || s < 0.0 || s > 1.0) {
      throw Error('`s` must be a real number from range [0.0, 1.0]');
    }

    if (this.none || endColor.none) {
      throw Error("`lerp` method can't be used with `none` colors");
    }

    const r = this.r + s * (endColor.r - this.r);
    const g = this.g + s * (endColor.g - this.g);
    const b = this.b + s * (endColor.b - this.b);
    const color = new Color(r, g, b, false);

    return color;
  }

  static isColor(value: unknown): value is Color {
    return value instanceof Color;
  }

  static from(value: NullOr<ColorOrStringOrArray>) {
    const color = new Color();
    color.from(value);
    return color;
  }

  private fromString(stringColor: string) {
    if (stringColor === 'none') {
      this.none = true;
      return;
    }

    // Expand shorthand form (e.g. "03F") to full form (e.g. "0033FF").
    //  ^  - matches the beginning of input.
    //  \d - matches any digit.
    //  $  - matches the end of input.
    //  i  - case-insensitive search.
    stringColor = stringColor.replace(/^#([a-f\d])([a-f\d])([a-f\d])$/i, (_, r, g, b) => {
      return '#' + r + r + g + g + b + b;
    });

    const result = /^#([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(stringColor);
    if (!result) {
      throw Error('`stringColor` must be a valid hex-color string');
    }

    this.r = parseInt(result[1], 16) / 255.0;
    this.g = parseInt(result[2], 16) / 255.0;
    this.b = parseInt(result[3], 16) / 255.0;
    this.none = false;
  }

  private fromArray(arrayColor: Array<number>) {
    if (arrayColor.length === 0) {
      this.none = true;
      return;
    }

    if (arrayColor.length !== 3) {
      throw Error('`arrayColor` must be either an array of length 3 or empty');
    }

    [this.r, this.g, this.b] = arrayColor;
    this.none = false;
  }
}
