import { Box } from '@mui/material';
import React from 'react';
import { Page, pageTopMargin } from '../../components/page/page';
import { AboutMe } from './about_me';
import { AdditionalInformation } from './additional_information';
import { ProfessionalSkills } from './professional_skills';
import { WorkExperience } from './work_experience';

export const Home: React.FC = () => {
  return (
    // Height of the `AboutMe` block is equal to viewport height without the top margin in the `Page` component.
    <Page>
      <Box width='100%' height={`calc(100vh - ${pageTopMargin}px)`}>
        <AboutMe />
      </Box>
      <ProfessionalSkills />
      <WorkExperience />
      <AdditionalInformation />
    </Page>
  );
};
