import { Box, Card, CardContent } from '@mui/material';
import React from 'react';
import COMPANIES_DATA from '../../assets/companies/companies.json';
import { CONTEXT } from '../../context';
import { CompaniesData, Company, TranslatableString } from '../../types';
import { Link } from '../link';
import { Text } from '../text';
import { MediaFile, MediaSlider } from './media_slider';
import { TagCloud } from './tag_cloud';

export type Project = {
  title: TranslatableString;
  date: TranslatableString;
  companyId?: string;
  descriptionFilePath: TranslatableString;
  tags?: Array<string>;
  mediaFiles?: Array<MediaFile>;
  directoryPath?: string;
  url?: string;
};

type ProjectCardProps = {
  project: Project;
};

export const ProjectCard: React.FC<ProjectCardProps> = ({ project }) => {
  const { language } = React.useContext(CONTEXT);

  let titleNode = <Text variant='h2'>{project.title[language]}</Text>;

  if (project.url) {
    titleNode = <Link to={project.url}>{titleNode}</Link>;
  }

  const companiesData = React.useMemo((): CompaniesData => COMPANIES_DATA, []);

  const subtitleNode = React.useMemo((): React.ReactNode => {
    const dateNode = <Text variant='h3'>{project.date[language]}</Text>;

    let companyNode: React.ReactNode = null;
    if (project.companyId) {
      const company: Company = companiesData[project.companyId];
      companyNode = <Text variant='h3'>{company.name[language]}</Text>;

      if (company.websiteURL) {
        companyNode = <Link to={company.websiteURL}>{companyNode}</Link>;
      }
    }

    return (
      <Box display='flex' flexDirection={{ xs: 'column', md: 'row' }} gap={{ xs: 0, md: '50px' }}>
        {dateNode}
        {companyNode}
      </Box>
    );
  }, [project, language, companiesData]);

  const directoryPath = project.directoryPath ? project.directoryPath + '/' : '';
  const descriptionFilePath = `${directoryPath}${project.descriptionFilePath[language]}`;

  const mediaSliderProps = {
    mediaFiles: project.mediaFiles ?? [],
    directoryPath: project.directoryPath,
    slidesPerView: 7,
    breakpoints: {
      '@0.50': {
        slidesPerView: 2
      },
      '@0.75': {
        slidesPerView: 3
      },
      '@1.00': {
        slidesPerView: 4
      },
      '@1.50': {
        slidesPerView: 5
      },
      '@2.00': {
        slidesPerView: 6
      }
    }
  };

  return (
    <Card sx={{ width: '100%', backgroundColor: 'background.card' }}>
      <CardContent>
        {titleNode}

        {subtitleNode}

        <Text textFilePath={descriptionFilePath} maxLength={300} useMarkdown={true} sx={{ marginBottom: '10px' }} />

        <Box width='100%' display={project.tags ? 'flex' : 'none'} marginBottom='10px'>
          <TagCloud tags={project.tags ?? []} />
        </Box>

        <Box width='100%' height='120px' display={project.mediaFiles ? 'flex' : 'none'}>
          <MediaSlider {...mediaSliderProps} />
        </Box>
      </CardContent>
    </Card>
  );
};
