import { SVGElementsFactory } from '../svg_elements_factory';
import { NullOr, VectorOrArrayOrNumber } from '../core/types';
import { Vector } from '../core/vector';
import { Actor, IActor } from './actor';

export interface IPathActor extends IActor {
  vertices?: NullOr<Array<VectorOrArrayOrNumber>>;
}

export class PathActor extends Actor {
  private path: SVGPathElement;

  constructor(parentContainer: SVGGElement) {
    super(parentContainer);
    this.path = SVGElementsFactory.createPath(this.container);
    this.setFillColor('none'); // Path has no fill by default.
  }

  setParameters(parameters: IPathActor) {
    super.setParameters(parameters);

    if (parameters.vertices !== undefined) {
      this.setVertices(parameters.vertices);
    }
  }

  setVertices(vertices: NullOr<Array<VectorOrArrayOrNumber>>, time?: number, duration?: number): PathActor {
    const verticesVectors = vertices !== null ? vertices.map((v) => Vector.from(v)) : vertices;
    const verticesString = verticesVectors !== null ? this.makeVerticesString(verticesVectors) : null;
    this.setAttribute(this.path, 'd', 'vertices', verticesString, time, duration);
    return this;
  }

  private makeVerticesString(vertices: Array<Vector>): string {
    if (vertices.length === 0) {
      return '';
    }

    let verticesString = `M ${vertices[0].x} ${vertices[0].y}`;
    verticesString = vertices.slice(1).reduce((s, v) => s + ` L ${v.x} ${v.y}`, verticesString);

    return verticesString;
  }
}
