import { Box, Pagination, PaginationItem, PaginationRenderItemParams, Paper, Stack } from '@mui/material';
import React from 'react';
import { useSearchParams } from 'react-router-dom';
import { Element as ScrollAnchor, scroller } from 'react-scroll';
import { CONTEXT } from '../../context';
import { TranslatableString } from '../../types';
import { Text } from '../text';
import { Project, ProjectCard } from './project_card';
import { TagCloud } from './tag_cloud';

// TODO4: Add an option to search by tags.
//        When user clicks the tag it must be highlighted and the close button must be appeared.
//        Only cards containing all of the highlighted tags must be displayed at the page.

// TODO4: Add page loading indicator.
//        On mobiles it's not obvious if the page is fully loaded or the loading is still in progress.

// TODO5: This component is too complex. Think about decomposition.
//        Probably we need to move all filtration and search related stuff to separate component.

// Represents data storing in the `professional_projects.json` and `personal_projects.json` files.
type ProjectsData = {
  title: TranslatableString;
  descriptionFilePath: TranslatableString;
  projects: Array<Project>;
};

type ProjectsListProps = {
  projectsData: ProjectsData;
  projectsPerPage: number;
  useReverseProjectIds: boolean; // Set to `true` to freeze IDs of the old projects and make their links permanent.
};

const CustomPaginationItem: React.FC<PaginationRenderItemParams> = ({ ...props }) => (
  <PaginationItem {...props} sx={{ fontWeight: 'bold', fontSize: 24 }} />
);

export const ProjectsList: React.FC<ProjectsListProps> = ({ projectsData, projectsPerPage, useReverseProjectIds }) => {
  const [pageIndex, setPageIndex] = React.useState<number>(1);
  const [searchProjectId, setSearchProjectId] = React.useState<number>(-1);

  const [searchParameters, setSearchParameters] = useSearchParams();

  const { language } = React.useContext(CONTEXT);

  const projectsNumber = projectsData.projects.length;

  const pagesNumber = Math.ceil(projectsNumber / projectsPerPage);

  const sourceProjectCardNodes = React.useMemo((): Array<React.ReactNode> => {
    const sourceProjectCardNodes = projectsData.projects.map((v, i) => {
      const projectId = useReverseProjectIds ? projectsNumber - i - 1 : i; // Fixed project identifier.
      const sourceProjectCardNode = (
        <ScrollAnchor key={i} name={`project-${projectId}`}>
          <ProjectCard project={v}></ProjectCard>
        </ScrollAnchor>
      );

      return sourceProjectCardNode;
    });

    return sourceProjectCardNodes;
  }, [projectsNumber, projectsData, useReverseProjectIds]);

  const projectCardNodes = React.useMemo((): Array<React.ReactNode> => {
    return sourceProjectCardNodes.slice((pageIndex - 1) * projectsPerPage, pageIndex * projectsPerPage);
  }, [sourceProjectCardNodes, pageIndex, projectsPerPage]);

  const tagCloud = React.useMemo(
    (): Array<string> => projectsData.projects.map((v) => v.tags ?? []).reduce((v0, v1) => [...v0, ...v1]),
    [projectsData]
  );

  const handlePageChange = (event: any, newPageIndex: number) => {
    const firstProjectIndex = (newPageIndex - 1) * projectsPerPage; // Index of the first project on the new page.
    const firstProjectId = useReverseProjectIds ? projectsNumber - firstProjectIndex - 1 : firstProjectIndex;
    setSearchParameters({ id: firstProjectId.toString() });
  };

  React.useEffect(() => {
    if (searchParameters.has('id')) {
      const projectIdString = searchParameters.get('id') as string;
      let projectId = -1; // Project card with this ID, if exists, will be shown at the beginning of the page.
      try {
        projectId = Number.parseInt(projectIdString);
      } catch (error) {
        console.error(`Failed to parse project ID: ${projectIdString}`);
        return;
      }

      if (projectId >= 0 && projectId < projectsNumber) {
        const projectIndex = useReverseProjectIds ? projectsNumber - projectId - 1 : projectId; // Ent-to-end index.
        const projectPageIndex = Math.floor(projectIndex / projectsPerPage) + 1;
        setPageIndex(projectPageIndex);
        setSearchProjectId(projectId);
      } else {
        console.error(`Project ID is out of range: ${projectId}`);
        return;
      }
    }
  }, [searchParameters, projectsNumber, projectsPerPage, useReverseProjectIds]);

  // Scroll to the query project if the `searchProjectId` or `pageIndex` were changed and required project is presented
  // on the current page.
  React.useEffect(() => {
    const firstProjectIndex = (pageIndex - 1) * projectsPerPage;
    const lastProjectIndex = Math.min(pageIndex * projectsPerPage, projectsNumber) - 1;
    const idRangeStart = useReverseProjectIds ? projectsNumber - lastProjectIndex - 1 : firstProjectIndex;
    const idRangeEnd = useReverseProjectIds ? projectsNumber - firstProjectIndex - 1 : lastProjectIndex;
    if (searchProjectId >= idRangeStart && searchProjectId <= idRangeEnd) {
      // TODO4: The 2000ms delay before scrolling to the query project is a temporary workaround.
      //        Need to execute the `scrollTo` method right after all cards were rendered.
      //        The `useEffect` fires its callback when the root element itself is rendered.
      //        It doesn't take its child nodes into account.
      setTimeout(() => {
        scroller.scrollTo(`project-${searchProjectId}`, { offset: -60 });
      }, 2000);
    }
  }, [projectCardNodes, searchProjectId, pageIndex, projectsNumber, projectsPerPage, useReverseProjectIds]);

  return (
    <>
      <Paper sx={{ width: '100%' }}>
        <Box display='flex' flexDirection='column' alignItems='center' margin='10px'>
          <Text variant='h1' align='center'>
            {projectsData.title[language]}
          </Text>
          <Text textFilePath={projectsData.descriptionFilePath[language]} useMarkdown={true} />
          <Box width='100%' marginBottom='10px'>
            <TagCloud tags={tagCloud} view='size' sort={{ key: 'tag', order: 'ascending' }} sizeRange={[0.95, 2.0]} />
          </Box>
          <Stack width='100%' spacing={1}>
            {projectCardNodes}
          </Stack>
        </Box>
      </Paper>

      <Box
        sx={{
          width: '100vw',
          position: 'fixed',
          left: '0',
          bottom: '1vh',
          display: pagesNumber > 1 ? 'flex' : 'none',
          justifyContent: 'center',
          zIndex: '1'
        }}
      >
        <Box
          sx={{
            backgroundColor: 'background.default',
            border: 1,
            borderColor: 'primary.main',
            borderRadius: '25px',
            boxShadow: '0 5px 10px -3px'
          }}
        >
          <Pagination
            page={pageIndex}
            count={pagesNumber}
            size={'large'}
            onChange={handlePageChange}
            renderItem={CustomPaginationItem}
            sx={{ margin: '5px' }}
          ></Pagination>
        </Box>
      </Box>
    </>
  );
};
