import { GrahamScanAnimation } from './graham_scan_animation';

type Point = [number, number];

export class GrahamScan {
  static getConvexHull(points: Array<Point>, animation: GrahamScanAnimation): Array<number> {
    // Initialize three ordered sets with indices of the hull points, inner points and undefined points.
    // Initially all points are placed in the undefined set.
    const hull: Array<number> = [];
    const inner: Array<number> = [];
    const undef: Array<number> = [...Array(points.length).keys()];
    animation.updateHull(hull, inner, undef, false, 0.0, 0.0);

    // Show an initial set of points.
    animation.showComment('This is an initial set of points.', 5.0);
    animation.showPoints(5.0, 5.0);
    animation.hideComment(5.0);

    animation.showComment(
      'Consider 3 ordered sets: "hull", "inner" and "undefined". Initially all points are in the "undefined" set.',
      5.0
    );
    animation.showTable(0.0, 1.0);
    animation.hideComment(5.0);

    // Find index of the point with the smallest Y coordinate.
    const pointsY = points.map((v) => v[1]);
    const minYIndex = pointsY.indexOf(Math.min(...pointsY));
    animation.showComment('Find index of the point with the smallest Y coordinate.', 5.0);
    animation.showOriginPoint(minYIndex, 0.0, 1.0);
    animation.hideComment(5.0);

    // Compute angles between the X axis and a radius-vector of each point.
    // Point with the smallest Y coordinate will have zero angle.
    const angles: Array<number> = [];
    for (let i = 0; i < points.length; ++i) {
      const direction = [points[i][0] - points[minYIndex][0], points[i][1] - points[minYIndex][1]];
      angles[i] = (Math.atan2(direction[1], direction[0]) * 180.0) / Math.PI;
    }
    animation.showComment('Compute angles between the X axis and a radius-vector of each point.', 5.0);
    animation.showAngles(angles, 5.0, 5.0);
    animation.hideOriginPoint(minYIndex, 0.0, 1.0);
    animation.hideComment(5.0);

    // Sort the points in ascending order by the computed angles.
    undef.sort((v1, v2) => angles[v1] - angles[v2]);
    animation.showComment('Sort the points in ascending order by the computed angles.', 5.0);
    animation.updateHull(hull, inner, undef, false, 0.0, 0.0);
    animation.hideComment(5.0);

    // The first point will be on the convex hull since it has the smallest Y coordinate.
    hull.push(undef.shift() as number);
    animation.showComment('The first point will be on the convex hull since it has 0 angle with itself.', 5.0);
    animation.updateHull(hull, inner, undef, false, 0.0, 0.0);
    animation.hideComment(5.0);

    // Proceed to consider each point in the sorted array.
    animation.showComment(
      "While there are at least 2 undefined points take the first one and check it. If it's on the right side of " +
        'segment, connecting the last hull point with the next undefined point, then these 3 points are giving the ' +
        "left turn. In this case the checked point is part of the hull. Otherwise it's inner point of the hull.",
      5.0
    );
    animation.showTurn(0.0, 0.0);
    while (undef.length >= 2) {
      while (true) {
        const test: Array<number> = [hull.at(-1) as number, ...undef.slice(0, 2)];
        animation.checkTurn(test, 2.0, 1.0);

        const isLeftTurn = this.isLeftTurn(points[test[0]], points[test[1]], points[test[2]]);
        animation.setTurn(isLeftTurn, 2.0, 1.0);

        if (isLeftTurn) {
          break;
        }

        inner.push(undef[0]);
        undef[0] = hull.pop() as number;
        animation.updateHull(hull, inner, undef, false, 0.0, 1.0);
      }

      hull.push(undef.shift() as number);
      animation.updateHull(hull, inner, undef, false, 0.0, 1.0);
    }
    animation.hideTurn();
    animation.hideComment();

    hull.push(undef.shift() as number);
    animation.updateHull(hull, inner, undef, true);

    animation.showComment('Convex hull complete.', 0.0);
    animation.hideComment(5.0);

    return hull;
  }

  private static isLeftTurn(a: Point, b: Point, c: Point) {
    return (b[0] - a[0]) * (c[1] - a[1]) - (b[1] - a[1]) * (c[0] - a[0]) > 0.0;
  }
}
