import React from 'react';
import { ScreenRotation as RotateScreenIcon } from '@mui/icons-material';
import { Box, keyframes, Modal, Typography, useMediaQuery } from '@mui/material';

const rotationKeyFrames = keyframes`
  0%{
    transform: rotate(45deg)
  }
  95%{
    transform: rotate(-45deg)
  }
  100%{
    transform: rotate(45deg)
  }
`;

export const PortraitScreenBlocker: React.FC = () => {
  const isPortrait = useMediaQuery('@media (orientation: portrait)');
  return (
    <Modal open={isPortrait} sx={{ width: '100vw', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
      <Box width='50%' display='flex' flexDirection='column' alignItems='center' justifyContent='center' color='white'>
        <RotateScreenIcon sx={{ width: '100%', height: '100%', animation: `${rotationKeyFrames} 3s infinite` }} />
        <Typography fontSize='2em' align='center' sx={{ marginTop: '10px' }}>
          please rotate your device
        </Typography>
      </Box>
    </Modal>
  );
};
