import { Edit as EditModeIcon, Fullscreen as EnterFullScreenIcon } from '@mui/icons-material';
import { FullscreenExit as ExitFullScreenIcon, Home as HomeIcon, PlayArrow as PlayModeIcon } from '@mui/icons-material';
import { Box, Collapse, Fab, ThemeProvider } from '@mui/material';
import React from 'react';
import { Link } from 'react-router-dom';
import { PortraitScreenBlocker } from './portrait_screen_blocker';
import { Themes } from '../../themes';

export type ProjectViewerMode = 'play' | 'edit';

interface IProjectViewer {
  playbackController?: React.ReactNode;
  projectControls?: React.ReactNode;
  onModeSwitch?: (mode: ProjectViewerMode) => boolean;
  children?: React.ReactNode;
}

// TODO4: Probably it's better to pass `plotContainerId` as a props.

export function ProjectViewer({ playbackController, projectControls, onModeSwitch, children }: IProjectViewer) {
  const [mode, setMode] = React.useState('edit');
  const [isFullScreen, setIsFullScreen] = React.useState(false);

  const isSwitchModeButtonShown = playbackController !== undefined && projectControls !== undefined;
  const isFullScreenButtonShown = playbackController !== undefined || projectControls !== undefined;
  const isPlaybackControllerShown = playbackController !== undefined && mode === 'play' && !isFullScreen;
  const isProjectControlsShown = projectControls !== undefined && mode === 'edit' && !isFullScreen;

  if (isSwitchModeButtonShown && !onModeSwitch) {
    throw Error('`onModeSwitch` must be defined when both `playbackController` and `projectControls` are presented');
  }

  const enterFullScreen = React.useCallback(() => {
    if (!document.fullscreenElement) {
      document.documentElement.requestFullscreen();
    }
    setIsFullScreen(true);
  }, []);

  const exitFullScreen = React.useCallback(() => {
    if (document.fullscreenElement && document.exitFullscreen) {
      document.exitFullscreen();
    }
    setIsFullScreen(false);
  }, []);

  const handleSwitchModeButtonClick = React.useCallback(() => {
    const newMode = mode === 'edit' ? 'play' : 'edit';
    const result = onModeSwitch && onModeSwitch(newMode);
    if (result) {
      setMode(newMode);
      exitFullScreen();
    }
  }, [mode, onModeSwitch, exitFullScreen]);

  const handleFullScreenButtonClick = React.useCallback(() => {
    isFullScreen ? exitFullScreen() : enterFullScreen();
  }, [isFullScreen, enterFullScreen, exitFullScreen]);

  return (
    <>
      {/* Modal window preventing access in vertical screen orientation. */}
      <PortraitScreenBlocker />

      <ThemeProvider theme={Themes.DARK_THEME}>
        <Box sx={{ height: '100vh', display: 'flex' }}>
          {/* Plot container and time controller. */}
          <Box sx={{ display: 'flex', flex: '1', flexFlow: 'column', backgroundColor: 'background.default' }}>
            <Box id='plotContainer' sx={{ display: 'flex', flex: '1', alignItems: 'center', justifyContent: 'center' }}>
              {children}
            </Box>
            <Collapse in={isPlaybackControllerShown} sx={{ width: '75%', margin: 'auto' }}>
              {playbackController}
            </Collapse>
          </Box>

          {/* Project specific controls. */}
          <Box sx={{ maxWidth: '35%', backgroundColor: 'background.default' }}>
            <Collapse in={isProjectControlsShown} orientation={'horizontal'}>
              {projectControls}
            </Collapse>
          </Box>

          {/* Floating buttons. */}
          <Box sx={{ position: 'fixed', right: '5px', bottom: '5px', display: 'flex', gap: '5px' }}>
            <Fab onClick={handleSwitchModeButtonClick} sx={{ display: isSwitchModeButtonShown ? '' : 'none' }}>
              {mode === 'edit' ? <PlayModeIcon /> : <EditModeIcon />}
            </Fab>
            <Fab onClick={handleFullScreenButtonClick} sx={{ display: isFullScreenButtonShown ? '' : 'none' }}>
              {isFullScreen ? <ExitFullScreenIcon /> : <EnterFullScreenIcon />}
            </Fab>
            <Link to='/'>
              <Fab>
                <HomeIcon />
              </Fab>
            </Link>
          </Box>
        </Box>
      </ThemeProvider>
    </>
  );
}
