import { Actor } from './actor';

export class GroupActor extends Actor {
  private actors: Map<string, Actor> = new Map<string, Actor>();

  constructor(parentContainer: SVGGElement) {
    super(parentContainer, true);
  }

  add(actor: Actor, key: string) {
    if (key === '') {
      throw Error('`key` must be a non-empty string');
    }

    if (this.actors.has(key)) {
      throw Error('`key` is already presented in the group');
    }

    actor.setParentContainer(this.container);
    this.actors.set(key, actor);
  }

  get<T extends Actor = Actor>(key: string): T {
    if (!this.actors.has(key)) {
      throw Error('`key` is not in the group');
    }

    return this.actors.get(key) as T;
  }

  setTrackTime(trackTime: number) {
    super.setTrackTime(trackTime);
    this.actors.forEach((v) => v.setTrackTime(trackTime));
  }

  getTrackDuration(): number {
    return Math.max(...Array.from(this.actors.values()).map((v) => v.getTrackDuration()), super.getTrackDuration());
  }
}
