import { NullOr, VectorOrArrayOrNumber } from '../core/types';
import { Vector } from '../core/vector';
import { SVGElementsFactory } from '../svg_elements_factory';
import { StringTrack } from '../tracks/string_track';
import { Actor, IActor } from './actor';

type TextAlignment = 'lt' | 'lc' | 'lb' | 'ct' | 'cc' | 'cb' | 'rt' | 'rc' | 'rb';

const TEXT_ALIGNMENT = {
  lt: ['start', 'hanging'],
  lc: ['start', 'middle'],
  lb: ['start', 'baseline'],
  ct: ['middle', 'hanging'],
  cc: ['middle', 'middle'],
  cb: ['middle', 'baseline'],
  rt: ['end', 'hanging'],
  rc: ['end', 'middle'],
  rb: ['end', 'baseline'],
};

export interface ITextActor extends IActor {
  text?: string;
  isYFlipped?: NullOr<boolean>;
  position?: NullOr<VectorOrArrayOrNumber>;
  isRelativePosition?: [boolean, boolean] | boolean;
  size?: NullOr<number>;
  alignment?: NullOr<TextAlignment>;
}

export class TextActor extends Actor {
  private text: SVGTextElement;
  private lineSpacing = 1.5; // Vertical spacing between lines in em's. // TODO4: Add setter.
  private widthLimit: NullOr<number> = null;

  constructor(parentContainer: SVGGElement) {
    super(parentContainer);
    this.text = SVGElementsFactory.createText(this.container);
    this.setStrokeColor('none'); // Text has no stroke by default.
    this.setTextContent(null);
  }

  setParameters(parameters: ITextActor) {
    super.setParameters(parameters);

    if (parameters.text !== undefined) {
      this.setText(parameters.text);
    }

    if (parameters.isYFlipped !== undefined) {
      this.setIsYFlipped(parameters.isYFlipped);
    }

    if (parameters.position !== undefined) {
      this.setPosition(parameters.position);
    }

    if (parameters.isRelativePosition !== undefined) {
      this.setIsRelativePosition(parameters.isRelativePosition);
    }

    if (parameters.size !== undefined) {
      this.setSize(parameters.size);
    }

    if (parameters.alignment !== undefined) {
      this.setAlignment(parameters.alignment);
    }
  }

  // TODO4: Try to use the new `this.setProperty` method here.
  setText(text: string, time?: number, duration?: number): TextActor {
    if (time !== undefined) {
      let track = this.tracks.get('text');
      if (!track) {
        track = new StringTrack((v: string) => this.setText(v));
        this.tracks.set('text', track);
      }

      track.addValue(text, time, duration);
    } else {
      this.setTextContent(text);
      this.fitTextToWidthLimit();
    }

    return this;
  }

  setIsYFlipped(isYFlipped: NullOr<boolean>): TextActor {
    // TODO4: There are possible unwanted side effects if the style will contain another attributed.
    //        Need to parse the `style` JSON string and then change or remove only the
    //        `transform-box`, `transform-origin` and `transform` attributes.
    if (isYFlipped) {
      this.text.setAttribute('style', 'transform-box: fill-box; transform-origin: 0 60%; transform: scaleY(-1)');
    } else {
      this.text.removeAttribute('style');
    }

    return this;
  }

  setPosition(position: NullOr<VectorOrArrayOrNumber>, time?: number, duration?: number): TextActor {
    position = position !== null ? Vector.from(position) : position;
    const [x, y] = position ? [position.x, position.y] : [null, null];
    const callback = (key: string, value: NullOr<number>) => this.setElementAttribute(this.text, key, value);
    this.setProperty('position-x', x, (v) => callback('x', v), time, duration);
    this.setProperty('position-y', y, (v) => callback('y', v), time, duration);
    return this;
  }

  //  TODO4: Is there a point to animate these flags? Seems it won't working properly.
  setIsRelativePosition(isRelativePosition: [boolean, boolean] | boolean, time?: number, duration?: number): TextActor {
    if (typeof isRelativePosition === 'boolean') {
      isRelativePosition = [isRelativePosition, isRelativePosition];
    }

    const callback = (key: string, value: boolean) => this.setIsRelativeAttribute(key, value);
    this.setProperty('is-relative-position-x', isRelativePosition[0], (v) => callback('x', v), time, duration);
    this.setProperty('is-relative-position-y', isRelativePosition[1], (v) => callback('y', v), time, duration);
    return this;
  }

  setSize(size: NullOr<number>, time?: number, duration?: number): TextActor {
    if (size !== null && (!Number.isFinite(size) || size < 0.0)) {
      throw Error('`size` must be either `null` or a non-negative real number');
    }

    const callback = (value: NullOr<number>) => {
      this.setElementAttribute(this.text, 'font-size', value);
      this.fitTextToWidthLimit();
    };

    this.setProperty('size', size, (v) => callback(v), time, duration);

    return this;
  }

  setAlignment(alignment: NullOr<TextAlignment>, time?: number, duration?: number): TextActor {
    // TODO4: We need to take the `isYFlipped` flag into account here.
    //        When text is flipped the top will become bottom and vise versa.
    const [textAnchor, alignmentBaseline] = alignment ? TEXT_ALIGNMENT[alignment] : [null, null];
    this.setAttribute(this.text, 'text-anchor', 'alignment', textAnchor, time, duration);
    this.setAttribute(this.text, 'alignment-baseline', 'alignment', alignmentBaseline, time, duration);
    return this;
  }

  setWidthLimit(widthLimit: NullOr<number>): TextActor {
    if (widthLimit !== null && (!Number.isFinite(widthLimit) || widthLimit <= 0.0)) {
      throw Error('`widthLimit` must be either `null` or a positive real number');
    }

    this.widthLimit = widthLimit;
    this.fitTextToWidthLimit();

    return this;
  }

  private setTextContent(text: NullOr<string>) {
    // The `<text>` element must not have `textContent` and `<tspan>` elements simultaneously,
    // so remove all children elements first and then set the `textContent` field.
    this.text.childNodes.forEach((v) => this.text.removeChild(v));
    this.text.textContent = text;
  }

  private fitTextToWidthLimit() {
    // if (this.text.textContent !== null && this.text.childNodes.length > 1) {
    //   throw Error('`<text>` element must not have `textContent` and child `<tspan>` elements simultaneously');
    // }

    if (!this.widthLimit || this.text.getBBox().width <= this.widthLimit) {
      return;
    }

    const lines = Array.from(this.text.childNodes.values()).map((v) => v.textContent ?? '');
    const words = lines.reduce((text, line) => text + line + ' ', '').split(' ');
    if (words.length < 2) {
      return;
    }

    // Break down the text to multiple lines to satisfy the specified `widthLimit`.
    this.setTextContent(null);
    while (words.length > 0) {
      const textSpan = SVGElementsFactory.createTextSpan(this.text);
      textSpan.setAttribute('x', this.text.getAttribute('x') ?? '0');
      textSpan.setAttribute('dy', `${this.lineSpacing}em`);
      textSpan.textContent = words[0];

      let line;
      do {
        line = textSpan.textContent;
        words.shift();
        textSpan.textContent += words.length ? ` ${words[0]}` : '';
      } while (this.text.getBBox().width <= this.widthLimit && words.length);

      textSpan.textContent = line;
    }

    // SVG standard doesn't allow to align multiline text vertically, so we need to adjust 1-st line position manually.
    const linesNumber = this.text.childNodes.length;
    const alignment = this.text.getAttribute('alignment-baseline') ?? 'middle';
    const offset = -(linesNumber - 1) * this.lineSpacing;
    const oddOffset = 0.25 * (linesNumber % 2);
    const firstLineOffset = alignment === 'baseline' ? offset : alignment === 'middle' ? offset + oddOffset : 0.85;
    (this.text.childNodes[0] as SVGTSpanElement).setAttribute('dy', `${firstLineOffset}em`);
  }
}
